/* ============
 * Mutations for the loader module
 * ============
 *
 * The mutations that are available on the
 * loader module.
 */
import { SET_LOADER } from './mutation-types'

export default {
  [SET_LOADER] (state, payLoad) {
    if (payLoad.loader) {
      state.INSTANCE = state.INSTANCE + 1
    } else {
      state.INSTANCE = state.INSTANCE - 1
      if (state.INSTANCE < 0) {
        state.INSTANCE = 0
      }
    }
    if ('message' in payLoad) {
      state.MESSAGE = payLoad.message
    } else {
      state.MESSAGE = ''
    }
    state.LOADER = (state.INSTANCE > 0)
  }
}



