<template>
  <div>
    <div class="loader" v-if='loader'>
      <div class="content-loader">
        <div class='loader--spinningDisc'></div>
        <label>{{message}}</label>
      </div>
    </div>
    <v-dialog :clickToClose="false"/>
    <router-view></router-view>
  </div>
</template>

<script>

import store from './store'
import { router } from '@/util/bootstrap'
import { mapGetters, mapActions } from 'vuex'
import setTranslate from '@/config/translate'
import VueI18n from 'vue-i18n'

const i18n = new VueI18n()

export default {
  name: 'app',
  store,
  router,
  i18n,
  created: function () {
    var UserAuthenticated = !!sessionStorage.getItem('id_token')
    if (!UserAuthenticated) {
      console.log('no authenticado')
      // el usuario no esta autenticado
      localStorage.setItem('savedLangs', '')
      // seteamos el lenguaje por defecto
    }
    this.setLang(this.lang)
    setTranslate(i18n, this.lang)
  },
  computed: {
    ...mapGetters({
      loader: 'loader/loader',
      message: 'loader/message',
      lang: 'auth/lang'
    })
  },
  methods: {
    ...mapActions({
      setLang: 'auth/setLang'
    })
  }
}

</script>

<style lang="sass" src="./styles/application.scss">
</style>
<style lang="scss">
@import './styles/base/variables';
body{
  font-family: 'Open Sans', 'Helvetica', 'Arial', sans-serif;
}
  .loader{
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,ffffff+100&1+0,0.9+100 */
background: -moz-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(255,255,255,0.9) 100%); /* FF3.6-15 */
background: -webkit-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%,rgba(255,255,255,0.9) 100%); /* Chrome10-25,Safari5.1-6 */
background: radial-gradient(ellipse at center, rgba(255,255,255,1) 0%,rgba(255,255,255,0.9) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#e6ffffff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

  position: fixed;
  margin: 0 auto;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  animation: bg-whire 1.5s linear ;
}
.vue-dialog .dialog-content {
    padding: 0px !important;
}
.vue-dialog .dialog-content .dialog-c-title{
    color: map-get($colors, white);
    padding: 0px;
    width: 25%;
    display: inline-block;
}
.vue-dialog .dialog-content .dialog-c-title .warning{
    background: map-get($colors, warning);
    padding: 15px;
}
.vue-dialog .dialog-content .dialog-c-title .error{
    background: map-get($colors, error);
    padding: 15px;
}
.vue-dialog .dialog-content .dialog-c-title .info{
    background: map-get($colors, info);
    padding: 15px;
}
.vue-dialog .dialog-content .dialog-c-title .success{
    background: map-get($colors, success);
    padding: 15px;
}
.vue-dialog .dialog-content .dialog-c-text{
    padding: 40px 1em 40px 0;
    width: 74%;
    display: inline-block;
}
.vue-dialog .dialog-buttons button:first-child, .vue-dialog .vue-dialog-buttons button:first-child {
    color: gray;
    padding-right: 1em;
}
.vue-dialog .dialog-buttons button:last-child, .vue-dialog .vue-dialog-buttons button:last-child{
    background: map-get($colors, primary-03) !important;
    color: map-get($colors, white);
    padding-left: 1em;
    padding-right: 1em;
    border-radius: 4px;
}
@keyframes bg-whire {
  0%{
    opacity: 0;
  }
  20%{
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
.loader .content-loader{
  position: absolute;
  top: 50%;
  left: 50%;
  color: map-get($colors, primary-02);
  width: 300px;
  text-align: center;
  transform: translate(-50%, -50%);
}
.loader .text-loader{
  font-weight: 500;
}

/* LOADER */
.loader--spinningDisc {
  border: solid 0.5em map-get($colors, loader-01);
  border-right-color: transparent;
  border-left-color: transparent;
  padding: 0.5em;
  width: 3em;
  height: 3em;
  border-radius: 50%;
  background: map-get($colors, loader-02);
  background-clip: content-box;
  animation: spinDisc 1.5s linear infinite;
  margin: 15px auto;
}
.dialog-buttons, .vue-dialog-buttons{
  background: map-get($colors, gray-01);
  border: none!important;
  display: inline-block!important;
  text-align: right;
  padding-right: 1.5em;
}
.dialog-buttons button, .vue-dialog-buttons button{
    background:transparent!important;
    font-size: 16px!important;
    transition: .2s linear;
    font-weight: bold!important;
    flex: none!important;
    width: auto !important;
    display: block;
    border-radius: 2px;
    height: 35px;
    color: map-get($colors, primary-04);
    display: inline-block;
    margin: .5em 0;
}
.dialog-buttons button:hover, .vue-dialog-buttons button:hover{
  text-decoration: underline;
}
.v--modal{
  box-shadow: 0 20px 60px -2px rgba(0, 0, 0, 0.5);
}
.vue-dialog .dialog-content .dialog-c-title .error, .vue-dialog .dialog-content .dialog-c-title .warning, .vue-dialog .dialog-content .dialog-c-title .success{
  background: map-get($colors, white);
  color: map-get($colors, primary-04);
  font-size: 4em;
  text-align: center;
}
.vue-dialog .dialog-c-text{
  font-size: 16px;
}
.vue-dialog .dialog-content{
  color: gray;
}
.v--modal-overlay{
  z-index: 9999!important;
}
@keyframes spinDisc {
  0%{
    opacity: 0;
  }
  20%{
    opacity: 1;
  }
  50% {
    border-top-color: map-get($colors, loader-02);
    border-bottom-color: map-get($colors, loader-02);
    background-color: map-get($colors, loader-01);
  }
  80%{
    opacity: 1;
  }
  100% {
    transform: rotate(1turn);
    opacity: 0;
  }
}
label.titleMsg {
  color: map-get($colors, negro);
  font-weight: bold;
}
ul.msgError {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

</style>



