/* ============
 * Getters for the search module
 * ============
 *
 * The getters that are available on the
 * search module.
 */
import moment from 'moment'

export default {
  travel: (state) => {
    return {
      DEPARTURE: state.DEPARTURE,
      ARRIVAL: state.ARRIVAL,
      DATE_DEPARTURE: (typeof state.DATE_DEPARTURE === 'string') ? moment(state.DATE_DEPARTURE) : state.DATE_DEPARTURE,
      DATE_RETURN: (typeof state.DATE_RETURN === 'string') ? moment(state.DATE_RETURN) : state.DATE_RETURN,
      TYPE_WAY: state.TYPE_WAY,
      TYPE_TRAVEL: state.TYPE_TRAVEL
    }
  },
  way: (state) => {
    return state.TYPE_WAY
  },
  departure: (state) => {
    return state.DEPARTURE
  },
  arrival: (state) => {
    return state.ARRIVAL
  },
  dateDeparture: (state) => {
    return (typeof state.DATE_DEPARTURE === 'string') ? moment(state.DATE_DEPARTURE) : state.DATE_DEPARTURE
  },
  dateArrival: (state) => {
    return (typeof state.DATE_RETURN === 'string') ? moment(state.DATE_RETURN) : state.DATE_RETURN
  },
  typeTravel: (state) => {
    return state.TYPE_TRAVEL
  }
}



