import Vue from 'vue'
import setTranslate from '@/config/translate'
import VueI18n from 'vue-i18n'
import VuexRouterSync from 'vuex-router-sync'
import store from '@/store'
import vSelect from 'vue-select'
import { VTooltip, VPopover, VClosePopover } from 'v-tooltip'

import Axios from 'axios'
import VueRouter from 'vue-router'
import routes from '@/router'
import vmodal from 'vue-js-modal'
// import $ from 'jquery'

// window.$ = $

Vue.use(VueI18n)
const i18n = new VueI18n()
Vue.config.debug = process.env.NODE_ENV !== 'production'
Vue.component('v-select', vSelect)
Vue.directive('tooltip', VTooltip)
Vue.directive('close-popover', VClosePopover)
Vue.component('v-popover', VPopover)
/* ============
 * Axios
 * ============
 *
 * Promise based HTTP client for the browser and node.js.
 * Because Vue Resource has been retired, Axios will now been used
 * to perform AJAX-requests.
 *
 * https://github.com/mzabriskie/axios
 */

Axios.defaults.baseURL = process.env.API_LOCATION
Axios.defaults.headers.common.Accept = 'application/x-www-form-urlencoded'
Axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest'

Axios.interceptors.request.use(
  config => config,
  (error) => {
    return Promise.reject(error)
  })
Axios.interceptors.response.use(
  response => response,
  (error) => {
    if (error.response.data.code === 80) {
      location.href = '/'
    } else {
      if (!('details' in error.response.data)) {
        setTranslate(i18n, store.state.auth.lang)
        error.response.data.details = i18n.t('MSG_SERVER_ERROR')
      } else {
        if (Array.isArray(error.response.data.details) && error.response.data.details.length > 0) {
          let msgError = '<label class="titleMsg">' + i18n.t('MSG_INCORRECT_INFORMATION') + '</label>'
          msgError += '<ul class="msgError">'
          for (var i = 0; i < error.response.data.details.length; i++) {
            msgError += '<li>' + i18n.t(error.response.data.details[i].field)
            msgError += '<ul>'
            for (var j = 0; j < error.response.data.details[i].messages.length; j++) {
              msgError += '<li>' + error.response.data.details[i].messages[j] + '</li>'
            }
            msgError += '</ul>'
            msgError += '</li>'
          }
          msgError += '</ul>'
          error.response.data.details = msgError
        }
      }
      return Promise.reject(error)
    }
  })

Vue.$http = Axios
Object.defineProperty(Vue.prototype, '$http', {
  get () {
    return Axios
  }
})

const EventBus = new Vue()

Object.defineProperties(Vue.prototype, {
  $eventBus: {
    get: function () {
      return EventBus
    }
  }
})

/* ============
 * Vuex Router Sync
 * ============
 *
 * Effortlessly keep vue-Router and vuex store in sync.
 *
 * https://github.com/vuejs/vuex-router-sync/blob/master/README.md
 */

store.dispatch('auth/check')

/* ============
 * Vue Router
 * ============
 *
 * The official Router for Vue.js. It deeply integrates with Vue.js core
 * to make building Single Page Applications with Vue.js a breeze.
 *
 * http://router.vuejs.org/en/index.html
 */

Vue.use(VueRouter)

export const router = new VueRouter({
  routes,
  // mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 } // Al entrar a una ruta, mostrar el scroll en el top
  }
})
router.beforeEach((to, from, next) => {
  // Inyecta token pasado por url a todas las peticiones
  if (!store.getters['auth/authenticated']) {
    store.dispatch('auth/login', to.query.token)
  }

  // if (to.name === 'home') {
  //   window.location.href = process.env.URL_DEFAULT_HOME
  // }

  if (to.matched.some(m => m.meta.auth) && !store.state.auth.authenticated) {
    /*
     * If the user is not authenticated and visits
     * a page that requires authentication, redirect to the login page
     */
    next({
      name: 'login.index'
    })
  } else if (to.matched.some(m => m.meta.guest) && store.state.auth.authenticated) {
    /*
     * If the user is authenticated and visits
     * an guest page, redirect to the dashboard page
     */
    next({
      name: 'home.index'
    })
  } else {
    next()
  }
})
VuexRouterSync.sync(store, router)

Vue.router = router

/* ============
 * Vue i18n
 * ============
 *
 * Internationalization plugin of Vue.js.
 *
 * https://kazupon.github.io/vue-i18n/
 */

Vue.use(vmodal, { dialog: true })

export default {
  router
}



