<template>
    <div id="navbarTogglerDemo02" class="collapse navbar-collapse justify-content-end">
        <form class="form-inline my-2 my-lg-0">
            <ul class="navbar-nav mr-auto mt-2 mt-md-0">
                <li class="nav-item">
                    <span class="nav-email">info@boletoxpress.com</span>
                </li>
                <li>
                  <div class="language-header">
                    <v-select v-model="selectLang" :options="[{label: $t('LBL_LANGUAGE_ENGLISH'), value: 'en'},{label: $t('LBL_LANGUAGE_SPANISH'), value: 'es'}]"></v-select>
                  </div>
                </li>
                <li class="nav-item">
                    <div class="user-sesion" @click="togglelogin()" v-if="!this.seller">
                      <v-popover class="popover-login">
                      {{ $t('LBL_SIGN_IN') }}
                      <template slot="popover">
                            <form @submit.prevent="login()" class="content-login" v-show="showLogin">
                                <div class="form-group col-12 alert-message">
                                    <label for="inputUser" class="">{{ $t('LBL_USER') }}</label>
                                    <input id="inputUser" name="usuario" v-validate data-vv-rules="required|min:6" type="text" class="form-control" :placeholder="$t('LBL_ENTER_USER')" v-model="user">
                                    <small v-if="errors.has('usuario')">{{ errors.first('usuario') }}</small>
                                </div>
                                <div class="form-group col-12 alert-message">
                                    <label for="inputPassword" class="">{{ $t('LBL_PASSWORD') }}</label>
                                    <input id="inputPassword" name="contrasenia" v-validate data-vv-rules="required|min:6" type="password" class="form-control" :placeholder="$t('LBL_ENTER_PASSWORD')" v-model="password">
                                    <small v-if="errors.has('contrasenia')">{{ errors.first('contrasenia') }}</small>
                                </div>
                                <div class="col-12">
                                    <button type="submit" class="btn btn-primary">{{ $t('LBL_ENTER') }}</button>
                                </div>
                            </form>
                    </template>
                  </v-popover>
                      </div>
                    <div class="user-logout" v-else><span>{{this.nameSeller}}<i class="fa fa-user-circle" aria-hidden="true"></i></span><span @click="logout()">{{ $t('LBL_SIGN_OUT') }}</span></div>
                </li>
            </ul>
        </form>
    </div>
<!-- <div>
  <div data-delay="0" class="dropdown w-dropdown">
    <div class="dropdown-toggle w-clearfix w-dropdown-toggle">
      <div class="icon w-icon-dropdown-toggle"></div><img src="static/images/main/flag-usa.jpg" alt="" class="image-10">
      <div class="text-block-9">USA</div>
    </div>
    <nav class="dropdown-list w-dropdown-list"><a href="#" class="dropdown-link w-dropdown-link">México</a><a
        href="#" class="dropdown-link w-dropdown-link">Perú</a></nav>
  </div>
  <nav role="navigation" class="nav-menu-copy w-nav-menu"><a href="#"
      class="nav-link borde-verde w-nav-link">ingresa</a><a href="#" class="nav-link w-nav-link">registrate</a>
    <div class="text-block-10">Aprovecha los beneficios</div>
  </nav>
</div> -->
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import setTranslate from '@/config/translate'
export default {
  data: function () {
    return {
      selectLang: {label: process.env.APP_DEFAULT_LABEL_LANG, value: process.env.APP_DEFAULT_LANG},
      selected: '',
      isSeller: false,
      showLogin: false,
      user: '',
      password: '',
      nameSeller: ''
    }
  },
  watch: {
    selectLang: function (val) {
      if (this.authenticated) {
        // this.selectLang = obj.value
        setTranslate(this.$i18n, this.selectLang.value)
        this.setLang(this.selectLang.value)
        this.$eventBus.$emit('errorClear')
        this.$http.put(`${process.env.URL_CHANGE_LANG}/${this.selectLang.value}`).then(response => {
          if (response.data.data !== false) {
            this.$store.dispatch('auth/login', response.data.data)
            this.$eventBus.$emit('loadTocketType')
            this.$forceUpdate('full')
          }
        })
      }
    }
  },
  created () {
    this.selectLang.value = this.lang
    this.selectLang.label = this.lang === 'es' ? 'Español' : 'English'
    this.isSeller = this.seller
    this.nameSeller = this.sellerUser
    this.setLang(this.selectLang.value)
  },
  computed: {
    ...mapGetters({
      authenticated: 'auth/authenticated',
      lang: 'auth/lang',
      seller: 'auth/seller',
      sellerUser: 'auth/sellerUser'
    })
  },
  methods: {
    changeLang: function () {
      if (this.authenticated) {
        setTranslate(this.$i18n, this.selectLang)
        this.setLang(this.selectLang)
        this.$eventBus.$emit('errorClear')
        this.$http.put(`${process.env.URL_CHANGE_LANG}/${this.selectLang}`).then(response => {
          if (response.data.data !== false) {
            this.$store.dispatch('auth/login', response.data.data)
            this.$eventBus.$emit('loadTocketType')
            this.$forceUpdate('full')
          }
        })
      }
    },
    login: function () {
      this.$validator.validateAll().then(result => {
        if (result) {
          var vue = this
          let data = {
            u: this.user,
            p: this.password
          }
          this.setLoader({loader: true})
          this.$http.post(process.env.URL_LOGIN, data).then(response => {
            this.$store.dispatch('auth/loginSeller', response.data.data)
            this.showLogin = !this.showLogin
            this.nameSeller = response.data.data.sellerUser
          }).catch(function (error) {
            vue.$modal.hide('dialog')
            vue.$modal.show('dialog', {
              title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
              text: error.response.data.details,
              buttons: [{
                title: vue.$t('LBL_ACCEPT'),
                handler: () => { vue.$modal.hide('dialog') }
              }]
            })
          }).then(() => {
            this.setLoader({loader: false})
          })
        } else {
          //  this.$modal.hide('dialog')
          //  this.$modal.show('dialog', {
          //    title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          //    text: this.$t('MSG_INCOMPLETE_FORM') + '2',
          //    buttons: [{
          //      title: this.$t('LBL_ACCEPT'),
          //      handler: () => { this.$modal.hide('dialog') }
          //    }]
          //  })
        }
      })
    },
    logout: function () {
      this.$modal.hide('dialog')
      this.$modal.show('dialog', {
        title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
        text: this.$t('COM_USERDROPDOWN_TXT_SIGNOUT_CONFIRMATION'),
        buttons: [
          {
            title: this.$t('LBL_ACCEPT'),
            handler: () => {
              this.$modal.hide('dialog')
              this.setLoader({loader: true})
              this.$http.put(process.env.URL_LOGOUT).then(response => {
                this.$store.dispatch('auth/logout')
                this.$store.dispatch('auth/login', response.data.data.token)
              }).catch(function (error) {
                console.log(error)
              }).then(() => {
                this.setLoader({loader: false})
              })
            }
          },
          {
            title: this.$t('LBL_CANCEL'),
            handler: () => {
              this.$modal.hide('dialog')
            }
          }
        ]
      })
    },
    togglelogin () {
      this.showLogin = !this.showLogin
    },
    ...mapActions({
      setLang: 'auth/setLang',
      setLoader: 'loader/setLoader',
      loginSeller: 'auth/loginSeller'
    })
  }
}
</script>



