<template>
  <div>
    <bx-header></bx-header>
    <router-view></router-view>
    <bx-footer></bx-footer>
  </div>
</template>

<script>
import bxHeader from './header'
import bxFooter from './footer'
export default {
  beforeRouteLeave (to, from, next) {
    if (to.name === 'summary' || to.name === 'Payment' || to.name === 'success') {
      next('/home')
    } else {
      next()
    }
  },
  components: {
    bxHeader,
    bxFooter
  }
}
</script>



