<template lang="html">
  <div class="text-center content-not-found">
    <div class="ico-not-found">
      <img src="/static/images/404.svg" alt="">
    </div>
    <div class="text-not-found">
        <h2>{{ $t('LBL_404_TITLE') }}</h2>
        <p>{{ $t('LBL_404_DESC') }}</p>
        <button type="button" class="btn btn-nf" @click="addNewTravel()">{{ $t('LBL_HOME_PAGE') }}</button>
    </div>
  </div>
</template>
<script>
export default {
  methods: {
    addNewTravel: function () {
      this.$router.push({
        name: 'home'
      })
    }
  }
}
</script>
<style lang="scss">
@import '../styles/base/variables';
.content-not-found{
    position: absolute;
    width: 100%;
    height: 100%;
    background: map-get($colors, gray-01);
}
 .ico-not-found {
        width: 400px;
        margin: 6em auto 0;
        box-shadow: 0 17em 15em -5em map-get($colors, gray-06);
        padding: 1em 0 3em;
        border-bottom: 1px solid map-get($colors, gray-07);
        padding: 1em;
    }
    .text-not-found {
        width: 400px;
        margin: 2em auto;
        color: gray;
        text-align: justify;
        padding: 1em;
    }
    .content-not-found h2 {
        font-size: 2em;
    }
    .ico-not-found img {
        width: 100%;
        opacity: .7;
    }
    .btn-nf {
        background: none;
        color: map-get($colors, gray-13);
        padding: 7px 1em;
        border-radius: 1em;
        border: none;
        background: transparent;
        transition: all .2s ease-in-out;
        font-size: 16px;
        margin: 0 auto;
        display: block;
        text-decoration: underline;
    }
    .btn-nf:hover {
        background: map-get($colors, gray-01);
        cursor: pointer;
        text-decoration: none;
    }
</style>



