export default [
  {
    path: '/',
    component: require('@/layouts/default.vue').default,
    children: [
      {
        path: '',
        redirect: '/home'
        // beforeEnter(to, from, next) {
        //   window.location.href = process.env.URL_DEFAULT_HOME
        // }
      },
      {
        path: '/payment',
        name: 'Payment',
        component: require('@/pages/payment.vue').default
      },
      {
        path: '/services',
        name: 'services',
        component: require('@/pages/services.vue').default
      },
      {
        path: '/summary',
        name: 'summary',
        component: require('@/pages/summary.vue').default
      },
      {
        path: '/success',
        name: 'success',
        component: require('@/pages/completed.vue').default
      },
      {
        path: '/delete',
        name: 'delete',
        component: require('@/pages/purchaseDelete.vue').default
      }
    ]
  },
  {
    path: '/',
    component: require('@/layouts/secondary.vue').default,
    children: [
      {
        path: '/terms-and-conditions',
        name: 'terms',
        component: require('@/pages/terms.vue').default
      },
      {
        path: '/about-us',
        name: 'aboutUs',
        component: require('@/pages/aboutUs.vue').default
      },
      {
        path: '/contact-us',
        name: 'contactUs',
        component: require('@/pages/contactUs.vue').default
      }
    ]
  },
  {
    path: '/home',
    name: 'home',
    // component: require('@/pages/home.vue').default
    beforeEnter (to, from, next) {
      window.location.href = process.env.URL_DEFAULT_HOME
    }
  },
  {
    path: '*',
    name: '404',
    component: require('@/pages/404.vue').default
  }
]



