/* ============
 * Mutations for the search module
 * ============
 *
 * The mutations that are available on the
 * search module.
 */
import Vue from 'vue'
import moment from 'moment'
import {
  SWAP_LOCATIONS,
  SET_TRAVEL,
  CLEAR_TRAVEL,
  CHANGE_TRAVEL_FARE,
  RESTART_STATE
} from './mutation-types'

export default {
  [SWAP_LOCATIONS] (state) {
    let departure = state.DEPARTURE
    let arrival = state.ARRIVAL
    state.DEPARTURE = arrival
    state.ARRIVAL = departure
  },
  [SET_TRAVEL] (state, travel) {
    Object.assign(state, travel)
  },
  [CLEAR_TRAVEL] (state) {
    let emptyState = { DEPARTURE: {}, ARRIVAL: {}, DATE_DEPARTURE: null, DATE_RETURN: null, TYPE_WAY: 0, TYPE_TRAVEL: 0 }
    Object.assign(state, emptyState)
  },
  [CHANGE_TRAVEL_FARE] (state, obj) {
    let currentTravelItem = state.LIST_TRAVEL.find(travel => travel === obj.travelItem)
    Vue.set(currentTravelItem, 'selectedFare', obj.selectedFare)
  },
  [RESTART_STATE] (state) {
    state.DEPARTURE = {}
    state.ARRIVAL = {}
    state.DATE_DEPARTURE = moment().startOf('day')
    state.DATE_RETURN = null
    state.TYPE_WAY = 0
    state.TYPE_TRAVEL = 0
  }
}



