                  <small v-if="errors.has(keyForm(travel, index) + '.phone')">{{ errors.first(keyForm(travel, index) + '.phone') }}</small>
                </div>
              </div>
              <div class="col-12 col-md-5" v-if="validate.validEmail">
                <div class="form-group alert-message">
