                  <label class="col-12 rad checkRuc">
                    <label :for="'RUC' + travel[0].keyGroupTravel + index">{{ $t('PHD_DOC_RUC') }}:</label>
                    <input :id="'RUC' + travel[0].keyGroupTravel + index" type="checkbox" v-model="passengers[index].withRuc" @click="showDataRuc(index)">
                    <i aria-hidden="true"></i>
