                </div>
              </div>
             <div class="col-12 col-md-4 alert-message"  v-if="passengers[index].withRuc" v-on:keyup="validateRuc(index)">
               <input type="text" name="companycode" v-validate data-vv-rules="required|numeric|min:11|max:11" class="form-control" :placeholder="$t('PHD_RUC')" v-model="passengers[index].companyRuc" maxlength="11" key="companycode-input"/>
               <small v-if="errors.has(keyForm(travel, index) + '.companycode')">{{ errors.first(keyForm(travel, index) + '.companycode') }}</small>
