               <input type="text" name="companyname"  v-validate data-vv-rules="required" class="form-control" :placeholder="$t('PHD_RAZON_SOCIAL')" v-model="passengers[index].companyName" key="companyname-input"/>
               <small v-if="errors.has(keyForm(travel, index) + '.companyname')">{{ errors.first(keyForm(travel, index) + '.companyname') }}</small>
