             <div class="col-12 col-md-4 form-group alert-message" v-if="passengers[index].withRuc">
               <input type="text" name="companyaddress"  v-validate data-vv-rules="required" class="form-control" :placeholder="$t('PHD_ADDRESS')" v-model="passengers[index].companyAddress" key="companyaddress-input"/>
               <small v-if="errors.has(keyForm(travel, index) + '.companyaddress')">{{ errors.first(keyForm(travel, index) + '.companyaddress') }}</small>
