                </label>
                <!--div class="row form-group checkPassengerPay">
                  <div class="col-12 ">
                    <input type="checkbox" v-model="passengers[index].pays"> <label class="sexo-m">{{ $t('LBL_PASSENGER_PAYS') }}:</label>
