      <article class="container-alert" v-else>
        <div class="alert-custom-warning">
            <strong>{{ $t('LBL_WARNING') }}!</strong>{{'   ' + $t('LBL_NOT_SEAT_SELECTED') }}
        </div>
      </article>
    </div>
  </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex'
import moment from 'moment'
import DateDropdown from '@/components/datepicker/date'
export default {
  props: {
    travel: {
      required: true
    },
    listTicketType: {
      required: true
    }
  },
  computed: {
    ...mapGetters({
      items: 'cart/items',
      selectTravel: 'travels/selectTravel',
      paymentPassenger: 'payment/passenger',
      listOptions: 'travels/listOptions'
    }),
    getPassenger: function () {
      let returnPassengers = []
      if (this.travel[0].isMultiroute) {
        if ('path' in this.travel[0]) {
          // verificamos cual de los tramos tiene mas pasajeros.
          var indexMaxPassenger = 0
          this.travel[0].path.forEach((oPath, index) => {
            if (oPath.seats.length > this.travel[0].path[indexMaxPassenger].seats.length) {
              indexMaxPassenger = index
            }
          })
          returnPassengers = this.travel[0].path[indexMaxPassenger].seats
        } else {
          returnPassengers = []
        }
      } else {
        if ('seats' in this.travel[0]) {
          returnPassengers = this.travel[0].seats
        } else {
          returnPassengers = []
        }
      }
      return returnPassengers
    }
  },
  data: function () {
    return {
      email: '',
      isEdit: 0,
      passengers: [],
      validate: {
        validRuc: '',
        validDob: '',
        validDocType: '',
        validEmail: '',
        validPhone: '',
        validSex: '',
        validTypeTicket: 0
      },
      country: ''
    }
  },
  components: {
    DateDropdown
  },
  mounted: function () {
    this.showPassengerEmpty(0)
  },
  created: function () {
    let aPassengers = this.getPassenger
    let aTempPassengers = []
    let ticketType = this.listTicketType.length > 1 ? '' : this.listTicketType[0].id
    if (aPassengers.length > 0) {
      aPassengers.forEach(function (element) {
        aTempPassengers.push(Object.assign({},
          {
            firstName: '',
            lastName: '',
            ticketType: ticketType,
            phone: '',
            birthday: '',
            email: '',
            sex: 'M',
            documentType: 'DNI',
            documentNumber: '',
            luggageCount: '',
            frequentPassengerCode: '',
            studentCode: '',
            contactEmergency: '',
            withRuc: false,
            companyRuc: '',
            companyName: '',
            companyAddress: ''
          }
        ))
      })
      this.$set(this, 'passengers', aTempPassengers)
    }
  },
  watch: {
    travel: function () {
      let aPassengers = this.getPassenger
      let aTempPassengers = []
      let ticketType = this.listTicketType.length > 1 ? '' : this.listTicketType[0].id
      if (aPassengers.length > 0) {
        aPassengers.forEach(function (element) {
          if ('dataPerson' in element) {
            aTempPassengers.push(Object.assign({},
              element.dataPerson
            ))
          } else {
            aTempPassengers.push(Object.assign({},
              {
                firstName: '',
                lastName: '',
                ticketType: ticketType,
                phone: '',
                birthday: '',
                email: '',
                sex: 'M',
                documentType: 'DNI',
                documentNumber: '',
                luggageCount: '',
                frequentPassengerCode: '',
                studentCode: '',
                contactEmergency: '',
                withRuc: false,
                companyRuc: '',
                companyName: '',
                companyAddress: ''
              }
            ))
          }
        })
        this.$set(this, 'passengers', aTempPassengers)
      }
    }
  },
  methods: {
    ...mapActions({
      setSelectTravel: 'travels/setSelectTravel',
      setPassengerPays: 'payment/setPassengerPays',
      setLoader: 'loader/setLoader'
    }),
    completeName: function (index) {
      return this.passengers[index].firstName + ' ' + this.passengers[index].lastName
    },
    showDataRuc: function (index) {
      if (!this.passengers[index].withRuc) {
        this.passengers[index].companyRuc = ''
        this.passengers[index].companyName = ''
        this.passengers[index].companyAddress = ''
      }
    },
    yearMaxBirthday () {
      return moment().year(moment().year() - 17).format('YYYY')
    },
    yearMinBirtday () {
      return moment().year(moment().year() - 100).format('YYYY')
    },
    formatDate (input, country) {
      var format = ''
      if (country === 'PE') {
        format = 'DD/MM/YYYY'
      } else {
        format = 'MM-DD-YYYY'
      }
      let dateMoment = moment(input).format('dddd') + ' ' + moment(input).format(format)
      return dateMoment
    },
    verifyMandatoryField: function () {
      for (var i = 0; i < this.travel.length; i++) {
        var skin = this.travel[i].companySkin
        this.listOptions.forEach((oOptions, index) => {
          if (oOptions.skin === skin) {
            var isPeru = oOptions.data.PAIS_RESIDENTE.value === 'PE'
            this.validate.validRuc = isPeru
            this.validate.validDob = oOptions.data.WEB_SALE_FORM_SHOW_DOB.value
            this.validate.validDocType = oOptions.data.WEB_SALE_FORM_SHOW_DOC_TYPE.value
            this.validate.validEmail = oOptions.data.WEB_SALE_FORM_SHOW_EMAIL.value
            this.validate.validPhone = oOptions.data.WEB_SALE_FORM_SHOW_PHONE.value
            this.validate.validSex = oOptions.data.WEB_SALE_FORM_SHOW_SEX.value
            this.validate.validEmergency = isPeru
            this.country = oOptions.data.PAIS_RESIDENTE.value
          }
        })
      }
    },
    editSeatsBus: function (keyGroupTravel) {
      let params = {}
      if (this.items[keyGroupTravel].length === 1) {
        // vieje directo ida
        params = Object.assign({}, {
          selectedFare: {
            fareKey: this.items[keyGroupTravel][0].key,
            fareId: this.items[keyGroupTravel][0].fare
          },
          FARE_ID: this.items[keyGroupTravel][0].fare,
          KEY_FARE: this.items[keyGroupTravel][0].key,
          KEY_FARE_RW: null,
          KEY_GROUP_TRAVEL: keyGroupTravel,
          TRAVEL_ADD_CART_OW: true,
          TRAVEL_ADD_CART_RW: null,
          TYPE_WAY: this.items[keyGroupTravel][0].typeWay
        })
      } else if (this.items[keyGroupTravel].length === 2) {
        // viaje redondo misma empresa
        params = Object.assign({}, {
          selectedFare: {
            fareKey: this.items[keyGroupTravel][0].key,
            fareId: this.items[keyGroupTravel][0].fare
          },
          FARE_ID: this.items[keyGroupTravel][0].fare,
          FARE_ID_RW: this.items[keyGroupTravel][1].fare,
          KEY_FARE: this.items[keyGroupTravel][0].key,
          KEY_FARE_RW: this.items[keyGroupTravel][1].key,
          KEY_GROUP_TRAVEL: keyGroupTravel,
          TRAVEL_ADD_CART_OW: true,
          TRAVEL_ADD_CART_RW: true,
          TYPE_WAY: this.items[keyGroupTravel][0].typeWay,
          TYPE_WAY_RW: this.items[keyGroupTravel][1].typeWay
        })
      }
      this.$eventBus.$emit('showEditSeatsBus', params)
    },
    mostrarvuelta () {
      this.$eventBus.$emit('mostrarvuelta')
    },
    keyForm (travel, index) {
      return travel[0].keyGroupTravel + index
    },
    typeTravel () {
      if (this.travel.length === 1) {
        if (this.travel[0].typeWay === 1) {
          if (this.selectTravel.TYPE_WAY === 1) {
            // viaje redondo pero aun no va al retorno
            return true
          } else {
            // viaje redondo pero ya hiso click en el boton vuelta
            return false
          }
        } else {
          return false
        }
      } else {
        return false
      }
    },
    verifySeatsMarked: function () {
      let validate = true
      if (this.items.length === 0) {
        validate = false
      } else {
        if (this.travel[0].isMultiroute) {
          var indexMaxPassenger = 0
          this.travel[0].path.forEach((oPath, index) => {
            if (oPath.seats.length > this.travel[0].path[indexMaxPassenger].seats.length) {
              indexMaxPassenger = index
            }
          })
          this.travel[0].path[indexMaxPassenger].seats.forEach((seat, index) => {
            for (var i = 0; i < this.travel[0].numberPaths; i++) {
              if ('path' in this.travel[0]) {
                var oPath = this.travel[0].path.find(obj => obj.indexPath === i + 1)
                if (typeof oPath === 'undefined' || !(index in oPath.seats)) {
                  validate = false
                }
              } else {
                validate = false
              }
            }
          })
        }
      }
      return validate
    },
    passengerIsEmpty: function (fullName) {
      if (!fullName) {
        return true
      } else {
        return false
      }
    },
    showPassenger: function () {
      if (this.travel.length === 1) {
        if (this.travel[0].typeWay === 0) {
          // viaje solo ida: se muestra el pasajero
          return true
        } else {
          // viaje redondo pero aun estan completando la ida
          return false
        }
      } else if (this.travel.length === 2) {
        return true
      } else {
        return false
      }
    },
    showPassengerEmpty: function (index) {
      this.verifyMandatoryField()
      if (this.showPassenger()) {
        let aPassenger = this.getPassenger
        if (aPassenger.length > 0) {
          if (index >= aPassenger.length) {
            // esto pasa cuando estoy registrando el utimo pasajero y una ves terminado se llama a esta funcion
            this.isEdit = 0
          } else {
            for (var i = index; i < aPassenger.length; i++) {
              if (!aPassenger[i].fullName) {
                // no tiene pasajero registrado
                this.isEdit = aPassenger[i].seatId
                break
              } else {
                if (i === aPassenger.length - 1) {
                  this.isEdit = 0
                }
              }
            }
          }
        }
      }
    },
    showRegister: function (seat) {
      this.isEdit = seat.seatId
    },
    showEdit: function (seat, index, scoped, keyGroupTravel) {
      this.isEdit = seat.seatId
      this.errors.clear(scoped)
      this.passengers[index].firstName = seat.dataPerson.firstName
      this.passengers[index].lastName = seat.dataPerson.lastName
      this.passengers[index].ticketType = seat.dataPerson.ticketType
      this.passengers[index].birthday = seat.dataPerson.birthday
      this.passengers[index].phone = seat.dataPerson.phone
      this.passengers[index].email = seat.dataPerson.email
      this.passengers[index].sex = seat.dataPerson.sex
      this.passengers[index].documentType = seat.dataPerson.documentType
      this.passengers[index].documentNumber = seat.dataPerson.documentNumber
      this.passengers[index].contactEmergency = seat.dataPerson.contactEmergency
      this.passengers[index].withRuc = seat.dataPerson.withRuc
      this.passengers[index].companyRuc = seat.dataPerson.companyRuc
      this.passengers[index].companyName = seat.dataPerson.companyName
      this.passengers[index].companyAddress = seat.dataPerson.companyAddress
      let keyPayment = keyGroupTravel + seat.seatId
      this.passengers[index].pays = (this.paymentPassenger.KEY === keyPayment)
    },
    deletePassenger: function (aSeat, index, keyGroupTravel, seatPassenger) {
      var vue = this
      let config = {
        url: process.env.URL_MULTIPLE_REMOVE_SEAT,
        data: {
          seats: aSeat.map((obj) => {
            return {
              seat: obj.seatId,
              keyPath: obj.keyPath,
              key: obj.key
            }
          })
        },
        method: 'DELETE'
      }
      this.setLoader({loader: true})
      this.$http(config).then(response => {
        let keyPayment = keyGroupTravel + seatPassenger.seatId
        this.setPassengerPays({
          VALUE: false,
          KEY: keyPayment,
          PASSENGER: null
        })
        this.$eventBus.$emit('loadShoppingCart')
        if (this.travel[0].isMultiroute) {
          if ('path' in this.travel[0]) {
            this.$eventBus.$emit('updateBus', {aSeat: aSeat})
            // this.$emit('verifyCompleteShoppingcart')
            // this.$forceUpdate()
          } else {
            // ningun asiento agregado
            this.removeTravel(this.travel[0].key, this.travel[0].keyGroupTravel)
          }
        } else {
          if ('seats' in this.travel[0]) {
            this.$eventBus.$emit('updateBus', {aSeat: aSeat})
            // this.$emit('verifyCompleteShoppingcart')
            // this.$forceUpdate()
          } else {
            // ningun asiento agregado
            this.removeTravel(this.travel[0].key, this.travel[0].keyGroupTravel)
          }
        }
        // this.passengers.splice(index, 1)
        this.showPassengerEmpty(index + 1)
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      }).then(() => {
        this.setLoader({loader: false})
      })
    },
    passengerSaveOrEdit: function (aSeat, keyGroupTravel, index, seatId) {
      this.registerOption = false
      let personInfo = {
        aSeat: aSeat,
        fullName: this.completeName(index),
        dataPerson: {
          firstName: this.passengers[index].firstName,
          lastName: this.passengers[index].lastName,
          ticketType: this.passengers[index].ticketType,
          sex: this.passengers[index].sex,
          email: this.passengers[index].email,
          idBusCel: aSeat.map(function (obj) { return obj.seatId }),
          phone: this.passengers[index].phone,
          luggageCount: 0,
          frequentPassengerCode: '',
          studentCode: ''
        }
      }
      // let keyPassengerPay = keyGroupTravel + personInfo.dataPerson.idBusCel.join('')
      let keyPassengerPay = keyGroupTravel + seatId
      this.setPassengerPays({
        VALUE: this.passengers[index].pays,
        KEY: keyPassengerPay,
        PASSENGER: personInfo.dataPerson
      })
      personInfo.dataPerson.birthday = this.passengers[index].birthday
      personInfo.dataPerson.documentType = this.passengers[index].documentType
      personInfo.dataPerson.documentNumber = this.passengers[index].documentNumber
      personInfo.dataPerson.contactEmergency = this.passengers[index].contactEmergency
      personInfo.dataPerson.withRuc = this.passengers[index].withRuc
      personInfo.dataPerson.companyRuc = this.passengers[index].companyRuc
      personInfo.dataPerson.companyName = this.passengers[index].companyName
      personInfo.dataPerson.companyAddress = this.passengers[index].companyAddress
      let passengers = []
      if (this.items[keyGroupTravel].length === 1) {
        // vieje directo ida
        passengers.push(Object.assign({}, personInfo.dataPerson, {travels: [this.items[keyGroupTravel][0].key]}))
      } else if (this.items[keyGroupTravel].length === 2) {
        // no se valida si son diferentes empresas ya que siempre se envia la misma estructura solo en el servicio passengerTemp
        var keyTravels = []
        this.items[keyGroupTravel].forEach((obj) => {
          keyTravels.push(obj.key)
        })
        passengers.push(Object.assign({}, personInfo.dataPerson, {travels: keyTravels}))
      } else {
        alert('El carrito de compras no esta bien creado')
        return false
      }
      var vue = this
      this.setLoader({loader: true})
      this.loader = !this.loader
      this.$http.post(process.env.URL_ADD_PASSENGER_TEMP, { passengers: passengers }).then(response => {
        this.loader = !this.loader
        let data = response.data
        if (data.code === 10) {
          this.$eventBus.$emit('showShoppingCart')
          this.showPassengerEmpty(index + 1)
        } else {
          alert('error al guardar los pasajeros')
        }
        // this.$eventBus.$emit('clearDateDropdown')
        // this.clearInput()
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      }).then(() => {
        this.setLoader({loader: false})
      })
    },
    validateBeforeSubmit: function (scope, seat, keyGroupTravel, index, seatId) {
      // var countryCode = this.items[keyGroupTravel][0].countryCode
      // if (countryCode === 'PE' && this.passenger.birthday === '' || this.passenger.birthday === null) {
      //   this.$modal.hide('dialog')
      //   this.$modal.show('dialog', {
      //     title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
      //     text: this.$t('MSG_INCOMPLETE_FORM') + '1',
      //     buttons: [{
      //       title: this.$t('LBL_ACCEPT'),
      //       handler: () => { this.$modal.hide('dialog') }
      //     }]
      //   })
      // }
      this.$validator.validateAll(scope).then(result => {
        if (result) {
          this.passengerSaveOrEdit(seat, keyGroupTravel, index, seatId)
        } else {
          //  this.$modal.hide('dialog')
          //  this.$modal.show('dialog', {
          //    title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          //    text: this.$t('MSG_INCOMPLETE_FORM') + '2',
          //    buttons: [{
          //      title: this.$t('LBL_ACCEPT'),
          //      handler: () => { this.$modal.hide('dialog') }
          //    }]
          //  })
        }
      })
    },
    numberSeats: function (index) {
      var numberSeats = []
      this.travel.forEach((oTravel) => {
        if (oTravel.isMultiroute) {
          oTravel.path.forEach((oPath) => {
            if (index in oPath.seats) {
              numberSeats.push({
                seatId: oPath.seats[index].seatId,
                number: oPath.seats[index].number,
                keyPath: oPath.seats[index].keyPath,
                key: oTravel.key,
                keyGroupTravel: oTravel.keyGroupTravel,
                isMultiroute: oTravel.isMultiroute
              })
            }
          })
        } else {
          if (index in oTravel.seats) {
            numberSeats.push({
              seatId: oTravel.seats[index].seatId,
              number: oTravel.seats[index].number,
              keyPath: oTravel.seats[index].keyPath,
              key: oTravel.key,
              keyGroupTravel: oTravel.keyGroupTravel,
              isMultiroute: oTravel.isMultiroute
            })
          }
        }
      })
      return numberSeats
    },
    getPrice: function (index) {
      var total = 0
      if (this.travel[0].isMultiroute) {
        this.travel[0].path.forEach((oPath) => {
          if (index in oPath.seats) {
            total = total + oPath.seats[index].total
          }
        })
      } else {
        if (index in this.travel[0].seats) {
          total = total + this.travel[0].seats[index].total
        }
      }
      if (this.travel[1]) {
        if (this.travel[1].isMultiroute) {
          this.travel[1].path.forEach((oPath) => {
            if (index in oPath.seats) {
              total = total + oPath.seats[index].total
            }
          })
        } else {
          if (index in this.travel[1].seats) {
            total = total + this.travel[1].seats[index].total
          }
        }
      }
      return total
    },
    completeMarkedSeat: function (index) {
      var complete = 1
      var indexTravel = 0
      if (this.travel[indexTravel].isMultiroute) {
        for (let i = 0; i < this.travel[indexTravel].numberPaths; i++) {
          if ('path' in this.travel[indexTravel]) {
            let oPath = this.travel[indexTravel].path.find(obj => obj.indexPath === i + 1)
            if (typeof oPath === 'undefined' || !(index in oPath.seats)) {
              complete = 0
            }
          }
        }
      } else {
        if ('seats' in this.travel[indexTravel] && index in this.travel[indexTravel].seats) {
          complete = 1
        } else {
          complete = 0
        }
      }
      // Verificar vuelta
      if (this.travel[1]) {
        indexTravel = 1 // Vuelta
        if (this.travel[indexTravel].isMultiroute) {
          for (let i = 0; i < this.travel[indexTravel].numberPaths; i++) {
            if ('path' in this.travel[indexTravel]) {
              let oPath = this.travel[indexTravel].path.find(obj => obj.indexPath === i + 1)
              if (typeof oPath === 'undefined' || !(index in oPath.seats)) {
                complete = 0
              }
            }
          }
        } else {
          if ('seats' in this.travel[indexTravel] && index in this.travel[indexTravel].seats) {
            complete = 1
          } else {
            complete = 0
          }
        }
      }
      return complete
    },
    normalizeSeats: function (index, indexTravel) {
      var numberSeats = []
      if (this.travel[indexTravel].isMultiroute) {
        for (var i = 0; i < this.travel[indexTravel].numberPaths; i++) {
          if ('path' in this.travel[indexTravel]) {
            var oPath = this.travel[indexTravel].path.find(obj => obj.indexPath === i + 1)
            if (typeof oPath === 'undefined' || !(index in oPath.seats)) {
              numberSeats.push({
                seatId: null,
                number: '##',
                keyPath: null,
                indexPath: i + 1,
                key: this.travel[indexTravel].key,
                keyGroupTravel: this.travel[indexTravel].keyGroupTravel,
                isMultiroute: this.travel[indexTravel].isMultiroute
              })
            } else {
              numberSeats.push({
                seatId: oPath.seats[index].seatId,
                number: oPath.seats[index].number,
                keyPath: oPath.seats[index].keyPath,
                indexPath: i + 1,
                key: this.travel[indexTravel].key,
                keyGroupTravel: this.travel[indexTravel].keyGroupTravel,
                isMultiroute: this.travel[indexTravel].isMultiroute
              })
            }
          } else {
            numberSeats.push({
              seatId: null,
              number: '##',
              keyPath: null,
              indexPath: i + 1,
              key: this.travel[indexTravel].key,
              keyGroupTravel: this.travel[indexTravel].keyGroupTravel,
              isMultiroute: this.travel[indexTravel].isMultiroute
            })
          }
        }
      } else {
        if ('seats' in this.travel[indexTravel] && index in this.travel[indexTravel].seats) {
          numberSeats.push({
            seatId: this.travel[indexTravel].seats[index].seatId,
            number: this.travel[indexTravel].seats[index].number,
            keyPath: this.travel[indexTravel].seats[index].keyPath,
            key: this.travel[indexTravel].key,
            keyGroupTravel: this.travel[indexTravel].keyGroupTravel,
            isMultiroute: this.travel[indexTravel].isMultiroute
          })
        } else {
          numberSeats.push({
            seatId: null,
            number: '##',
            keyPath: null,
            key: this.travel[indexTravel].key,
            keyGroupTravel: this.travel[indexTravel].keyGroupTravel,
            isMultiroute: this.travel[indexTravel].isMultiroute
          })
        }
      }
      return numberSeats
    },
    removeTravel: function (keyTravel, keyGroupTravel) {
      var vue = this
      this.$http.delete(process.env.URL_REMOVE_SHOPPINGCART, { data: {key: keyTravel} }).then(response => {
        this.setLoader({loader: true})
        this.$eventBus.$emit('loadShoppingCart')
        if (this.selectTravel.KEY_GROUP_TRAVEL === keyGroupTravel) {
          // si se elimina el viaje actual por el boton del carrito de compras
          this.setSelectTravel({
            KEY_FARE: null,
            KEY_FARE_RW: null,
            TRAVEL_ADD_CART_OW: false,
            TRAVEL_ADD_CART_RW: false,
            TYPE_WAY: 0,
            FARE_ID: null
          })
          this.$eventBus.$emit('loadTripOneWay')
        }
        this.$emit('verifyCompleteShoppingcart')
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      }).then(() => {
        this.setLoader({loader: false})
      })
    },
    validateRuc (index) {
      var code = this.passengers[index].companyRuc
      if (code.length >= 10) {
        this.setLoader({loader: true})
        this.loader = !this.loader
        this.$http.get(`${process.env.URL_SEARCH_SUNAT}?number=${this.passengers[index].companyRuc}&type=RUC`).then(response => {
          this.loader = !this.loader
          let data = response.data
          if (data.code === 10) {
            var result = data.data[0].searchResult
            this.passengers[index].companyName = result.RazonSocial
            this.passengers[index].companyAddress = result.Direccion
          } else {
            alert('error al guardar los pasajeros')
          }
        }).catch(function (error) {
          console.log(error)
        }).then(() => {
          this.setLoader({loader: false})
        })
      }
    },
    validateReniec (index) {
      var code = this.passengers[index].documentNumber
      if (code.length === 8) {
        this.setLoader({loader: true})
        this.loader = !this.loader
        this.$http.get(`${process.env.URL_SEARCH_RENIEC}?number=${this.passengers[index].documentNumber}&type=DNI`).then(response => {
          this.loader = !this.loader
          let data = response.data
          if (data.code === 10) {
            var result = data.data.searchResult
            this.passengers[index].firstName = result.Nombre
            this.passengers[index].lastName = result.Paterno + ' ' + result.Materno
          } else {
            alert('error al guardar los pasajeros')
          }
        }).catch(function (error) {
          console.log(error)
        }).then(() => {
          this.setLoader({loader: false})
        })
      }
    },
    showField (fieldName) {
      var response
      switch (fieldName) {
        case 'radioDocumentType':
          response = (this.travel[0].countryCode === 'PE')
          break
        case 'documentNumber':
          response = (this.travel[0].countryCode === 'PE')
          break
        case 'birthdate':
          response = (this.travel[0].countryCode === 'PE')
          break
        case 'checkRuc':
          response = (this.travel[0].countryCode === 'PE')
          break
        case 'teléfonoemergencia':
          response = (this.travel[0].countryCode === 'PE')
          break
        default:
          response = true
          break
      }
      return response
    },
    changeDocumentType () {
      this.errors.remove('documento')
    }
  }
}
</script>

<style lang="scss">
@import '../../styles/base/variables';
.frm-passenger{
  div{
    .alert-message{
      text-align: left;
      small{
        color: red;
      }
    }
  }
  .birthdate{
    padding: 0;
    .date-dropdown-container{
      >div:first-child{
        .dropdown-menu{
          min-width: 40px!important;
          li{
            a{
              padding-left: 0;
              text-align: center;
              padding-right: 0;
            }
          }
        }
      }
      >div:nth-child(2){
        margin: 0 1em;
        .dropdown-menu{
          min-width: 60px!important;
          li{
            a{
              padding-left: 0;
              text-align: center;
              padding-right: 0;
            }
          }
        }
      }
      >div:last-child{
        .dropdown-menu{
          min-width: 40px!important;
          width: 90px!important;
        }
      }
    }
  }
}
.radioSex{
  margin-bottom: 0px !important;
  .alert-message{
    margin-bottom: 1rem !important;
  }
}
.container-alert {
  padding: 5px !important
}
.alert-custom-warning {
  background-color: map-get($colors, warning-light) !important;
  border-color: map-get($colors, warning-dark) !important;
  color: map-get($colors, warning-text) !important;
  padding: 4px 10px;
  text-align: left !important;
  strong {
    font-weight: bold;
  }
}
input[name='name'], input[name='lastname'] {
  text-transform: capitalize;
}

.control--checkbox {
  position: relative;
  display: block;
  padding: 0px 0px 0px 45px;
  font-size: 14px;
  cursor: pointer;
  text-align: left;
}

.control--checkbox input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: -1;
}

.checkbox {
  position: absolute;
  top: 0px;
  left: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  background: map-get($colors, secondary-01);
  border: 2px solid map-get($colors, secondary-01);
  border-radius: 2px;
  /*transition: border-color .2s;
  transition-delay: .3s;*/
}

.filler {
  position: relative;
  width: 16px;
  height: 16px;
  background: white;
  transform: scale(1, 1);
  transition: transform .2s;
  transition-delay: .2s;
  will-change: transform;
}

.control--checkbox .checkbox svg {
  position: absolute;
  top: -2px;
  left: -2px;
}

.control--checkbox polyline {
  position: absolute;
  fill: none;
  stroke: #FFF;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 18px;
  stroke-dashoffset: 18px;
  transition: stroke-dashoffset .2s;
  transition-delay: 0s;
  will-change: stroke-dashoffset;
}

/* Checked state */
.control--checkbox input:checked {
  ~.checkbox .filler {
    transform: scale(0, 0);
    border-radius: 3px;
    transition-delay: 0s;
  }

  ~.checkbox polyline {
    stroke-dashoffset: 0;
    transition-delay: .2s;
  }
}

@keyframes bounce {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(0.8, 0.8);
  }
  100% {
    transform: scale(1, 1);
  }
}
</style>



