/* ============
 * Getters for the cart module
 * ============
 *
 * The getters that are available on the
 * cart module.
 */

export default {
  shoppingCartHasTrips: (state) => {
    if (Object.keys(state.items).length > 0) {
      return true
    } else {
      return false
    }
  },
  items: (state) => state.items,
  total: (state) => state.symbol + ' ' + state.total
}



