<template>
  <div class="containerinfoCity">
    <div class="ui fluid search selection dropdown select-basic"
    :class="{ 'active visible':showMenu, 'error': isError }"
    @click="openOptions">
    <input :id="id" class="search"
    autocomplete="off"
    v-model="searchText"
    ref="input"
    @focus="openOptions"
    @blur="blurInput"
    @keydown.up="prevItem"
    @keydown.down="nextItem"
    @keyup.enter="enterItem"
    @keydown.tab="tabItem"
    @keydown.delete="deleteTextOrItem"
    @keyup="listDeparture" v-if="boolKeyUp"
    />
    <input :id="id" class="search"
    autocomplete="off"
    v-model="searchText"
    ref="input"
    @focus="openOptions"
    @blur="blurInput"
    @keydown.up="prevItem"
    @keydown.down="nextItem"
    @keyup.enter="enterItem"
    @keydown.tab="tabItem"
    @keydown.delete="deleteTextOrItem"
    @keyup="listArrival" v-if="!boolKeyUp"
    />
    <div class="text container-text-select"
      :class="textClass">{{inputText}}
    </div>
    <figure><img src="/static/images/arrow-bottom.svg" alt=""></figure>
    <div class="menu"
      ref="menu"
      @mousedown.prevent
      :class="menuClass"
      :style="menuStyle">
      <template v-for="(option, idx) in filteredOptions">
        <div :key="idx" class="item"
          :class="{ 'selected': option.selected, 'current': pointer === idx }"
          @click.stop="selectItem(option)"
          @mousedown="mousedownItem"
          @mouseenter="pointerSet(idx)" v-html="highlight(option.value)">
          </div>
      </template>
    </div>
  </div>
  </div>
</template>

<script>
import common from '@/libs/common.js'

export default {
  props: {
    id: {
      type: String
    },
    selectedOption: {
      type: Object,
      default: () => { return { globalIdCity: '', value: '' } }
    },
    isError: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    },
    boolKeyUp: {
      type: Boolean,
      default: false
    },
    departureId: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      showMenu: false,
      searchText: '',
      pointer: 0,
      options: []
    }
  },
  watch: {
    filteredOptions () {
      this.pointerAdjust()
    }
  },
  computed: {
    inputText: function () {
      if (this.searchText) {
        return ''
      } else {
        let text = this.placeholder
        if (this.selectedOption.value) {
          text = this.selectedOption.value
        }
        return text
      }
    },
    textClass: function () {
      if (!this.selectedOption.value && this.placeholder) {
        return 'default'
      } else {
        return ''
      }
    },
    menuClass: function () {
      return {
        visible: this.showMenu,
        hidden: !this.showMenu
      }
    },
    menuStyle: function () {
      return {
        display: this.showMenu ? 'block' : 'none'
      }
    },
    filteredOptions: function () {
      if (this.searchText.length >= 3) {
        return this.options.filter(option => {
          return option.value.match(new RegExp('^' + this.searchText, 'i'))
        })
      } else {
        return this.options
      }
    }
  },
  methods: {
    deleteTextOrItem: function () {
      if (!this.searchText && this.selectedOption) {
        this.selectItem({})
        this.openOptions()
      }
    },
    listDeparture: function () {
      var vue = this
      if (vue.searchText.length >= 3) {
        vue.$http.get(`${process.env.URL_LIST_DEPARTURES}?term=` + vue.searchText).then(response => {
          vue.options = response.data.data
          if (response.data.data.length > 0) {
            vue.options = response.data.data
          } else {
            vue.options = [ { id: 0, value: vue.$t('NO_ITEMS') } ]
          }
        }).catch(error => {
          vue.$modal.hide('dialog')
          vue.$modal.show('dialog', {
            title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
            text: error.response.data.details,
            buttons: [{
              title: vue.$t('LBL_ACCEPT'),
              handler: () => { vue.$modal.hide('dialog') }
            }]
          })
        })
      }
    },
    listArrival: function () {
      var vue = this
      this.$http.get(`${process.env.URL_LIST_ARRIVALS}?departure=${vue.departureId}&term=${vue.searchText}`).then(response => {
        if (response.data.data.length > 0) {
          vue.options = response.data.data
        } else {
          vue.options = [ { id: 0, value: vue.$t('NO_ITEMS') } ]
        }
      }).catch(error => {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      })
    },
    openOptions: function () {
      common.openOptions(this)
    },
    blurInput: function () {
      common.blurInput(this)
    },
    closeOptions: function () {
      common.closeOptions(this)
    },
    prevItem: function () {
      common.prevItem(this)
    },
    nextItem: function () {
      common.nextItem(this)
    },
    enterItem: function () {
      common.enterItem(this)
    },
    tabItem: function () {
      common.tabItem(this)
    },
    pointerSet: function (index) {
      common.pointerSet(this, index)
    },
    pointerAdjust: function () {
      common.pointerAdjust(this)
    },
    mousedownItem: function () {
      common.mousedownItem(this)
    },
    selectItem: function (option) {
      this.searchText = '' // reset text when select item
      this.closeOptions()
      this.$emit('select', option)
    },
    highlight: function (cityName) {
      if (!this.searchText) {
        return cityName
      }
      return cityName.replace(new RegExp('(?![^&;]+;)(?!<[^<>]*)(' + this.searchText + ')(?![^<>]*>)(?![^&;]+;)', 'gi'), match => {
        return '<b>' + match + '</b>'
      })
    }
  }
}
</script>

<style scoped lang="scss">
@import '../styles/base/variables';
.containerinfoCity{
  width: 46%;
}
.select-basic{
  z-index: 9;
  .container-text-select{
    width: 100%;
    text-align: center;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    min-height: 20px;
  }
  figure{
    position: absolute;
    right: 1em;
    top: 0.8em;
  }
  .menu{
    position: absolute;
    background: map-get($colors, white);
    left: 0px;
    top: 2.8em;
    overflow-y: auto;
    height:150px;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
    text-align: left;
    &::-webkit-scrollbar {
      width: 12px;
    }
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.2);
    }

    &::-webkit-scrollbar-thumb {
      background: map-get($colors, primary-01);
      -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.1);
    }
    div{
      text-align: left;
    }
  }
}
.ui.fluid.dropdown {
    display: block;
    min-width: 0em;
}
.ui.selection.dropdown {
    cursor: pointer;
    word-wrap: break-word;
    line-height: 1em;
    white-space: normal;
    outline: 0;
    transform: rotateZ(0deg);
    min-width: 14em;
    min-height: 2.71428571em;
    display: inline-block;
    padding: 0.78571429em 2.1em 0.78571429em 1em;
    color: map-get($colors, gray-13);
    box-shadow: none;
    border-radius: 0.28571429rem;
    transition: box-shadow 0.1s ease, width 0.1s ease;
    width: 100%;
    top: .2em;
}
.ui.search.dropdown > input.search {
    background: none transparent !important;
    border: none !important;
    box-shadow: none !important;
    cursor: text;
    top: 0em;
    left: 1px;
    width: 100%;
    outline: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    padding: inherit;
}
.ui.search.selection.dropdown > input.search {
    line-height: 1.21428571em;
    padding: 0.67857143em 2.1em 0.67857143em 1em;
}
.ui.search.dropdown > input.search {
    position: absolute;
    z-index: 2;
}
.ui.dropdown:not(.button) > .default.text, .ui.default.dropdown:not(.button) > .text {
    color: map-get($colors, gray-13);
}
.ui.search.dropdown > .text {
    cursor: text;
    position: relative;
    left: 1px;
    z-index: 3;
}
.ui.dropdown > .text {
    display: inline-block;
    transition: none;
}
/* Menu Item Hover */
.ui.dropdown .menu > .item:hover {
  background: none transparent !important;
  padding: 1em;
}
/* Menu Item Hover for Key event */
.ui.dropdown .menu > .item.current {
  background: rgba(0, 0, 0, 0.05) !important;
  padding: 1em;
}

.ui.dropdown {
  min-height: 14px !important;
}

.ui.dropdown .text {
  font-size: 16px !important;
}

.ui.dropdown .menu > .item {
  font-size: 14px !important;
  padding: 1em;
}

.ui.search.selection.dropdown > input.search {
  font-size: 16px !important;
  line-height: 14px !important;
}

.ui.selection.dropdown .menu {
  min-width: calc(100%) !important;
  width: calc(100%) !important;
}
.ui.label > .delete.icon:after {
  content: "x";
  color: map-get($colors, secondary-09);
  font-size: 16px;
  font-style: normal;
}
@media (max-width: 576px) {
  .ui.selection.dropdown {
    min-width: auto;
  }
}
.ui.fluid.search.selection.dropdown.select-basic {
  display: inline-block;;
  justify-content: space-between;
}
@media (max-width: 575px) {
  // .ui.fluid.search.selection.dropdown.select-basic {
  //   z-index: 99549
  // }
  .first-element, .second-element {
    .ui.fluid.search.selection.dropdown.select-basic {
      padding: .5rem;
      background: none;
  }

  }
  .second-element .ui.fluid.search.selection.dropdown.select-basic {
    z-index: 1;
    // background: none;
  }
}

</style>



