<template>
  <div>
    <div class="content-services" v-show="!editSeatsBus" @click="closeAllToolTip()">
      <bx-widget @loadTravels="loadTravels"></bx-widget>
      <bx-travel-info v-if="this.listTravels.length > 0" @loadTravelsReturn="loadTravelsReturn">
      </bx-travel-info>
      <div class="background-resumen"></div>
      <div class="header-travel" v-if="this.listTravels.length > 0" >
        <div class="width-general row">
          <div class="col-sm-6 border-right-light summary-travel">{{ $t('LBL_SELECT_TRAVEL') }}</div>
          <div class="col-sm-6 row header-price">
            <div class="col-sm-3">
            </div>
            <div class="col-sm-9">
              <div class="filters" >
                <v-select :placeholder="$t('LBL_ORDER_BY')" v-model="selectOrder"
                  :options="optionsOrder"></v-select>
              </div>
              <div>
                <v-popover>
                  <button class="form-control" @click="togglefilter()">{{ $t('LBL_FILTERS') }} <i class="fa fa-filter tooltip-target" aria-hidden="true"></i></button>
                  <template slot="popover">
                    <div v-for="(company, index) in listCompany" :key="index" class="bg-gray-filter">
                      <label :for="company.skin">{{ company.name }}</label>
                      <input type="checkbox" :id="company.skin" :value="company.skin" v-model="filterCompany">
                    </div>
                  </template>
                </v-popover>
              </div>
            </div>
          </div>
        </div>
        <bx-travel-list :travels="filterTravels" :showCompany="showCompany">
        </bx-travel-list>
        <bx-shopping-cart @loadTravelsReturn="loadTravelsReturn" @loadTravels="loadTravels" :listTicketType='listTicketType'>
        </bx-shopping-cart>
      </div>
    </div>
    <div class="content-services" v-if="editSeatsBus">
      <button @click="editSeatsBus = false" v-if="TYPE_WAY === 0 || TYPE_WAY === 2">{{ $t('LBL_BACK') }}</button>
      <button @click="editSeatsBus = false" v-if="TYPE_WAY === 1">{{ $t('LBL_CONTINUE') }}</button>
      <bx-travel-seats
          :travelItem="travelItem"
          :selectedFare="selectedFare"
          :FARE_ID="FARE_ID"
          :KEY_FARE="KEY_FARE"
          :KEY_FARE_RW="KEY_FARE_RW"
          :KEY_GROUP_TRAVEL="KEY_GROUP_TRAVEL"
          :TRAVEL_ADD_CART_OW="TRAVEL_ADD_CART_OW"
          :TRAVEL_ADD_CART_RW="TRAVEL_ADD_CART_RW"
          :TYPE_WAY="TYPE_WAY" ></bx-travel-seats>
    </div>
  </div>
</template>

<script>
import bxWidget from '@/components/widget/index'
import bxTravelSeats from '@/components/travels/detail'
import bxTravelInfo from '@/components/travels/info'
import bxTravelList from '@/components/travels/list'
import bxShoppingCart from '@/components/shopping/cart'
import { mapActions, mapGetters } from 'vuex'

export default {
  data: function () {
    return {
      // /.. User and Location
      user: {
        u: 'boletoxpress',
        p: '123qaz',
        lang: process.env.APP_DEFAULT_LANG
      },
      location: {
        latitude: '',
        longitude: ''
      },
      // ../
      editSeatsBus: false,
      travelItem: {},
      selectedFare: {},
      FARE_ID: {
        required: true
      },
      KEY_FARE: {
        required: true
      },
      KEY_FARE_RW: {
        required: true
      },
      KEY_GROUP_TRAVEL: {
        required: true
      },
      TRAVEL_ADD_CART_OW: {
        required: true
      },
      TRAVEL_ADD_CART_RW: {
        required: true
      },
      TYPE_WAY: {
        required: true
      },
      filterCompany: [],
      showFilters: false,
      showCompany: false,
      listCompany: [],
      listTravels: [],
      selectOrder: '',
      listTicketType: []
    }
  },
  // watch: {
  //   optionsOrder: function (value) {
  //     this.selectOrder = this.selectOrder === '' ? '' : value[this.selectOrder.value - 1]
  //   }
  // },
  created: function () {
    this.$eventBus.$off()
    this.removeLocalStorage()
    // this.startSession()
    this.getLocation()
  },
  mounted: function () {
    this.loadSearchTravel()
    this.$eventBus.$on('showEditSeatsBus', ($event) => {
      this.selectedFare = $event.selectedFare
      this.FARE_ID = $event.FARE_ID
      this.KEY_FARE = $event.KEY_FARE
      this.KEY_FARE_RW = $event.KEY_FARE_RW
      this.KEY_GROUP_TRAVEL = $event.KEY_GROUP_TRAVEL
      this.TRAVEL_ADD_CART_OW = $event.TRAVEL_ADD_CART_OW
      this.TRAVEL_ADD_CART_RW = $event.TRAVEL_ADD_CART_RW
      this.TYPE_WAY = $event.TYPE_WAY
      this.editSeatsBus = true
    })
    this.$eventBus.$on('loadTripOneWay', ($event) => {
      this.loadTravels()
    })
    this.$eventBus.$on('loadTocketType', ($event) => {
      this.loadTicketType()
    })
  },
  beforeRouteLeave (to, from, next) {
    if (to.name === 'success') {
      next(false)
    } else {
      if (this.shoppingCartHasTrips && to.name === 'home') {
        this.$modal.hide('dialog')
        this.$modal.show('dialog', {
          title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: this.$t('MSG_VALIDATION_LEAVE_PAGE'),
          buttons: [
            {
              title: this.$t('LBL_ACCEPT'),
              handler: () => {
                this.$modal.hide('dialog')
                this.setLoader({loader: true})
                this.$http.put(process.env.URL_END_SESSION).then(response => {
                  console.log(response)
                  this.$eventBus.$off()
                }).catch(function (error) {
                  console.log(error)
                }).then(() => {
                  this.setLoader({loader: false})
                  next()
                })
              }
            },
            {
              title: this.$t('LBL_CANCEL'),
              handler: () => {
                this.$modal.hide('dialog')
                next(false)
              }
            }
          ]
        })
      } else {
        next()
      }
    }
  },
  computed: {
    ...mapGetters({
      // /.. User and lang
      lang: 'auth/lang',
      authenticated: 'auth/authenticated',
      // ../
      items: 'cart/items',
      travels: 'travels/listTravel',
      search: 'search/travel',
      selectTravel: 'travels/selectTravel',
      loader: 'loader/loader',
      shoppingCartHasTrips: 'cart/shoppingCartHasTrips'
    }),
    filterTravels () {
      let listTravelsTemp = this.listTravels.slice(0)
      if (listTravelsTemp.length > 0) {
        if (this.selectOrder !== null) {
          if (this.selectOrder.value === '1') {
            // De menor a mayor Precio
            listTravelsTemp.sort(function (a, b) {
              return Math.min.apply(null, a.fare.map((o) => o.price)) - Math.min.apply(null, b.fare.map((o) => o.price))
            })
          } else if (this.selectOrder.value === '2') {
            // De mayor a menor Precio
            listTravelsTemp.sort(function (a, b) {
              return Math.max.apply(null, b.fare.map((o) => o.price)) - Math.max.apply(null, a.fare.map((o) => o.price))
            })
          } else if (this.selectOrder.value === '3') {
            // De menor a mayor Hora salida
            listTravelsTemp.sort(function (a, b) {
              if (a.travelDepartureDatetime > b.travelDepartureDatetime) {
                return 1
              }
              if (a.travelDepartureDatetime < b.travelDepartureDatetime) {
                return -1
              }
              return 0
            })
          } else if (this.selectOrder.value === '4') {
            // De mayor a menor Hora salida
            listTravelsTemp.sort(function (a, b) {
              if (b.travelDepartureDatetime > a.travelDepartureDatetime) {
                return 1
              }
              if (b.travelDepartureDatetime < a.travelDepartureDatetime) {
                return -1
              }
              return 0
            })
          }
          if (this.filterCompany.length > 0) {
            return listTravelsTemp.filter((travel) => this.filterCompany.includes(travel.companyCode))
          }
        }
      }
      return listTravelsTemp
    },
    filterCompanies () {
      let companies = []
      for (let i = 0; i < this.listCompany.length; i++) {
        companies.push(this.listCompany[i])
      }
      return companies.map((company, index) => {
        return {label: company.name, value: company.skin}
      })
    },
    optionsOrder: function () {
      let options = [{label: this.$t('LBL_LOW_HIGH_PRICE'), value: '1'}, {label: this.$t('LBL_HIGH_LOW_PRICE'), value: '2'}, {label: this.$t('LBL_HIGH_LOW_DEPARTURE_TIME'), value: '3'}, {label: this.$t('LBL_LOW_HIGH_DEPARTURE_TIME'), value: '4'}]
      return options
    }
  },
  beforeRouteUpdate (to, from, next) {
    if (this.selectTravel.TYPE_WAY === 2) {
      this.loadTravelsReturn(this.selectTravel)
    } else {
      this.loadTravels()
    }
    next()
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      if (vm.selectTravel.TYPE_WAY === 2) {
        vm.loadTravelsReturn(vm.selectTravel)
      } else {
        vm.loadTravels()
      }
      vm.loadTicketType()
      vm.loadOptions()
    })
  },
  methods: {
    loadOptions: function () {
      var vue = this
      this.$http.get(`${process.env.URL_GET_OPTIONS}`).then(response => {
        this.setListOptions(response.data.data)
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      })
    },
    loadTicketType: function () {
      var vue = this
      this.$http.get(`${process.env.URL_LIST_TICKET_TYPE}`).then(response => {
        this.listTicketType = response.data.data
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      })
    },
    loadTravels () {
      let vue = this
      this.setLoader({loader: true, message: this.$t('MSG_SEARCHING_TRIPS')})
      var params = new URLSearchParams()
      let query = {
        d: this.search.DEPARTURE.id,
        a: this.search.ARRIVAL.id,
        dd: this.search.DATE_DEPARTURE.format('YYYY-MM-DD')
      }
      if (this.search.DATE_RETURN != null) {
        Object.assign(query, { rd: this.search.DATE_RETURN.format('YYYY-MM-DD') })
      }
      for (let param in query) {
        params.set(param, query[param])
      }
      if ('rd' in query) {
        this.setSelectTravel({ TYPE_WAY: 1 })
      } else {
        this.setSelectTravel({ TYPE_WAY: 0 })
      }
      this.listTravels = []
      this.$http.get(`${process.env.URL_LIST_ONEWAY}?${params.toString()}`).then(response => {
        this.listTravels = response.data.data
        let aCompany = []
        this.listCompany = []
        response.data.data.forEach((obj) => {
          if (!(aCompany.includes(obj.companyCode))) {
            aCompany.push(obj.companyCode)
            this.listCompany.push({
              name: obj.company,
              skin: obj.companyCode
            })
          }
        })
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      }).then(() => {
        this.setLoader({loader: false})
      })
    },
    loadTravelsReturn (keys) {
      let vue = this
      this.setLoader({loader: true, message: this.$t('MSG_SEARCHING_TRIPS')})
      this.listTravels = []
      this.$http.get(`${process.env.URL_LIST_RETURN}?key=${keys.KEY_FARE}`).then(response => {
        //        this.setSelectTravel({TYPE_WAY: 2})
        this.listTravels = response.data.data
        let aCompany = []
        this.listCompany = []
        response.data.data.forEach((obj) => {
          if (!(aCompany.includes(obj.companyCode))) {
            aCompany.push(obj.companyCode)
            this.listCompany.push({
              name: obj.company,
              skin: obj.companyCode
            })
          }
        })
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      }).then(() => {
        this.setLoader({loader: false})
      })
    },
    togglefilter () {
      this.showFilters = !this.showFilters
    },
    closeAllToolTip () {
      this.showFilters = false
      this.showCompany = false
    },
    ...mapActions({
      // /.. Auth and removeLocalStorage
      cartRestartState: 'cart/restartState',
      searchRestartState: 'search/restartState',
      travelsRestartState: 'travels/restartState',
      autthRestartState: 'auth/restartState',
      paymentRestartState: 'payment/restartState',
      // ../
      setTravel: 'search/setTravel',
      listTravel: 'travels/setListTravel',
      setListOptions: 'travels/setListOptions',
      setSelectTravel: 'travels/setSelectTravel',
      setLoader: 'loader/setLoader'
    }),
    removeLocalStorage () {
      sessionStorage.setItem('id_token', '')
      this.setLoader({loader: false})
      this.cartRestartState()
      // this.searchRestartState()
      this.travelsRestartState()
      // this.autthRestartState()
      // this.paymentRestartState()
    },
    loadSearchTravel () {
      let obj = {
        DEPARTURE: {
          id: this.$route.query.d
        },
        ARRIVAL: {
          id: this.$route.query.a
        },
        DATE_DEPARTURE: this.$route.query.dd,
        DATE_RETURN: (typeof this.$route.query.rd !== 'undefined') ? this.$route.query.dd : null,
        TYPE_WAY: 0,
        TYPE_TRAVEL: 0
      }
      this.setTravel(obj)
    },
    startSession () {
      this.user.lang = this.lang
      this.user.token = sessionStorage.getItem('id_token')
      this.removeLocalStorage()
      if (!this.authenticated) {
        this.$http.post(process.env.URL_START_SESSION, this.user).then(response => {
          this.$store.dispatch('auth/login', response.data.data.token)
          this.$store.dispatch('auth/culqi', response.data.data.culqi)
        })
      }
    },
    getLocation () {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(this.showPosition)
      } else {
        console.log('No soporta el navegador')
      }
    },
    showPosition (position) {
      this.location.latitude = position.coords.latitude
      this.location.longitude = position.coords.longitude
      let data = {
        lat: this.location.latitude,
        long: this.location.longitude
      }
      this.$http.post(process.env.URL_VISITOR, data).then(response => {
      })
    }
  },
  filters: {
    orderBy (list) {
      list.sort(function (a, b) {
        return Math.max.apply(null, a.fare.map((o) => o.price)) - Math.max.apply(null, b.fare.map((o) => o.price))
      })
    }
  },
  components: {
    bxWidget,
    bxTravelSeats,
    bxTravelInfo,
    bxTravelList,
    bxShoppingCart
  }
}
</script>

