                  overflow:hidden;
                  white-space:nowrap;
                  text-overflow: ellipsis;
                  padding: 4px 4px 0;
                }
                .calendar-body{
                  border-bottom: 4px solid #3a3a3a;
                  .num{
                    line-height: 1.3em;
                  }
                  span{
                    line-height: .8em;
                    display: block;
                    span{
                      display: none;
                    }
                  }
                }
              }
            }
            .button-prev{
              left: 0;
            }
            .button-next{
              right: 10px;
            }
        }
        .selected{
          .calendar-body{
              border-bottom: 4px solid #fff!important;
          }
        }
      }
      .places{
        li{
          height: auto;
          padding: 0 0 .5em;
          font-size: 18px;
          i{
            display: none;
          }
        }
      }
    }
    > div:nth-child(2){
      background: #3a3a3a;
    }
  }
}
</style>



