<template>
    <div class="datepicker" v-if="skin === 'search'">
        <div class="datepicker-inner" @click="showDatepicker">
            <!-- <p class="title">{{ title }}</p> -->
            <div class="datepicker-container" :class="[ disabled ? 'disabled' : '' ]">
                <div class="dp-head">
                    <transition name="show" mode="out-in">
                      <span :key="day" class="day">{{ day }}</span>
                    </transition>
                </div>
                <div class="content">
                    <transition name="show" mode="out-in">
                      <span :key="month" class="month">{{ month }}</span>
                    </transition>
                    <transition name="show" mode="out-in">
                      <span :key="year" class="year">{{ year }}</span>
                    </transition>
                    <!-- <transition name="show" mode="out-in">
                        <span :key="dayofweek" class="dayofweek">{{ dayofweek }}</span>
                    </transition> -->
                </div>
            </div>
        </div>
        <datepicker-calendar
            :visible="isVisible"
            :date="date"
            :minDate="minDate"
            @changedDate="selectDate($event)"
            @changedYear="selectYear($event)"
            @cancel="hideDatepicker"
            :show="show"
            :lang="lang">
        </datepicker-calendar>
    </div>
    <div v-else>
        ...
    </div>
</template>

<script>
import calendar from './calendar'
import moment from 'moment'
import { mapGetters } from 'vuex'

export default {
  props: {
    eventAfterChangeDate: {type: String, required: true},
    skin: {type: String, default: 'search'},
    myDate: {type: Object},
    minDate: {type: Object, default: () => moment().startOf('day')},
    format: {type: String, default: 'YYYY-MM-DD'},
    name: {type: String},
    title: {type: String},
    disabled: {type: Boolean, default: false},
    show: {type: Boolean, default: false},
    openDatepicker: {type: Boolean}
  },
  data: function () {
    return {
      date: this.myDate,
      isVisible: this.openDatepicker
    }
  },
  created: function () {
    this.date = moment(this.myDate).locale(process.env.APP_DEFAULT_LANG)
  },
  methods: {
    selectDate: function (date) {
      this.date = date
      this.hideDatepicker(null)
      this.$emit(this.eventAfterChangeDate, date)
    },
    selectYear: function (date) {
      this.date = date
      this.hideDatepicker(null)
      this.$emit(this.eventAfterChangeDate, date)
    },
    showDatepicker: function () {
      if (!this.disabled) {
        this.$set(this, 'isVisible', true)
        setTimeout(() => {
          document.addEventListener('click', this.hideDatepicker)
          document.addEventListener('keydown', this.hideDatepicker)
        }, 0)
      }
    },
    hideDatepicker: function (e) {
      if (!e) {
        this.detachEvents()
      } else {
        switch (e.type) {
          case 'click':
            this.detachEvents()
            break
          case 'keydown':
            if (e.keyCode === 27) {
              this.detachEvents()
            }
            break
          default:
            console.log('type')
        }
      }
    },
    detachEvents: function () {
      this.$set(this, 'isVisible', false)
      setTimeout(() => {
        document.removeEventListener('click', this.hideDatepicker)
        document.removeEventListener('keydown', this.hideDatepicker)
      }, 0)
    }
  },
  computed: {
    year: function () {
      return this.date.format('YYYY')
    },
    month: function () {
      return this.date.format('MMMM')
    },
    day: function () {
      return this.date.format('DD')
    },
    dayofweek: function () {
      return this.date.format('dddd')
    },
    ...mapGetters({
      lang: 'auth/lang'
    })
  },
  components: {
    'datepicker-calendar': calendar
  },
  watch: {
    'myDate': function (val) {
      this.date = val
    },
    lang: {
      handler: function (val) {
        this.date = moment(this.myDate).locale(val)
      },
      deep: true
    },
    isVisible: {
      handler: function () {
        this.$forceUpdate()
      },
      deep: true
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../styles/base/variables';

@keyframes show-me {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.show-enter-active {
  animation: show-me 350ms;
}
.show-leave-active {
  animation: show-me 350ms reverse;
}

.datepicker{
  width: 120px;
  display: inline-block;
  text-align: center;
  margin: 0 3px;
  position: relative;
  .title {
    font-size: .9rem;
    margin: 0;
  }
  .datepicker-container {
    cursor: pointer;
    color: map-get($colors, gray-13);
    .dp-head{
      display: flex;
      justify-content: space-between;
      align-items: center;
      display: inline-block;
      padding-bottom: 0;
      vertical-align: bottom;
      .day{
                // padding-top: 6px;
        font-size: 2rem;
        line-height: 1.8rem;
        vertical-align: bottom;
      }
    }
    .content {
      // color: map-get($colors, gray-13);
      display: flex;
      align-items: center;
      margin-bottom: .2rem;
      display: inline-block;
      vertical-align: bottom;
      width: 61px;
      //height: 29px;
      text-align: left;
      .year{
        font-size: .8rem;
        padding-left: .3rem;
      }
      .month{
        font-size: .8rem;
        text-transform: capitalize;
      }
    }
    // .content span, .header span{
    //  display: block;
    // }
    .content {
      display: flex;
      justify-content: space-around;
      font-size: 2rem;
      line-height: .8;
      // .dayofweek {
      //  font-size: .8rem;
      //  padding-bottom: .2rem;
      //           text-transform: capitalize;
      // }
    }
    &.disabled {
      opacity: 0.45;
      cursor: not-allowed;
      pointer-events: none;
    }
  }
}
</style>



