/* ============
 * Getters for the travels module
 * ============
 *
 * The getters that are available on the
 * travels module.
 */

export default {
  listTravel: (state) => {
    return state.LIST_TRAVEL
  },
  selectTravel: (state) => {
    return state.SELECT_TRAVEL
  },
  listOptions: (state) => {
    return state.LIST_OPTIONS
  }
}



