<template>
<div class="header header-summary">
  <div class="width-general summary">
      <nav class="navbar navbar-expand-md navbar-light bg-faded">
        <button class="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo02" aria-controls="navbarTogglerDemo02" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <figure class="inline logo-travel">
          <img @click="home()" src="/static/images/logo.png" alt="" width="90px">
        </figure>
        <bx-header-basic></bx-header-basic>
      </nav>
    </div>
    <div class="breadcrumb-main container-fluid mb-0">
      <section class="row width-general">
        <div class="col-6 col-md-4" :class="{ selected: selected01 }"><p class="limitLine">{{ $t('LBL_SELECTION_SERVICES') }}</p></div>
        <div class="col-6 col-md-4 text-center" :class="{ selected: selected02 }"><p class="limitLine">{{ $t('LBL_PURCHASE_SUMMARY') }}</p></div>
        <div class="col-6 col-md-4 text-center" :class="{ selected: selected03 }"><p>{{ $t('LBL_PAYMENT_METHODS') }}</p></div>
      </section>
      <section class="row" v-show="false">
        <div class="col-md-12 selected successHeader">
          <p class="text-center"><i class="fa fa-check-circle" aria-hidden="true"></i>{{ $t('LBL_SUCCESS_OPRATION')}}</p>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import bxHeaderBasic from './headerBasic'
export default {
  components: {
    bxHeaderBasic
  },
  computed: {
    selected01 () {
      return (this.$route.name === 'services' || this.$route.name === 'summary' || this.$route.name === 'Payment')
    },
    selected02 () {
      return (this.$route.name === 'summary' || this.$route.name === 'Payment')
    },
    selected03 () {
      return (this.$route.name === 'Payment')
    }
  },
  methods: {
    home: function () {
      // this.$router.push({
      //   name: 'home'
      // })
      window.location.href = process.env.URL_DEFAULT_HOME
    }
  }
}
</script>



