/* ============
 * Mutations for the cart module
 * ============
 *
 * The mutations that are available on the
 * cart module.
 */
import Vue from 'vue'
import {
  CREATE_SHOPPING_CART,
  RESTART_STATE
} from './mutation-types'

export default {
  [RESTART_STATE] (state) {
    state.items = {}
    state.discount = null
    state.subtotal = null
    state.fee = null
    state.total = null
  },

  [CREATE_SHOPPING_CART] (state, listShoppingcart) {
    state.items = {}
    var symbol = ''
    listShoppingcart.travelsOneWay.forEach((travel) => {
      symbol = travel.symbol
      if ('path' in travel) {
        let aPath = []
        travel.path.forEach((oPath, index) => {
          if (oPath.seats.length > 0) {
            let aSeat = []
            oPath.seats.forEach((seat) => {
              if (seat.passenger === null) {
                aSeat.push(Object.assign({}, {
                  indexPath: index + 1,
                  keyPath: oPath.keyPath,
                  seatId: seat.seatId,
                  number: seat.numberSeat,
                  discount: seat.discount,
                  subtotal: seat.subtotal,
                  total: seat.total
                }))
              } else {
                aSeat.push(Object.assign({}, {
                  indexPath: index + 1,
                  keyPath: oPath.keyPath,
                  seatId: seat.seatId,
                  dataPerson: normalizeDataPerson(seat),
                  fullName: seat.passenger,
                  number: seat.numberSeat,
                  discount: seat.discount,
                  subtotal: seat.subtotal,
                  total: seat.total
                }))
              }
            })
            aPath.push(Object.assign({}, {
              indexPath: index + 1,
              keyPath: oPath.keyPath,
              seats: aSeat
            }))
          }
        })
        let item = normalizeItem(travel, 1, travel.path.length, travel.key, travel.key)
        if (aPath.length > 0) {
          Object.assign(item, {path: aPath})
        }
        Vue.set(state.items, item.keyGroupTravel, [item])
      } else {
        let item = normalizeItem(travel, 0, 1, travel.key, travel.key)
        let aSeat = []
        travel.seats.forEach((seat) => {
          if (seat.passenger === null) {
            aSeat.push(Object.assign({}, {
              keyPath: travel.key,
              seatId: seat.seatId,
              number: seat.numberSeat,
              discount: seat.discount,
              subtotal: seat.subtotal,
              total: seat.total
            }))
          } else {
            aSeat.push(Object.assign({}, {
              keyPath: travel.key,
              seatId: seat.seatId,
              dataPerson: normalizeDataPerson(seat),
              fullName: seat.passenger,
              number: seat.numberSeat,
              discount: seat.discount,
              subtotal: seat.subtotal,
              total: seat.total
            }))
          }
        })
        if (aSeat.length > 0) {
          Object.assign(item, {seats: aSeat})
        }
        Vue.set(state.items, item.keyGroupTravel, [item])
      }
    })
    if (listShoppingcart.travelsRoundTrip.length > 0) {
      listShoppingcart.travelsRoundTrip.forEach((travel) => {
        if (travel.roundKey in state.items) {
          var aKey = travel.key.split(',')
          var key = ''
          if (aKey.length > 1) {
            key = aKey[1]
          } else {
            key = aKey[0]
          }
          if ('path' in travel) {
            let aPath = []
            travel.path.forEach((oPath, index) => {
              let aSeat = []
              oPath.seats.forEach((seat) => {
                if (seat.passenger === null) {
                  aSeat.push(Object.assign({}, {
                    indexPath: index + 1,
                    keyPath: oPath.keyPath,
                    seatId: seat.seatId,
                    number: seat.numberSeat,
                    discount: seat.discount,
                    subtotal: seat.subtotal,
                    total: seat.total
                  }))
                } else {
                  aSeat.push(Object.assign({}, {
                    indexPath: index + 1,
                    keyPath: oPath.keyPath,
                    seatId: seat.seatId,
                    dataPerson: normalizeDataPerson(seat),
                    fullName: seat.passenger,
                    number: seat.numberSeat,
                    discount: seat.discount,
                    subtotal: seat.subtotal,
                    total: seat.total
                  }))
                }
              })
              aPath.push(Object.assign({}, {
                indexPath: index + 1,
                keyPath: oPath.keyPath,
                seats: aSeat
              }))
            })
            let item = normalizeItem(travel, 1, travel.path.length, key, travel.roundKey)
            if (aPath.length > 0) {
              Object.assign(item, {path: aPath})
            }
            state.items[travel.roundKey].push(item)
          } else {
            let item = normalizeItem(travel, 0, 1, key, travel.roundKey)
            let aSeat = []
            travel.seats.forEach((seat) => {
              if (seat.passenger === null) {
                aSeat.push(Object.assign({}, {
                  keyPath: key,
                  seatId: seat.seatId,
                  number: seat.numberSeat,
                  discount: seat.discount,
                  subtotal: seat.subtotal,
                  total: seat.total
                }))
              } else {
                aSeat.push(Object.assign({}, {
                  keyPath: key,
                  seatId: seat.seatId,
                  dataPerson: normalizeDataPerson(seat),
                  fullName: seat.passenger,
                  number: seat.numberSeat,
                  discount: seat.discount,
                  subtotal: seat.subtotal,
                  total: seat.total
                }))
              }
            })
            if (aSeat.length > 0) {
              Object.assign(item, {seats: aSeat})
            }
            state.items[travel.roundKey].push(item)
          }
        }
      })
    }
    state.discount = listShoppingcart.discount
    state.subtotal = listShoppingcart.subTotal
    state.fee = listShoppingcart.fee
    state.total = listShoppingcart.subTotal + listShoppingcart.fee
    state.symbol = symbol
  }

}

function normalizeItem (travel, isMultiroute, numberPaths, key, keyGroupTravel) {
  return Object.assign({}, {
    countryCode: travel.countryCode,
    companyName: travel.company,
    companySkin: travel.companySkin,
    companyLogo: travel.companyLogo,
    departure: travel.departureName,
    arrival: travel.arrivalName,
    departureDate: travel.departureDate,
    arrivalDate: travel.arrivalDate,
    travelTime: travel.travelTime,
    key: key,
    keyGroupTravel: keyGroupTravel,
    fare: travel.fare,
    typeWay: travel.type,
    isMultiroute: isMultiroute,
    numberPaths: numberPaths,
    couponApplies: travel.couponApplies,
    discount: travel.discount,
    subtotal: travel.subtotal,
    total: travel.total,
    price: travel.price,
    currency: 'symbol' in travel ? travel.symbol : ''
  })
}
function normalizeDataPerson (seat) {
  return Object.assign({}, {
    firstName: seat.fName,
    lastName: seat.lName,
    ticketType: seat.ticketTypeCode,
    sex: seat.sex,
    email: seat.email,
    phone: seat.phone,
    birthday: seat.birthday,
    documentType: seat.documentType,
    documentNumber: seat.documentNumber,
    luggageCount: 0,
    frequentPassengerCode: '',
    studentCode: '',
    contactEmergency: seat.contactEmergency,
    withRuc: seat.withRuc,
    companyRuc: seat.companyRuc,
    companyName: seat.companyName,
    companyAddress: seat.companyAddress
  })
}



