<template>
  <div>
    <div class="row content-summary">
      <div class="col-9 col-md-10">
        <div class="row content-travel-detail" v-for="(travel, index) in travels" :key="index">
          <div class="col-12 col-md-3">
            <span>{{ $t('LBL_DEPARTURE_2') }}:</span>
            <p>{{travel.departure}}</p>
            <span><i class="fa fa-calendar-o" aria-hidden="true"></i>{{formatDate(travel.departureDate, travel.countryCode)}}</span>
            <span><i aria-hidden="true" class="fa fa-clock-o"></i>{{formatHour(travel.departureDate)}}</span>
            <div v-if="needshuttleDeparture===1 && index === 0">
              <span class="title-shuttle">{{ $t('LBL_DEPARTURE_SHUTTLE') }}:</span>
              <div v-if="travel.departureShuttleId != null">
                <comp-vselect :keyTravel="travel.key" :type="'d'" :valueDefault="{label: travel.departureShuttleName,value: travel.departureShuttleId}" @selectShuttle="setShuttleDeparture($event)"></comp-vselect>
                <label class="label-shuttle-ow">{{ shuttleDeparture.label }}</label>
              </div>
              <div v-else>
                <comp-vselect :keyTravel="travel.key" :type="'d'" @selectShuttle="setShuttleDeparture($event)"></comp-vselect>
              </div>
            </div>
            <div v-if="needshuttleArrival && index === 1 && travels[0].companySkin === travels[0].companySkin">
              <span class="title-shuttle">{{ $t('LBL_DEPARTURE_SHUTTLE') }}:</span>
              <label v-if="shuttleArrival" class="label-shuttle-rw">{{ shuttleArrival.label }}</label>
            </div>
          </div>
          <div class="col-12 col-md-2 text-center logo-summary">
            <v-popover>
              <img :src="'/static/images/logos-companies/'+travel.companySkin+'-logo.png'" class="inline img-business tooltip-target img-responsive">
              <template slot="popover" class="content-info-company">
                <div class="content-info-company">
                  <div class="form-group col-12 alert-message" v-if="company.name">
                    <label for="labelCompanyName" class="">{{ $t('LBL_COMPANY_NAME') }}</label>
                    <label id="labelCompanyName" name="labelCompanyName" class="">{{company.name}}</label>
                  </div>
                  <div class="form-group col-12 alert-message" v-if="company.ruc">
                    <label for="labelRuc" class="" >{{ $t('LBL_RUC') }}</label>
                    <label id="labelRuc" name="labelRuc" class="" v-if="company.ruc">{{company.ruc}}</label>
                  </div>
                  <div class="form-group col-12 alert-message" v-if="company.address">
                    <label for="labelAddress" class="">{{ $t('LBL_ADDRESS') }}</label>
                    <label id="labelAddress" name="labelAddress" class="">{{company.address}}</label>
                  </div>
                  <div class="form-group col-12 alert-message" v-if="company.phone">
                    <label for="labelPhone" class="">{{ $t('LBL_PHONE') }}</label>
                    <label id="labelPhone" name="labelPhone">{{company.phone}}</label>
                  </div>
                  <div class="form-group col-12 alert-message">
                    <label for="labelBoarding" class="">{{ $t('LBL_BOARDING_TERMINAL') }}</label>
                    <label id="labelBoarding" name="labelBoarding" class="" v-if="company.boarding">{{company.boarding}}</label>
                    <label id="labelBoarding" name="labelBoarding" class="" v-else>{{$t('LBL_NO_INFORMATION')}}</label>
                  </div>
                  <div class="form-group col-12 alert-message">
                    <label for="labelLanding" class="">{{ $t('LBL_LANDING_TERMINAL') }}</label>
                    <label id="labelLanding" name="labelLanding" class="" v-if="company.landing">{{company.landing}}</label>
                    <label id="labelLanding" name="labelLanding" class="" v-else>{{$t('LBL_NO_INFORMATION')}}</label>
                  </div>
                </div>
              </template>
            </v-popover>
          </div>
          <div class="col-12 col-md-3">
            <span>{{ $t('LBL_ARRIVAL') }}:</span>
            <p>{{travel.arrival}}</p>
            <span><i class="fa fa-calendar-o" aria-hidden="true"></i>{{formatDate(travel.arrivalDate, travel.countryCode)}}</span>
            <span><i aria-hidden="true" class="fa fa-clock-o"></i>{{formatHour(travel.arrivalDate)}}</span>
            <div v-if="needshuttleArrival && index === 0">
              <span class="title-shuttle">{{ $t('LBL_ARRIVAL_SHUTTLE') }}:</span>
              <div v-if="travel.arrivalShuttleId != null">
                <comp-vselect :keyTravel="travel.key" :type="'a'" :valueDefault="{label: travel.arrivalShuttleName,value: travel.arrivalShuttleId}" @selectShuttle="setShuttleArrival($event)"></comp-vselect>
                <label class="label-shuttle-ow">{{ shuttleArrival.label }}</label>
              </div>
              <div v-else>
                <comp-vselect :keyTravel="travel.key" :type="'a'" @selectShuttle="setShuttleArrival($event)"></comp-vselect>
              </div>
            </div>
            <div v-if="needshuttleDeparture && index === 1 && travels[0].companySkin === travels[0].companySkin">
              <span class="title-shuttle">{{ $t('LBL_ARRIVAL_SHUTTLE') }}:</span>
              <label v-if="shuttleDeparture" class="label-shuttle-rw">{{ shuttleDeparture.label }}</label>
            </div>
          </div>
          <div class="col-12 col-md-4 mt-3 mt-md-0">
            <Label v-if="travel.couponApplies">{{ $t('LBL_COUPON_APPLIED') }}</Label>
            <div class="form-group has-success" v-if="index === 0 && travel.couponApplies === false">
              <form class="row">
                <input v-model="coupon1" type="text" class="form-control" :placeholder="$t('PHD_CUPON')">
                <button class="btn applyCoupon" :disabled="coupon1.trim() ? false : true" type="button" name="button" @click="applyCoupon(travel.key, coupon1)"><i class="fa fa-ticket" aria-hidden="true"></i> {{ $t('LBL_APPLY') }} </button>
              </form>
            </div>
            <div class="form-group has-success" v-else-if="index === 1 && showCoupon && travel.couponApplies === false">
              <form class="">
                <input v-model="coupon2" type="text" class="form-control" :placeholder="$t('PHD_CUPON')">
                <button class="btn applyCoupon" :disabled="coupon2.trim() ? false : true" type="button" name="button" @click="applyCoupon(travel.key, coupon2)"><i class="fa fa-ticket" aria-hidden="true"></i> {{ $t('LBL_APPLY') }} </button>
              </form>
            </div>
            </div>
        </div>
      </div>
      <div class="col-3 col-md-2">
        <strong>{{ $t('LBL_SUBTOTAL') }}:</strong>
        <p class="sub-total"><span>{{getCurrency}}</span>{{subTotalTravel()}}</p>
      </div>
<!--      <div class="col-1">
        <button class="btn-remove" @click="removeTravel(travels[0].key)">{{ $t('LBL_REMOVE') }}</button>
      </div>-->
    </div>
</div>
</template>
<script>
import moment from 'moment'
import vSelect from '../components/select/vSelect'
import { mapActions, mapGetters } from 'vuex'
export default {
  props: {
    travels: {
      required: true
    },
    keyGroupTravel: {
      required: true
    }
  },
  created: function () {
    this.needshuttleDeparture = this.travels[0].isDepartureShuttle
    if (this.travels[0].departureShuttleId != null) {
      this.shuttleDeparture = {
        label: this.travels[0].departureShuttleName,
        value: this.travels[0].departureShuttleId
      }
      this.blockAjaxShuttleDep = true
    }
    this.needshuttleArrival = this.travels[0].isArrivalShuttle
    if (this.travels[0].arrivalShuttleId != null) {
      this.shuttleArrival = {
        label: this.travels[0].arrivalShuttleName,
        value: this.travels[0].arrivalShuttleId
      }
      this.blockAjaxShuttleArr = true
    }
  },
  data: function () {
    return {
      coupon1: '',
      coupon2: '',
      blockAjaxShuttleDep: false,
      blockAjaxShuttleArr: false,
      needshuttleDeparture: 0,
      needshuttleArrival: 0,
      shuttleDeparture: null,
      shuttleArrival: null,
      subtotal: '',
      showInfoCompany: false,
      company: {
        name: '',
        address: '',
        phone: '',
        ruc: '',
        boarding: '',
        landing: ''
      }
    }
  },
  computed: {
    showCoupon () {
      if (this.travels.length === 2 && this.travels[0].companySkin === this.travels[1].companySkin) {
        return false
      }
      return true
    },
    getCurrency () {
      return this.travels[0].currency
    },
    ...mapGetters({
      items: 'cart/items',
      selectTravel: 'travels/selectTravel'
    })
  },
  methods: {
    formatDate (input, country) {
      var format = ''
      if (country === 'PE') {
        format = 'DD/MM/YYYY'
      } else {
        format = 'MM-DD-YYYY'
      }
      let dateMoment = moment(input).format('dddd') + ' ' + moment(input).format(format)
      return dateMoment
    },
    formatHour (input, format = 'hh:mm a') {
      return moment(input).format(format)
    },
    applyCoupon: function (key, codeCoupon) {
      var vue = this
      let data = {
        key: key,
        coupon: codeCoupon
      }
      this.setLoader({loader: true})
      this.$http.post(process.env.URL_APPLY_COUPON, data).then(response => {
        if (response.data.data.apply === 1) {
          this.$emit('applyCoupon')
        }
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      }).then(() => {
        this.setLoader({loader: false})
      })
    },
    toggleInfoCompany () {
      if (!this.showInfoCompany) {
        this.$http.get(`${process.env.URL_GET_INFO_COMPANY}?key=` + this.selectTravel.KEY).then(response => {
          this.loader = !this.loader
          let data = response.data
          if (data.code === 10) {
            var result = data.data
            this.company.name = result.companyName
            this.company.address = result.companyAddress
            this.company.phone = result.companyPhone
            this.company.ruc = result.companyRuc
            this.company.boarding = result.terminalBoarding
            this.company.landing = result.terminalLanding
          } else {
            alert('Error en mostrar info de compania')
          }
        }).catch(function (error) {
          console.log(error)
        }).then(() => {
          this.showInfoCompany = true
        })
      } else {
        this.showInfoCompany = !this.showInfoCompany
      }
    },
    subTotalTravel: function () {
      let subtotal = 0
      this.travels.forEach((obj) => {
        subtotal = subtotal + obj.subtotal
      })
      return subtotal
    },
    setShuttleDeparture: function (event) {
      if (!this.blockAjaxShuttleDep) {
        let params = {
          key: this.travels[0].key,
          type: 'd',
          shuttle: event.value
        }
        this.addShuttle(params)
      }
      this.shuttleDeparture = event
      this.blockAjaxShuttleDep = false
    },
    setShuttleArrival: function (event) {
      if (!this.blockAjaxShuttleArr) {
        let params = {
          key: this.travels[0].key,
          type: 'a',
          shuttle: event.value
        }
        this.addShuttle(params)
      }
      this.shuttleArrival = event
      this.blockAjaxShuttleArr = false
    },
    addShuttle: function (params) {
      var vue = this
      this.setLoader({loader: true})
      this.$http.post(process.env.URL_ADD_SHUTTLE, params).then(response => {
        console.log(response)
        this.$emit('setShuttleValue')
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      }).then(() => {
        this.setLoader({loader: false})
      })
    },
    removeTravel: function (keyTravel) {
      var vue = this
      this.setLoader({loader: true})
      this.$http.delete(process.env.URL_REMOVE_SHOPPINGCART, { data: {key: keyTravel} }).then(response => {
        this.$emit('realoadShoppingcartList')
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i>'/* + vue.$t('LBL_ERROR') + */+ '</div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      }).then(() => {
        this.setLoader({loader: false})
      })
    },
    ...mapActions({
      setLoader: 'loader/setLoader'
    })
  },
  components: {
    'comp-vselect': vSelect
  }
}
</script>

<style lang="scss">
@import '../styles/base/variables';
.content-summary{
    border-bottom: 7px solid map-get($colors, gray-05);
    padding-top: 1em;
    border-radius: 2px;
    padding-bottom: .5em;
    .content-travel-detail{
      >div:nth-child(3){
        text-align: right;
      }
      .label-shuttle-ow {
        line-height: 14px;
        font-size: 12px;
        margin-top: 0.5em;
      }
      .label-shuttle-rw {
        line-height: 14px;
        font-size: 12px;
      }
    }
    &:hover{
      background: map-get($colors, gray-01);
    }
  strong, p{
    font-size: 14px;
    color: map-get($colors, gray-12);
  }
  .searchable{
    .dropdown-toggle{
      white-space: nowrap;
    }
    .dropdown-menu{
        display: grid;
        li{
          a{
            font-size: 14px;
            padding: .3em .5em;
            &:hover{
              color: #fff;
            }
          }
        }
      }
    .selected-tag{
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      width: 85%;
    }
  }
  p{
    font-weight: 600;
    font-size: 18px;
    margin-bottom: .3em;
    height: 54px;
    span{
      display: inline-block;
    }
  }
  span{
    font-size: 14px;
    color: gray;
    display: block;
    i{
      margin-right: .3em;
    }
  }
  span:nth-child(3){
    text-transform: capitalize;
  }
  .logo-summary{
    padding-top: .5em;
    span{
      height: 2px;
      width: 100%;
      background: map-get($colors, gray-05);
      margin-top: .5em;
      position: absolute;
      left: 0;
      &::after{
          width: 20px;
          height: 20px;
          content: '\f04b';
          position: absolute;
          right: -1.1em;
          top: -.4em;
          font: normal normal normal 14px/1 FontAwesome;
          color: map-get($colors, gray-06);
          z-index: 9;
        }
        &::before{
          content: '\f192';
          position: absolute;
          left: -.9em;
          top: -.4em;
          font: normal normal normal 14px/1 FontAwesome;
          color: map-get($colors, gray-06);
          z-index: 9;
          background: map-get($colors, white);
          border-radius: 50%;
          font-size: 16px;
        }
      img{
        margin-top: 10px;
      }
    }
  }
  .col-10 > div:nth-child(2) {
    > div:nth-child(1), > div:nth-child(2), > div:nth-child(3), > div:nth-child(4){
      border-top: 2px dashed map-get($colors, gray-05);
      padding-top: 15px;
    }
  }
  .col-10 > div:nth-child(1){
    div:nth-child(5){
      padding-top: 3%;
    }
  }
  >.col-2 {
    text-align: right;
    padding-right: 2em;
    .sub-total{
      font-size: 2em;
      color: map-get($colors, primary-01);
      span{
        font-size: 16px;
        margin-right: .2em;
      }
    }
  }
  .content-info-company{
    top: 1.5em;
  }
}
.purchase-detail{
    .general-payment-detail{
        border: 1px solid map-get($colors, gray-05);
        border-radius: 4px 4px 0 0;
        padding: 0em 1em 0 1em;
        p{
          margin: 0px;
          font-size: 13px;
          line-height: 15px;
          color: map-get($colors, gray-13);
        }
    }
  .headerpurchase-detail{
    height: 2rem;
    background: map-get($colors, secondary-02);
    border-radius: 4px 4px 0 0;
    div{
      p{
        color: map-get($colors, secondary-01)!important;
        font-family: "Roboto-Medium";
        margin-bottom: 0px;
        padding: 8px 2px;
        font-size: 14px!important;
      }
      button{
        background: map-get($colors, gray-14);
        border: none;
        padding: 6px 10px;
        color: map-get($colors, white);
        font-size: 14px;
        display: inline-block;
        &:hover{
          cursor: pointer;
        }
      }
    }
.img-business{margin-top: 8px;}
  }

  .footerpurchase-detail{
    height: 2rem;
    background-color: map-get($colors, gray-01);
    margin-right: -16px;
    margin-left: -16px;

    .removeNewTrip{
      background-color: map-get($colors, gray);
      color: map-get($colors, white);
      cursor: pointer;
    }
    .btn{
    padding: 3px;
    border-radius: 0px;
    line-height: inherit;
    width: 8em;
    }
    .text-right{
    padding: 5px;
    }
    .title-detail{padding: 5px;}
  }
  .form-group.has-success {
      padding-top: 1rem;
      .form-control{
      border-color: map-get($colors, secondary-01);
      }
      form{
        .applyCoupon{
          background: map-get($colors, secondary-01);
          color: map-get($colors, gray-01);
        }
      }

}

  .infoPurchase-detail{
    border: 1px solid map-get($colors, gray-05);
    padding: 10px;
    border-radius: 4px;
    margin: 10px 0 10px 0;
    background-color: map-get($colors, gray-01);
    .title-detail{
      font-family: "Roboto-Medium";
    }
    div{
      div{
        p{
          margin-bottom: 0px;
          color: map-get($colors, gray-13);
          line-height: 15px;
          padding-bottom: 1.2em;
        }

        .price{
          font-weight: 600;
        }
      }
    }
    .lineLeft{
      border-left: 4px solid map-get($colors, secondary-07);
      padding-left: 0.3em;
      margin-top: 0.5em;
      margin-bottom: 0.7em;
      .date-information{
        p{
          padding-bottom: 0px !important;
        }
      }
    }
    .viewPassenger{
      background-color: map-get($colors, gray-14);
      button{color: map-get($colors, white); background-color: transparent; cursor: pointer;}
      margin: 1px -10px -10px;
      border-radius: 0px 0px 4px 4px;
    }
    .has-success .form-control-success {
    background-image: url("../../static/images/min-check.svg");
    }
    .list-passenger{
      background: map-get($colors, white);
      margin-top: 1em;
      div{
        padding: 5px;
        img{
          width: 20px;
        }
        .green-color{
          padding-right: 20px;
        }
        .register-passenger{
          font-size: 12px;
        }
      }
    }
  }
}
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(5px);
  opacity: 0;
}
.title-shuttle{
  color: map-get($colors, primary-01) !important;
  margin: 5px 0;
}
@media (max-width: 767.98px) {
  .content-travel-detail > div > p{
    height: auto;
  }
  .content-summary .logo-summary {
    height: 90px;
  }
  .logo-summary img{
    width: 150px;
  }
  .content-summary .content-travel-detail > div:nth-child(3) {
    text-align: left;
  }
}
</style>



