<template>
  <div class="containerSuccesView container" v-if="response">
    <div class="row infoPayer">
      <div class="col-md-5 offset-md-1 info-code order-12 order-md-1 mt-5 mt-md-0">
        <div class="row">
          <div class="col-sm-12">
            <p>{{ $t('LBL_PAYMENT_TRANSACTION_CODE') }}</p>
            <h2>{{response.paymentCode}}</h2>
            <p>{{ $t('LBL_WEB_TRANSACTION_CODE') }}</p>
            <h2>{{response.transactionCode}}</h2>
          </div>
        </div>
          <div class="row company-codes" v-for="(obj, index) in response.webcodes" :key="index">
            <div class="col-sm-12">
              <label for="">{{ $t('LBL_COMPANY') }}:</label> {{obj.Company}}
            </div>
              <div class="col-sm-12">
                <label for="">{{ $t('LBL_WEB_CODE') }}:</label> {{obj.CodeWebSale_origin}}
              </div>
            </div>
          <div class="row">
            <div class="col-sm-12">
              <h4>{{ $t('LBL_WARN_PRINT_TICKET_LABEL') }}</h4>
              <p>{{ $t('LBL_WARN_PRINT_TICKET_TEXT') }}:</p>
            </div>
          </div>
      </div>
      <div class="col-md-5 order-1 order-md-12">
        <h2>{{fullName}},</h2>
        <p>{{ $t('VIEW_SUCCESS_MESSAGE') }}.</p>
        <div class="amount text-center">
          <p>{{ $t('LBL_AMOUNT_PAID') }}</p>
          <h2>{{response.amount | addFormatDecimal}}</h2>
          <span>{{response.currency}}</span>
        </div>
        <form class="" @submit.prevent="sendEmail()">
          <div class="">
            <input type="email" class="form-control" v-model="email" id="" :placeholder="$t('PHD_EMAIL')">
            <button type="submit" class="btn btn-primary btn-send">
              <i class="fa fa-send" aria-hidden="true"></i>
            </button>
          </div>
        </form>
        <div class="">
          <a class="downloadPDF" :href="urlPdf" target="_blank" v-if="showDownload">
            <span>{{ $t('LBL_DOWNLOAD_PDF') }}</span>
            <i class="fa fa-download" aria-hidden="true"></i>
          </a>
        </div>
        <button class="btn btn-primary newPurchase" type="button" name="button" @click="newCompra()">{{ $t('LBL_NEW_PURCHASE') }}</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  data: function () {
    return {
      response: null,
      fullName: null,
      email: '',
      urlPdf: '',
      showDownload: false
    }
  },
  beforeRouteLeave (to, from, next) {
    // se llama cuando iras a otra ruta
    if (to.name === 'home') {
      this.setLoader({loader: true})
      this.$http.put(process.env.URL_END_SESSION).then(response => {
        console.log(response)
      }).catch(function (error) {
        console.log(error)
      }).then(() => {
        this.setLoader({loader: false})
        next()
      })
    } else if (to.name === 'services' || to.name === 'summary' || to.name === 'Payment') {
      next('/home')
    } else {
      next()
    }
  },
  mounted: function () {
    this.downloadTicket()
    this.response = this.$route.params.data
    this.fullName = this.$route.params.fullName
  },
  methods: {
    sendEmail () {
      var vue = this
      this.setLoader({loader: true})
      this.$http.post(process.env.URL_SEND_EMAIL, {email: this.email}).then(response => {
        this.$modal.hide('dialog')
        this.$modal.show('dialog', {
          title: '<div class="success"><i class="fa fa-check-circle" aria-hidden="true"></i></div>',
          text: this.$t('MSG_SEND_PURCHASE_CONFIRMATION'),
          buttons: [{
            title: this.$t('LBL_ACCEPT'),
            handler: () => { this.$modal.hide('dialog') }
          }]
        })
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      }).then(() => {
        this.setLoader({loader: false})
      })
    },
    downloadTicket () {
      var vue = this
      this.$http.get(process.env.URL_DOWNLOAD_TICKET).then(response => {
        this.showDownload = true
        this.urlPdf = response.data.data
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      })
    },
    newCompra () {
      this.$router.push({
        name: 'home'
      })
    },
    ...mapActions({
      setLoader: 'loader/setLoader'
    })
  }
}
</script>

<style lang="scss">
@import '../styles/base/variables';
.containerSuccesView {
  color: map-get($colors, gray-14);
  padding-top: 9em;
  padding-bottom: 14em;
  div {
    div {
      margin: 0 auto; // width: 45%;
      position: relative;
      h2 {
        margin-bottom: 10px;
        text-transform: capitalize;
        span {
          color: map-get($colors, gray-13);
        }
      }
      span {
        font-size: 14px;
        color: map-get($colors, primary-01);
        font-family: "Roboto-Light";
        font-weight: 600;
      }
      .amount {
        background: map-get($colors, gray-01);
        padding-top: 1em;
        border-radius: 3px;
        margin-bottom: 3em;
      }
      .amount h2 {
        color: map-get($colors, secondary-07);
        font-family: Roboto-Medium;
        display: inline-block;
      }
      form {
        margin-bottom: 1em;
        div {
          display: inline-block;
          width: 100%;
          vertical-align: top;
          input {
            border-radius: 0px;
            &::-webkit-input-placeholder {
              /* Chrome/Opera/Safari */
              color: map-get($colors, gray-14);
            }
            &::-moz-placeholder {
              /* Firefox 19+ */
              color: map-get($colors, gray-14);
            }
            &:-ms-input-placeholder {
              /* IE 10+ */
              color: map-get($colors, gray-14);
            }
            &:-moz-placeholder {
              /* Firefox 18- */
              color: map-get($colors, gray-14);
            }
          }
        }
      }
      .downloadPDF {
        text-decoration: none;
        color: map-get($colors, gray-13);
        span {
          color: map-get($colors, gray-13) !important;
          vertical-align: top;
          transition: .2s linear;
        }
        &:hover{
          border-bottom: 1px solid map-get($colors, gray-11);
        }
      }
      .newPurchase {
        border-radius: 3px;
        width: 100%;
        margin-top: 2em;
        &:hover {
          cursor: pointer;
        }
      }
    }
  }
  .info-code{
    h2{
      margin-bottom: 1em;
    }
    p{
      margin-bottom: .2rem;
    }
    div{
      div{
        & h2:nth-child(2){
          font-size: 18px;
          max-width: 315px;
          font-weight: bold;
        }
      }
    }
  }
  .btn-send{
    border-radius: 0 3px 3px 0;
    right: 0;
    top: 0;
    position: absolute;
    padding: 6px 15px;
    cursor: pointer;
    height: 35px;
  }
  .infoPayer {
    margin-top: 2em;
  }
  .company-codes{
    padding: .1em 0 .1em 1em;
    margin-bottom: 1em;
    div{
      padding-left: .5em;
      label{
        font-weight: bold;
            margin-bottom: .1rem;
      }
    }
    & div:nth-child(1){
      border-left: 5px solid map-get($colors, primary-07);
    }
    & div:nth-child(2){
      border-left: 5px solid map-get($colors, primary-07);
    }
  }
  @media (max-width: 767.98px) {
    .containerSuccesView .infoPayer{
      width: auto !important;
      margin-top: 0px !important;
    }
  }
}
</style>



