/* ============
 * Mutations for the loader module
 * ============
 *
 * The mutations that are available on the
 * loader module.
 */
import { RESTART_STATE, SET_PASSENGER_PAYS } from './mutation-types'

export default {
  [RESTART_STATE] (state) {
    state.KEY = null
    state.PASSENGER = {}
  },

  [SET_PASSENGER_PAYS] (state, payLoad) {
    if (payLoad.VALUE) {
      // se marco el check de pasajero paga
      state.KEY = payLoad.KEY
      Object.assign(state.PASSENGER, payLoad.PASSENGER)
    } else {
      /* no esta marcado el check de pasajero paga
        se tiene que verificar si es un pasajero que
        tenia el check y se le quito o es uno que no tenia */
      if (state.KEY === payLoad.KEY) {
        // se esta desmarcando un pasajero
        state.KEY = null
        state.PASSENGER = {}
      }
    }
  }
}



