/* ============
 * Actions for the loader module
 * ============
 *
 * The actions that are available on the
 * loader module.
 */

import * as types from './mutation-types'

export const setLoader = ({ commit }, payload) => {
  commit(types.SET_LOADER, payload)
}
export default {
  setLoader
}



