/* ============
 * Mutation types for the search module
 * ============
 *
 * The mutation types that are available
 * on the search module.
 */

export const SWAP_LOCATIONS = 'SWAP_LOCATIONS'
export const SET_TRAVEL = 'SET_TRAVEL'
export const LIST_TRAVEL = 'LIST_TRAVEL'
export const CLEAR_TRAVEL = 'CLEAR_TRAVEL'
export const CHANGE_TRAVEL_FARE = 'CHANGE_TRAVEL_FARE'
export const RESTART_STATE = 'RESTART_STATE'

export default {
  SWAP_LOCATIONS,
  SET_TRAVEL,
  CLEAR_TRAVEL,
  CHANGE_TRAVEL_FARE,
  RESTART_STATE
}



