<template>
  <div class="cart">
    <div class="background-grey" v-if="isBackground"></div>
    <span id="count-cart" class="count-cart" v-if="isArrowLeft && !isShoppingcartEmpty()">{{countSeatsMarked()}}</span>
    <transition name="cartTransition">
    <i class="fa fa-shopping-cart" :title="$t('LBL_SHOPPING_CART')" aria-hidden="true" @click="showCart ()" v-if="isArrowLeft && !isShoppingcartEmpty()"></i>
      <div class="information-passenger" v-if="isCart">
        <i class="fa fa-chevron-right d-none d-md-inline-block" aria-hidden="true" @click="closeCart ()"></i>
        <div class="information-title">
          <button type="button" class="btn btn-primary btn-cart-delete" :title="$t('LBL_REMOVE_SHOPPING_CART')" @click="removeLocalStorage()" name="button" >
            <i class="fa fa-trash" aria-hidden="true"></i>
          </button>
          <p>{{ $t('LBL_SHOPPING_CART') }}</p>
          <button type="button" class="btn btn-primary btn-cart-menu d-md-none d-inline-block" @click="closeCart()">
            <i class="fa fa-bars" aria-hidden="true"></i>
          </button>
        </div>
        <div class="content-cart" v-show="showBtnPayment">
          <button type="button" :title="$t('LBL_ADD_NEW_TRIP')" class="btn btn-primary new-purchase" @click="addNewTravel()"  name="button" >
            <i class="fa fa-plus d-none d-md-inline-block" aria-hidden="true"></i>
            <span class="d-md-none d-inline-block">{{ $t('LBL_ADD_NEW_TRIP') }}</span>
          </button>
          <div>
            <button type="button" class="btn btn-primary btn-confirm" @click="saveListPassenger()" name="button">
              {{ $t('LBL_CONFIRM_PAY') }}
            </button>
          </div>
        </div>
        <div class="list-passenger" v-bind:class="{ 'short': showBtnPayment }">
          <div v-for="(travel, index) in items" :key="index">
            <be-passenger :travel ='travel' :listTicketType='listTicketType' @verifyCompleteShoppingcart="verifyCompleteShoppingcart"  @closeCart="closeCart"></be-passenger>
          </div>
        </div>
        <transition name="slide-fade">
        <div class="modal-confirm" v-show="showModalPayment">
          <button type="button" class="btn btn-primary btn-confirm" name="button" @click="showModalPayment=false">
              <i class="fa fa-pencil-square-o" aria-hidden="true"></i>
              {{ $t('LBL_EDIT') }}
          </button>
            <button type="button" :title="$t('LBL_ADD_NEW_TRIP')" class="btn btn-primary new-purchase" @click="addNewTravel()" name="button" >
            <i class="fa fa-plus" aria-hidden="true"></i>
            {{ $t('LBL_ADD_NEW_TRIP') }}
          </button>
            <button type="button" class="btn btn-primary btn-confirm" @click="saveListPassenger()" name="button">
              {{ $t('LBL_CONFIRM_PAY') }}
            </button>
        </div>
        </transition>
      </div>
    </transition>
  </div>
</template>
<script>

import cPassenger from '@/components/passenger/passenger'
import { mapGetters, mapActions } from 'vuex'

export default {
  props: {
    listTicketType: {
      required: true
    }
  },
  data: function () {
    return {
      isCart: false,
      isArrowLeft: true,
      isBackground: false,
      showBtnPayment: false,
      showModalPayment: false,
      showBtnReturnTrip: false
    }
  },
  computed: {
    ...mapGetters({
      items: 'cart/items',
      selectTravel: 'travels/selectTravel'
    })
  },
  mounted: function () {
    this.$eventBus.$on('showShoppingCart', ($event) => {
      this.showCart()
    })
    this.$eventBus.$on('loadShoppingCart', ($event) => {
      this.shoppingCartList()
    })
    this.$eventBus.$on('mostrarvuelta', ($event) => {
      this.mostrarvuelta()
    })
    // this.$eventBus.$on('countCart', ($event) => {
    //   var element = document.getElementById('count-cart')
    //   element.style.animation = 'count-animate .5s linear'
    //   setTimeout(() => { element.style.animation = 'none' }, 500)
    // })
  },
  methods: {
    ...mapActions({
      setSelectTravel: 'travels/setSelectTravel',
      cartRestartState: 'cart/restartState',
      searchRestartState: 'search/restartState',
      travelsRestartState: 'travels/restartState',
      autthRestartState: 'auth/restartState',
      createShoppingCart: 'cart/createShoppingCart',
      setLoader: 'loader/setLoader'
    }),
    addNewTravel: function () {
      this.closeCart()
      this.setSelectTravel({
        KEY_FARE: null,
        KEY_FARE_RW: null,
        TYPE_WAY: 0,
        TRAVEL_ADD_CART_OW: false,
        TRAVEL_ADD_CART_RW: false,
        KEY_GROUP_TRAVEL: null
      })
      this.$emit('loadTravels')
    },
    mostrarvuelta: function () {
      this.setSelectTravel({TYPE_WAY: 2})
      this.closeCart()
      this.$emit('loadTravelsReturn', this.selectTravel)
    },
    saveListPassenger: function () {
      if (this.verifyCompletedForms()) {
        let passengers = []
        for (var keyGroup in this.items) {
          if (this.items[keyGroup].length === 1) {
            // vieje directo ida
            if (this.items[keyGroup][0].isMultiroute) {
              this.items[keyGroup][0].path[0].seats.forEach((seat) => {
                passengers.push(Object.assign({}, seat.dataPerson, {travels: [this.items[keyGroup][0].key]}))
              })
            } else {
              this.items[keyGroup][0].seats.forEach((seat) => {
                passengers.push(Object.assign({}, seat.dataPerson, {travels: [this.items[keyGroup][0].key]}))
              })
            }
          } else if (this.items[keyGroup].length === 2) {
            if (this.items[keyGroup][0].companySkin === this.items[keyGroup][1].companySkin) {
              // viaje redondo misma empresa
              var keyTravels = []
              this.items[keyGroup].forEach((obj) => {
                keyTravels.push(obj.key)
              })
              if (this.items[keyGroup][0].isMultiroute) {
                this.items[keyGroup][0].path[0].seats.forEach((seat) => {
                  passengers.push(Object.assign({}, seat.dataPerson, {travels: keyTravels}))
                })
              } else {
                this.items[keyGroup][0].seats.forEach((seat) => {
                  passengers.push(Object.assign({}, seat.dataPerson, {travels: keyTravels}))
                })
              }
            } else {
              // viaje redondo diferente empresa
              this.items[keyGroup].forEach((obj) => {
                if (obj.isMultiroute) {
                  obj.path[0].seats.forEach((seat) => {
                    passengers.push(Object.assign({}, seat.dataPerson, {travels: [obj.key]}))
                  })
                } else {
                  obj.seats.forEach((seat) => {
                    passengers.push(Object.assign({}, seat.dataPerson, {travels: [obj.key]}))
                  })
                }
              })
            }
          } else {
            alert('El carrito de compras no esta bien creado')
            return false
          }
        }
        this.closeCart()
        var vue = this
        this.setLoader({loader: true})
        this.$http.post(process.env.URL_ADD_PASSENGER, { passengers: passengers }).then(response => {
          let data = response.data
          if (data.code === 10) {
            this.$router.push({ // despues de guardar nos dirigimos a la vista summary
              name: 'summary'
            })
          } else {
            alert('error al guardar los pasajeros')
          }
        }).catch(function (error) {
          vue.$modal.hide('dialog')
          vue.$modal.show('dialog', {
            title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
            text: error.response.data.details,
            buttons: [{
              title: vue.$t('LBL_ACCEPT'),
              handler: () => { vue.$modal.hide('dialog') }
            }]
          })
        }).then(() => {
          this.setLoader({loader: false})
        })
      } else {
        alert('aun no completa los datos de los pasajeros seleccionados')
      }
    },
    verifyCompletedForms: function () {
      let validate = true
      for (var keyGroup in this.items) {
        this.items[keyGroup].forEach((obj) => {
          if (obj.isMultiroute) {
            if ('path' in obj) {
              obj.path.forEach((oPath) => {
                oPath.seats.forEach((seat) => {
                  if (!('dataPerson' in seat)) {
                    validate = false
                  }
                })
              })
            } else {
              validate = false
            }
          } else {
            if ('seats' in obj) {
              obj.seats.forEach((seat) => {
                if (!('dataPerson' in seat)) {
                  validate = false
                }
              })
            } else {
              validate = false
            }
          }
        })
      }
      return validate
    },
    isShoppingcartEmpty: function () {
      if (Object.keys(this.items).length === 0) {
        return true
      } else {
        return false
      }
    },
    validateShoppingcartEmpty: function () {
      if (Object.keys(this.items).length === 0) {
        this.closeCart()
      }
    },
    closeCart: function () {
      this.isCart = false
      this.isBackground = false
      this.isArrowLeft = true
    },
    showCart: function () {
      this.isCart = true
      this.isBackground = true
      this.isArrowLeft = false
      this.shoppingCartList()
      this.verifySeatsMarked()
      this.verifyCompleteShoppingcart()
    },
    removeLocalStorage () {
      // this.$modal.hide('dialog')
      this.$modal.show('dialog', {
        title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
        text: this.$t('MSG_DELETE_SHOPPING_CART'),
        buttons: [
          {
            title: this.$t('LBL_YES'),
            handler: () => {
              this.$modal.hide('dialog')
              this.setLoader({loader: true})
              this.$http.put(process.env.URL_END_SESSION).then(() => {
                this.setLoader({loader: false})
                this.cartRestartState()
                this.$router.push({
                  name: 'home'
                })
              })
            }
          },
          {
            title: this.$t('LBL_NO'),
            handler: () => {
              this.$modal.hide('dialog')
            }
          }
        ]
      })
    },
    verifyCompleteShoppingcart: function () {
      this.validateShoppingcartEmpty()
      this.verifySeatsMarked()
      let validate = true
      // if (Object.keys(this.items).length === 0 && this.items.constructor === Object) {
      if (Object.keys(this.items).length === 0) {
        validate = false
      } else {
        for (var keyGroup in this.items) {
          if (this.items[keyGroup].length === 1 && this.items[keyGroup][0].typeWay !== 0) {
            // si el viaje es redondo y no tiene la vuelta
            validate = false
          }
          this.items[keyGroup].forEach((travel) => {
            if (travel.isMultiroute) {
              if ('path' in travel) {
                if (travel.path.length !== travel.numberPaths) {
                  // No marco asientos en todos los tramos
                  validate = false
                }
                travel.path.forEach((oPath) => {
                  if ('seats' in oPath) {
                    oPath.seats.forEach((seat) => {
                      if (!('dataPerson' in seat)) {
                        // No tiene registrado al pasajero
                        validate = false
                      }
                    })
                  } else {
                    // No existe asientos
                    validate = false
                  }
                })
                if (validate === true) {
                  // verificamos si todos los tramos tienen el mismo numero de asientos marcados
                  if (!travel.path.every((obj, i, arr) => obj.seats.length === arr[0].seats.length)) {
                    validate = false
                  }
                }
              } else {
                // No existe path de multirura
                validate = false
              }
            } else {
              if ('seats' in travel) {
                travel.seats.forEach((seat) => {
                  if (!('dataPerson' in seat)) {
                    // No tiene registrado al pasajero
                    validate = false
                  }
                })
              } else {
                // No existe asientos
                validate = false
              }
            }
          })
        }
      }
      this.showBtnPayment = validate
      this.showModalPayment = validate
    },
    verifySeatsMarked: function () {
      let validate = true
      // if (Object.keys(this.items).length === 0 && this.items.constructor === Object) {
      if (!this.verifyCompletedForms()) {
        validate = false
      } else {
        if (Object.keys(this.items).length === 0) {
          validate = false
        } else {
          for (var keyGroup in this.items) {
            this.items[keyGroup].forEach((travel) => {
              if (travel.isMultiroute) {
                var indexMaxPassenger = 0
                travel.path.forEach((oPath, index) => {
                  if (oPath.seats.length > travel.path[indexMaxPassenger].seats.length) {
                    indexMaxPassenger = index
                  }
                })
                travel.path[indexMaxPassenger].seats.forEach((seat, index) => {
                  for (var i = 0; i < travel.numberPaths; i++) {
                    if ('path' in travel) {
                      var oPath = travel.path.find(obj => obj.indexPath === i + 1)
                      if (typeof oPath === 'undefined' || !(index in oPath.seats)) {
                        validate = false
                      }
                    } else {
                      validate = false
                    }
                  }
                })
              }
            })
          }
        }
      }
      this.showBtnReturnTrip = validate
    },
    countSeatsMarked: function () {
      let count = 0
      if (Object.keys(this.items).length > 0) {
        for (var keyGroup in this.items) {
          this.items[keyGroup].forEach((travel) => {
            if (travel.isMultiroute) {
              if ('path' in travel) {
                travel.path.forEach((oPath, index) => {
                  count = count + oPath.seats.length
                })
              }
            } else {
              if ('seats' in travel) {
                count = count + travel.seats.length
              }
            }
          })
        }
      }
      return count
    },
    shoppingCartList: function () {
      var vue = this
      this.$http.get(process.env.URL_LIST_SHOPPINGCART).then((response) => {
        this.createShoppingCart(response.data.data)
        this.verifySeatsMarked()
        this.verifyCompleteShoppingcart()
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => {
              vue.$modal.hide('dialog')
              this.setLoader({loader: true})
              this.$http.put(process.env.URL_END_SESSION).then(() => {
                this.setLoader({loader: false})
              })
            }
          }]
        })
      })
    }
  },
  components: {
    'be-passenger': cPassenger
  }
}
</script>
<style>
.cartTransition-enter-active, .cartTransition-leave-active {
  transition: ease-in .2s
}
.cartTransition-enter, .cartTransition-leave-to /* .fade-leave-active in <2.1.8 */ {
  right: -37em;
}
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(5px);
  opacity: 0;
}

@media (max-width: 767.98px) {
  .information-passenger{
    width: 100%;
  }
  .information-passenger .information-title{
    position: relative;
  }
  .information-passenger .information-title .btn-cart-menu{
    position: absolute;
    top: 12px;
    right: 18px;
    padding: 4px 10px;
    border: 1px solid #fff;
    border-radius: 4px;
    background: none;
  }
  .information-passenger .list-passenger.short{
    height: 85%;
  }
  .information-passenger .list-passenger-detail .list-passenger-title{
    padding: 10px 0px 10px 10px;
  }
  .information-passenger .travel-tittle{
    width: 100%;
    height: 40px;
    padding-top: .2em;
  }
  .information-passenger .content-cart{
    position: static !important;
    width: 100% !important;
    background-color: #0d47a1;
    text-align: center;
    padding: 0px 0px .8em 0px;
  }
  .information-passenger .content-cart .new-purchase{
    font-weight: bold;
    border-radius: 0.25rem;
  }
  .information-passenger .content-cart .btn-confirm{
    padding: 7px 1em;
  }
  .information-passenger .content-cart > div{
    float: none !important;
    display: inline-block;
  }
  .information-passenger .travel-tittle .travel-depart{
    width: 49%;
  }
  .information-passenger .travel-tittle .travel-arrival{
    width: 49%;
  }
  .content-dep-price {
    position: static !important;
    width: 100% !important;
  }
  .content-dep-price .travel-departure-date{
    float: none !important;
    width: 100% !important;
    height: auto !important;
  }
  .information-passenger .content-dep-price .travel-departure-date span{
    display: inline-block;
    margin-right: 10px;
  }
  .list-passenger-detail .list > div > div:first-child{
    width: 100%;
    position: relative;
    border-right: none;
    // border-bottom: 1px solid #dee5e8;
  }
  .list-passenger-detail .list > div > .price-seat-cart{
    width: 20% !important;
    position: absolute;
    top: 0;
    right: 0;
  }
  .list-passenger-detail .btn-return-trip{
    margin: 0.2em 0 !important;
  }
  .list-passenger-detail .btn-return-trip > span{
    display: none;
  }
  .list-passenger-detail .btn-return-trip > i{
    margin-left: 0 !important;
  }
  .information-passenger .list div .register-passenger{
    width: 80%;
  }
  .information-passenger .list .path-oneway,
  .information-passenger .list .path-roundway{
    width: 48% !important;
  }
}
</style>



