<template>
  <div class="container-generic">
    <h2>{{ $t('LBL_CONTACT_US') }}</h2>
    <form action="">
        <div class="row">
            <div class="col-6 form-group">
                <input class="form-control" type="text" :placeholder="$t('PHD_FIRST_NAME')" :title="$t('PHD_FIRST_NAME')">
            </div>
            <div class="col-6 form-group">
                <input class="form-control" type="email" :placeholder="$t('PHD_EMAIL')">
            </div>
            <div class="col-12 form-group">
                <textarea rows="4" :placeholder="$t('PHD_MESSAGE')">
                </textarea>
            </div>
        </div>
        <button type="submit" class="btn btn-primary btn-send">
            <i class="fa fa-send" aria-hidden="true"></i>
        </button>
    </form>
   </div>
</template>

<script>
export default {

}
</script>



