/* ============
 * Actions for the search module
 * ============
 *
 * The actions that are available on the
 * search module.
 */

import * as types from './mutation-types'

export const swapLocations = ({ commit }) => {
  commit(types.SWAP_LOCATIONS)
}
export const setTravel = ({ commit }, payload) => {
  commit(types.SET_TRAVEL, payload)
}
export const listTravel = ({ commit }, payload) => {
  commit(types.LIST_TRAVEL, payload)
}
export const clearTravel = ({ commit }) => {
  commit(types.CLEAR_TRAVEL)
}
export const changeTravelFare = ({ commit }, payload) => {
  commit(types.CHANGE_TRAVEL_FARE, payload)
}
export const restartState = ({ commit }) => {
  commit(types.RESTART_STATE)
}

export default {
  swapLocations,
  setTravel,
  listTravel,
  clearTravel,
  changeTravelFare,
  restartState
}



