/* ============
 * Mutations for the travels module
 * ============
 *
 * The mutations that are available on the
 * travels module.
 */
import { SET_LIST_TRAVEL, SET_LIST_OPTIONS, SET_SELECT_TRAVEL, RESTART_STATE } from './mutation-types'

export default {
  [SET_LIST_TRAVEL] (state, listTravel) {
    state.LIST_TRAVEL = listTravel
  },
  [SET_LIST_OPTIONS] (state, listOptions) {
    state.LIST_OPTIONS = listOptions
  },
  [SET_SELECT_TRAVEL] (state, key) {
    Object.assign(state.SELECT_TRAVEL, key)
  },
  [RESTART_STATE] (state) {
    state.LIST_TRAVEL = null
    Object.assign(state.SELECT_TRAVEL, {
      KEY_FARE: null,
      KEY_FARE_RW: null,
      TYPE_WAY: 0,
      FARE_ID: null,
      TRAVEL_ADD_CART_OW: false,
      TRAVEL_ADD_CART_RW: false,
      KEY_GROUP_TRAVEL: null
    })
  }
}



