<template>
    <div class="datepicker__calendar" v-if="visible" @click.stop >
        <div v-if="showInfo.day" class="datepicket__calendar__content">
            <div class="datepicker__calendar__header">
                <div class="datepicker__calendar__month">
                    <div class="datepicker__calendar__controllers">
                        <button class="controller btn" @click.prevent="prevMonth()">
                            <i class="fa fa-angle-left"></i>
                        </button>
                        <transition name="show" mode="out-in">
                        <span :key="month.getMonthName()" v-text="month.getMonthName()"></span>
                        </transition>

                        <button class="controller btn" @click.prevent="nextMonth()">
                            <i class="fa fa-angle-right"></i>
                        </button>
                    </div>
                </div>
                <div class="datepicker__calendar__year__container">
                    <div :class="show ? 'datepicker__calendar__year': ''"  @click="showYear()">
                        <transition name="show" mode="out-in">
                            <span :key="month.getYear()">{{ month.getYear() }}</span>
                        </transition>
                    </div>
                </div>
            </div>
            <div class="datepicker__calendar__selector__header">
                <div class="datepicker__calendar__selector__weekdays" v-for="(day, index) in days" :key="'dcsw' + index" v-text="day"></div>
            </div>
            <transition name="show" mode="out-in">
                <div :key="month.getMonthName()" class="datepicker__calendar__selector__body">
                    <div v-for="(n, index) in month.getWeekStart()" :key="'dcsd1' + index" class="datepicker__calendar__selector__days"></div>
                    <div v-for="(day, index) in month.getDays()" :key="'dcsd2' + index" class="datepicker__calendar__selector__days" :class="[{'active': isActive(day)}, {'disabled': isDisabled(day)}]" @click="selectDate(day)">
                        <span>{{ day.format('D') }}</span>
                    </div>
                </div>
            </transition>
        </div>
        <div v-if="showInfo.year" class="datepicket__calendar__content">
            <div class="datepicker__calendar__header">

            </div>
            <div class="datepicker__calendar__selector__header">
                <div id="yearList" class="datepicker__calendar__selector__years">
                    <div class="datepicker__calendar__selector__year" v-for="(yearItem, index) in library.year" :key="'dcsy' + index" @click="setYear(yearItem)">{{ yearItem }}</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Month from '@/modules/month'
import moment from 'moment'
import { mapGetters } from 'vuex'

export default {
  props: {
    date: {},
    visible: { type: Boolean },
    minDate: { type: Object },
    show: { type: Boolean }
  },
  data: function () {
    return {
      days: moment.weekdaysShort(),
      month: new Month(this.date.month(), this.date.year()),
      showInfo: {
        day: true,
        month: false,
        year: false
      },
      library: {
        month: [], // Meses  ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
        year: []
      }
    }
  },
  computed: {
    ...mapGetters({
      lang: 'auth/lang'
    })
  },
  watch: {
    minDate: {
      handler: function () {
        // if (this.minDate.unix() > this.date.unix()) {
        if (this.minDate > this.date) {
          this.$emit('changedDate', this.minDate.clone())
          this.month = new Month(this.minDate.month(), this.minDate.year())
        }
        // }
      },
      deep: true
    },
    lang: {
      handler: function (val) {
        // this.date = moment(this.date).locale(val)
        this.days = moment.weekdaysShort()
        this.month = new Month(this.date.month(), this.date.year())
      },
      deep: true
    }
  },
  methods: {
    isActive: function (day) {
      return this.date.unix() === day.unix()
    },
    isDisabled: function (day) {
      if (!this.show) {
        return day.isBefore(this.minDate)
      } else {
        return this.minDate.isBefore(day)
      }
    },
    selectDate: function (day) {
      this.$emit('changedDate', day.clone())
    },
    cancel: function () {
      this.$emit('cancel')
    },
    nextMonth: function () {
      let month = this.month.month + 1
      let year = this.month.year
      if (month > 11) {
        year += 1
        month = 0
      }
      this.month = new Month(month, year)
    },
    prevMonth: function () {
      let month = this.month.month - 1
      let year = this.month.year
      if (month < 0) {
        year -= 1
        month = 11
      }
      this.month = new Month(month, year)
    },
    showOne: function (type) {
      switch (type) {
        case 'year':
          this.showInfo.day = false
          this.showInfo.month = false
          this.showInfo.year = true
          break
        case 'month':
          this.showInfo.day = false
          this.showInfo.month = true
          this.showInfo.year = false
          break
        default:
          this.showInfo.day = true
          this.showInfo.year = false
          this.showInfo.month = false
          break
      }
    },
    addYear: function () {
      var _this4 = this
      var listDom = document.getElementById('yearList')
      listDom.addEventListener('scroll', function (e) {
        if (listDom.scrollTop < listDom.scrollHeight - 100) {
          var len = _this4.library.year.length
          var lastYear = _this4.library.year[len - 1]
          _this4.library.year.push(lastYear + 1)
        }
      }, false)
    },
    showYear: function () {
      // Puede seleccionar cualquier día
      if (this.show) {
        // var _this3 = this
        var year = this.date.year()
        this.library.year = []
        var yearTmp = []
        for (var i = year - 60; i <= year; i++) {
          yearTmp.push(i)
        }
        this.library.year = yearTmp
        this.showOne('year')
        this.$nextTick(function () {
          var listDom = document.getElementById('yearList')
          listDom.scrollTop = listDom.scrollHeight - 100
        //   _this3.addYear()
        })
      }
    },
    setYear: function (year) {
    //   moment().year(year)
      var newDate = moment().year(year)
      this.month = new Month(newDate.month(), newDate.year())
      this.showOne()
    //   this.$emit('changedYear', newDate)
    }
  }
}

</script>
<style lang="scss">
@import '../../styles/base/variables';
@keyframes show-me {
   0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.none{
    display: none;
}
.show-enter-active {
  animation: show-me 300ms;
}
.show-leave-active {
  animation: show-me 300ms reverse;
}
$total-width: 220px;
$day-width: ($total-width - 20) / 7;

.datepicker__calendar{
    .datepicket__calendar__content{
        margin-top: 14px;
        color: map-get($colors, white);
        // background: map-get($colors, white);
        position: absolute;
        top: 83%;
        left: -62px;
        z-index: 1000;
        // background: red;
        display: grid !important;
        .datepicker__calendar__header{
            &:before{
                content: '';
                position: absolute;
                width: 0;
                height: 0;
                left: 0;
                right: 0;
                margin: auto;
                border-left: 10px solid transparent;
                border-right: 10px solid transparent;
                border-bottom: 10px solid map-get($colors, secondary-01);
                top: -6px;
            }
            position: relative;
            width: $total-width;
            background: map-get($colors, secondary-01);
            padding: 5px 5px 0px;
            text-align: left;
            display: flex !important;
            justify-content: space-between;
            .datepicker__calendar__year__container {
                position: relative;
                .datepicker__calendar__year{
                    transition: all 1s linear;
                    font-size: 1.3rem;
                    z-index: 100;
                    position: relative;
                    cursor: pointer;
                    &:after{
                        content: '';
                        position: absolute;
                        left: 0;
                        top: 50%;
                        width: 100%;
                        height: 1px;
                        background-color: map-get($colors, white);
                        opacity: .3;
                        z-index: 99
                    }
                    & > span {
                        position: relative;
                        padding-right: 6px;
                        background: map-get($colors, secondary-01);
                        z-index: 100;
                    }
                }
            }
            .datepicker__calendar__month{
                font-size: 18px;
                font-weight: normal;
                text-transform: capitalize;
                .datepicker__calendar__controllers{
                    float: right;
                    .controller{
                        outline:0;
                        box-shadow: none;
                        border: none;
                        background: map-get($colors, secondary-01);
                        &:hover i {
                            transform: scale(1.4);
                            font-weight: bold;
                        }
                        i {
                            transition: transform 200ms linear;
                            color: map-get($colors, white);
                        }
                    }
                }
            }
        }
        .datepicker__calendar__selector__header{
            padding: 2px 4px 0;
            min-height: 24px;
            width: $total-width;
            background: map-get($colors, secondary-04);
            .datepicker__calendar__selector__weekdays{
                font-size: 13px;
                float: left;
                text-transform: capitalize;
                width: $day-width;
                margin-right: 2px;
            }
            .datepicker__calendar__selector__years{
                overflow: auto;
                display: flex;
                flex-direction: column;
                height: $total-width;
                .datepicker__calendar__selector__year{
                    cursor: pointer;
                }
            }
            & div:last-child{
                margin-right: 0px;
            }
        }
        .datepicker__calendar__selector__body{
            padding: 5px 5px 0;
            background: map-get($colors, white);
            box-shadow: 0 4px 10px -2px grey;
            .datepicker__calendar__selector__days{
                &.disabled{
                    color: map-get($colors, gray-08);
                    pointer-events: none;
                    cursor: not-allowed;
                }
                color: rgb(105, 108, 110);
                font-size: 16px;
                float: left;
                width: $day-width + 1;
                line-height: $day-width;
                height: $day-width;
                position: relative;
                & > span {
                    margin: 0 auto;
                    display: block;
                    width: $day-width - 2;
                    line-height: $day-width - 4;
                    height: $day-width - 2;
                    border-radius: 50%;
                    border: map-get($colors, white) 1px solid;
                    &:hover {
                        cursor: pointer;
                        border: map-get($colors, secondary-04) 1px solid;
                    }
                }
                &.active > span:hover {
                    border: map-get($colors, secondary-04) 1px solid;
                    background: map-get($colors, secondary-04);
                }
                &.active > span{
                    color: map-get($colors, white);
                    background: map-get($colors, secondary-04);
                    border: 1px solid map-get($colors, secondary-04);
                }
            }
        }
    }
}
.controller.btn {
    padding: 0 4px;
    cursor: pointer;
}
</style>



