<template>
  <div class="container-vselect">
  <v-select v-model="selectShuttle" :options="listShuttle"></v-select>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  props: {
    keyTravel: {
      type: String
    },
    type: {
      type: String
    },
    valueDefault: {
      type: Object,
      default: null
    }
  },
  data () {
    return {
      listShuttle: [],
      selectShuttle: null
    }
  },
  watch: {
    selectShuttle: function (val) {
      this.$emit('selectShuttle', val)
    }
  },
  created: function () {
    let vue = this
    // this.setLoader({loader: true})
    this.$http.get(`${process.env.URL_GET_SHUTTLE}?key=` + this.keyTravel + `&type=` + this.type).then(response => {
      response.data.data.forEach((obj) => {
        this.listShuttle.push({
          label: obj.label,
          value: obj.id
        })
      })
    }).catch(function (error) {
      vue.$modal.hide('dialog')
      vue.$modal.show('dialog', {
        title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
        text: error.response.data.details,
        buttons: [{
          title: vue.$t('LBL_ACCEPT'),
          handler: () => { vue.$modal.hide('dialog') }
        }]
      })
    }).then(() => {
      // this.setLoader({loader: false})
      this.selectShuttle = this.valueDefault
    })
  },
  methods: {
    ...mapActions({
      setLoader: 'loader/setLoader'
    })
  }
}
</script>
<style lang="scss">
  .container-vselect{
    .dropdown-toggle{
      button.clear{
        display: none;
      }
    }
  }
</style>



