<template>
  <div class="">
    <div class="background-travel"></div>
    <div class="width-general">
      <div class="info-travel travel-container">
        <div class="col-sm-12 col-md-6 places">
          <ul>
            <li :class="typeTravelOW()">{{ $t('LBL_ONE_WAY') }}<i class="fa fa-circle" aria-hidden="true"></i></li>
            <li :class="typeTravelRT()" v-if="selectTravel.TYPE_WAY === 1 || selectTravel.TYPE_WAY === 2">{{ $t('LBL_RETURN_TRIP') }}<i class="fa fa-circle" aria-hidden="true"></i></li>
          </ul>
        </div>
        <div class="col-sm-6 travel-date inline" v-if="selectTravel.TYPE_WAY === 0 || selectTravel.TYPE_WAY === 1">
         <div class="dates-container departure">
           <div class="button-prev" @click="selectPrev()" v-show="!(getDate.unix() <= today.unix())">
             <i class="fa fa-angle-left"></i>
           </div>
           <div class="wrapper">
             <div class="slide" :key="index" v-for="(day, index) in days" @click="changeDate(day)">
               <div class="calendar inline" :class="{ 'selected' : day.unix() === getDate.unix() }">
                 <div class="">
                   <div class="calendar-head" style="text-transform:capitalize;">{{ day.format('dddd') }}</div>
                   <div class="calendar-body">
                     <div class="num">{{ day.format('DD') }}</div>
                     <span class="" style="text-transform:capitalize;">{{ day.format('MMM') }} <span>{{ day.format('YYYY') }}</span></span>
                   </div>
                 </div>
               </div>
             </div>
           </div>
           <div class="button-next" @click="selectNext()">
             <i class="fa fa-angle-right"></i>
           </div>
         </div>
       </div>
     </div>
   </div>
 </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import Moment from 'moment'
import { extendMoment } from 'moment-range'

const moment = extendMoment(Moment)

export default {
  data: function () {
    return {
      today: moment().startOf('day'),
      days: []
    }
  },
  computed: {
    ...mapGetters({
      departure: 'travel/departure',
      arrival: 'travel/arrival',
      departureDate: 'search/dateDeparture',
      arrivalDate: 'search/dateArrival',
      returnDate: 'search/dateReturn',
      selectTravel: 'travels/selectTravel',
      lang: 'auth/lang',
      items: 'cart/items',
      search: 'search/travel'
    }),
    getDate () {
      if (this.selectTravel.TYPE_WAY === 2) {
        return moment(this.arrivalDate).locale(this.lang)
      } else {
        return moment(this.departureDate).locale(this.lang)
      }
    }
  },
  mounted: function () {
    this.buildCalendar()
  },
  methods: {
    buildCalendar: function () {
      var diff = this.getDate.diff(this.today, 'days')
      var prev = null
      var next = null
      if (diff < 2) {
        prev = this.getPrevDays(2 - diff, this.getDate).start
        next = this.getNextDays(5 - (diff + 1), this.getDate).end
      } else {
        prev = this.getPrevDays(2, this.getDate).start
        next = this.getNextDays(2, this.getDate).end
      }
      this.days = Array.from(moment.range(prev, next).by('days'))
    },
    typeTravelOW (value) {
      if (this.selectTravel.TYPE_WAY === 1 || this.selectTravel.TYPE_WAY === 0) {
        return value === this.selected ? 'active' : ''
      }
    },
    typeTravelRT (value) {
      if (this.selectTravel.TYPE_WAY === 2) {
        return value === this.selected ? 'active' : ''
      }
    },
    getPrevDays (days, currentDay) {
      let posibleFirstDay = currentDay.clone().subtract(days, 'days').startOf('day')
      let firstDay = posibleFirstDay.unix() < this.today.unix() ? currentDay.clone() : posibleFirstDay
      return moment.range(firstDay, currentDay.clone())
    },
    getNextDays (days, currentDay) {
      let lastDay = currentDay.clone().add(days, 'days').startOf('day')
      return moment.range(currentDay.clone(), lastDay)
    },
    changeDate (date) {
      if (date.unix() !== this.getDate.unix()) {
        if (this.search.DATE_RETURN != null) {
          if (date.unix() > this.search.DATE_RETURN.unix()) {
            this.$modal.hide('dialog')
            this.$modal.show('dialog', {
              title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
              text: this.$t('MSG_DEPARTURE_DATE_GREATER_RETURN_DATE'),
              buttons: [{
                title: this.$t('LBL_ACCEPT'),
                handler: () => { this.$modal.hide('dialog') }
              }]
            })
          } else {
            if (this.selectTravel.TYPE_WAY === 2) {
              this.setTravel({ DATE_RETURN: date })
            } else {
              this.setTravel({ DATE_DEPARTURE: date })
            }
            let query = {
              d: this.search.DEPARTURE.id,
              a: this.search.ARRIVAL.id,
              dd: this.search.DATE_DEPARTURE.format('YYYY-MM-DD')
            }
            if (this.search.DATE_RETURN != null) {
              Object.assign(query, {rd: this.search.DATE_RETURN.format('YYYY-MM-DD')})
            }
            this.$router.push({
              name: 'services',
              query: query
            })
          }
        } else {
          if (this.verifyCompleteNowTravel(this.selectTravel.KEY_GROUP_TRAVEL)) {
            if (this.selectTravel.TYPE_WAY === 2) {
              this.setTravel({ DATE_RETURN: date })
            } else {
              this.setTravel({ DATE_DEPARTURE: date })
            }
            let query = {
              d: this.search.DEPARTURE.id,
              a: this.search.ARRIVAL.id,
              dd: this.search.DATE_DEPARTURE.format('YYYY-MM-DD')
            }
            if (this.search.DATE_RETURN != null) {
              Object.assign(query, {rd: this.search.DATE_RETURN.format('YYYY-MM-DD')})
            }
            // todo esta correcto y completado
            this.setSelectTravel({
              KEY_FARE: null,
              KEY_FARE_RW: null,
              TYPE_WAY: 0,
              TRAVEL_ADD_CART_OW: false,
              TRAVEL_ADD_CART_RW: false,
              KEY_GROUP_TRAVEL: null
            })
            this.$router.push({
              name: 'services',
              query: query
            })
          } else {
            // falta completar datos en el viaje
            this.$modal.hide('dialog')
            this.$modal.show('dialog', {
              title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
              text: this.$t('MSG_INCOMPLETE_TRIP'),
              buttons: [
                {
                  title: this.$t('LBL_ACCEPT'),
                  handler: () => {
                    this.$modal.hide('dialog')
                    var vue = this
                    this.setLoader({loader: true})
                    this.$http.delete(process.env.URL_REMOVE_SHOPPINGCART, { data: {key: this.selectTravel.KEY_FARE} }).then(response => {
                      this.$eventBus.$emit('loadShoppingCart')
                    }).catch(function (error) {
                      vue.$modal.hide('dialog')
                      vue.$modal.show('dialog', {
                        title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
                        text: error.response.data.details,
                        buttons: [{
                          title: vue.$t('LBL_ACCEPT'),
                          handler: () => { vue.$modal.hide('dialog') }
                        }]
                      })
                    }).then(() => {
                      if (this.selectTravel.TYPE_WAY === 2) {
                        this.setTravel({ DATE_RETURN: date })
                      } else {
                        this.setTravel({ DATE_DEPARTURE: date })
                      }
                      let query = {
                        d: this.search.DEPARTURE.id,
                        a: this.search.ARRIVAL.id,
                        dd: this.search.DATE_DEPARTURE.format('YYYY-MM-DD')
                      }
                      if (this.search.DATE_RETURN != null) {
                        Object.assign(query, {rd: this.search.DATE_RETURN.format('YYYY-MM-DD')})
                      }
                      this.setSelectTravel({
                        KEY_FARE: null,
                        KEY_FARE_RW: null,
                        TYPE_WAY: 0,
                        TRAVEL_ADD_CART_OW: false,
                        TRAVEL_ADD_CART_RW: false,
                        KEY_GROUP_TRAVEL: null
                      })
                      vue.setLoader({loader: false})
                      this.$router.replace({
                        name: 'services',
                        query: query
                      }, function () {
                        this.$eventBus.$off()
                      }, function () {
                        vue.$emit('loadTravels')
                      })
                    })
                  }
                },
                {
                  title: this.$t('LBL_CANCEL'),
                  handler: () => {
                    this.$modal.hide('dialog')
                  }
                }
              ]
            })
          }
        }
      }
    },
    selectNext () {
      let nextDay = this.getDate.clone().add(1, 'days')
      if (this.selectTravel.TYPE_WAY === 2) {
        this.setTravel({ DATE_RETURN: nextDay })
      } else {
        this.setTravel({ DATE_DEPARTURE: nextDay })
      }
    },
    selectPrev () {
      let prevDay = this.getDate.clone().subtract(1, 'days')
      if (this.selectTravel.TYPE_WAY === 2) {
        this.setTravel({ DATE_RETURN: prevDay })
      } else {
        this.setTravel({ DATE_DEPARTURE: prevDay })
      }
    },
    verifyCompleteNowTravel: function (keyGroup) {
      let validate = true
      if (Object.keys(this.items).length === 0) {
        validate = true
      } else {
        if (keyGroup in this.items) {
          if (this.items[keyGroup].length === 1 && this.items[keyGroup][0].typeWay !== 0) {
            // si el viaje es redondo y no tiene la vuelta
            validate = false
          }
          this.items[keyGroup].forEach((travel) => {
            if (travel.isMultiroute) {
              if ('path' in travel) {
                if (travel.path.length !== travel.numberPaths) {
                  // No marco asientos en todos los tramos
                  validate = false
                }
                travel.path.forEach((oPath) => {
                  if ('seats' in oPath) {
                    oPath.seats.forEach((seat) => {
                      if (!('dataPerson' in seat)) {
                        // No tiene registrado al pasajero
                        validate = false
                      }
                    })
                  } else {
                    // No existe asientos
                    validate = false
                  }
                })
                if (validate === true) {
                  // verificamos si todos los tramos tienen el mismo numero de asientos marcados
                  if (!travel.path.every((obj, i, arr) => obj.seats.length === arr[0].seats.length)) {
                    validate = false
                  }
                }
              } else {
                // No existe path de multirura
                validate = false
              }
            } else {
              if ('seats' in travel) {
                travel.seats.forEach((seat) => {
                  if (!('dataPerson' in seat)) {
                    // No tiene registrado al pasajero
                    validate = false
                  }
                })
              } else {
                // No existe asientos
                validate = false
              }
            }
          })
        }
      }
      return validate
    },
    ...mapActions({
      setLoader: 'loader/setLoader',
      setTravel: 'search/setTravel',
      setSelectTravel: 'travels/setSelectTravel'
    })
  },
  watch: {
    'departureDate': function () {
      this.buildCalendar()
    },
    'arrivalDate': function () {
      this.buildCalendar()
    },
    'getDate': function () {
      this.buildCalendar()
    }
  }
}
</script>

<style lang="scss">
</style>



