/* ============
 * Mutation types for the account module
 * ============
 *
 * The mutation types that are available
 * on the auth module.
 */

export const CHECK = 'CHECK'
export const LOGIN = 'LOGIN'
export const CULQI = 'CULQI'
export const LOGIN_SELLER = 'LOGIN_SELLER'
export const LOGOUT = 'LOGOUT'
export const RESTART_STATE = 'RESTART_STATE'
export const SET_LANG = 'SET_LANG'

export default {
  CHECK,
  LOGIN,
  CULQI,
  LOGIN_SELLER,
  LOGOUT,
  RESTART_STATE,
  SET_LANG
}



