<template lang="html">
  <div class="container-payment-method" v-if="response">
    <div class="header-down"></div>
    <div class="row">
      <div class="col-12">
        <h4 class="title-purchase-detail">{{ $t('LBL_PURCHASE_DETAIL') }}
            <div class="btn-cancelPurchase">
                <button class="btn cancelPurchase" type="button" name="button" v-on:click="removeLocalStorage()">{{ $t('LBL_CANCEL_PURCHASE') }}</button>
            </div>
        </h4>
      </div>
    </div>
    <div class="row resetRow">
        <div class="col-12" v-for="(item, index) in shoppingCart.items" :key="index">
          <section>
            <summary-detail @setShuttleValue="setShuttleValue" @applyCoupon="applyCoupon" @realoadShoppingcartList="shoppingCartList" :travels = "item" :keyGroupTravel = "index">
            </summary-detail>
          </section>
        <br />
        </div>
    </div>
    <div class="resetRow lineTop">
      <div class="row ">
        <div class="col-12 offset-md-6 col-md-6">
          <div class="payment-amount">
            <div class="row">
              <div class="col-5 col-md-5">
                <p class="title-detail">{{ $t('LBL_SUBTOTAL') }}</p>
              </div>
              <div class="col-7 col-md-7 text-right">
                <p>{{getCurrency()}} {{shoppingCart.subtotal}}</p>
              </div>
            </div>
            <div class="row">
              <div class="col-5 col-md-5">
                <p class="title-detail">{{ $t('LBL_DISCOUNT') }}</p>
              </div>
              <div class="col-7 col-md-7 text-right">
                <p>{{getCurrency()}} {{shoppingCart.discount}}</p>
              </div>
            </div>
            <div class="row">
              <div class="col-7 col-md-5">
                <p class="title-detail">{{ $t('LBL_COMMISSION_SALE') }}</p>
              </div>
              <div class="col-5 col-md-7 text-right">
                <p>{{getCurrency()}} {{shoppingCart.fee}}</p>
              </div>
            </div>
            <section class="row">
              <div class="col-5 col-md-5">
                <p class="title-detail">{{ $t('LBL_TOTAL') }}</p>
              </div>
              <div class="col-7 col-md-7 text-right">
                <p><span>{{getCurrency()}}</span><span>{{shoppingCart.total}}</span></p>
              </div>
            </section>
          </div>
          <div class="container-button-pay">
            <section>
              <div class="row resetRow">
                <div class="col btn-add">
                  <button class="col-md-12 btn addNewTrip" type="button" name="button" v-on:click="goTravels()">{{ $t('LBL_ADD_NEW_TRIP') }}</button>
                </div>
                <div class="col btn-add">
                  <button v-if="enableButtonPay" class="col-sm-12 btn btn-primary newPurchase" type="button" name="button" v-on:click="goPayTickets()">{{ $t('LBL_PAY') }}</button>
                  <button v-else class="col-sm-12 btn btn-primary newPurchase" disabled type="button" name="button" v-on:click="goPayTickets()">{{ $t('LBL_PAY') }}</button>
                </div>
              </div>
            </section>
          </div>
        </div>
     </div>
    </div>
</div>
<div class="container-payment-method" v-else>
    <h4 class="title-purchase-detail">{{ $t('CVAL_NOT_SHOPPING_CART_IN_SESSION') }}</h4>
</div>
</template>

<script>
import Vue from 'vue'
import cSummaryDetail from '../components/cSummaryDetail'
import { mapGetters, mapActions } from 'vuex'
export default {
  data: function () {
    return {
      response: false,
      enableButtonPay: true,
      shoppingCart: {}
    }
  },
  beforeRouteLeave (to, from, next) {
    // se llama cuando iras a otra ruta
    if (this.shoppingCartHasTrips && to.name === 'home') {
      this.$modal.hide('dialog')
      this.$modal.show('dialog', {
        title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
        text: this.$t('MSG_VALIDATION_LEAVE_PAGE'),
        buttons: [
          {
            title: this.$t('LBL_ACCEPT'),
            handler: () => {
              this.$modal.hide('dialog')
              this.setLoader({loader: true})
              this.$http.put(process.env.URL_END_SESSION).then(response => {
                console.log(response)
              }).catch(function (error) {
                console.log(error)
              }).then(() => {
                this.setLoader({loader: false})
                next()
              })
            }
          },
          {
            title: this.$t('LBL_CANCEL'),
            handler: () => {
              this.$modal.hide('dialog')
              next(false)
            }
          }
        ]
      })
    } else {
      next()
    }
  },
  mounted: function () {
    this.shoppingCartList()
  },
  computed: {
    ...mapGetters({
      items: 'cart/items',
      shoppingCartHasTrips: 'cart/shoppingCartHasTrips'
    })
  },
  methods: {
    normalizeItem: function (travel, isRound) {
      console.log('isRound')
      console.log(isRound)
      if (isRound === 0) {
        if (travel.isDepartureShuttle === 1 && travel.departureShuttleId === null) {
          this.enableButtonPay = false
        } else if (travel.isArrivalShuttle === 1 && travel.arrivalShuttleId === null) {
          this.enableButtonPay = false
        }
      }
      return Object.assign({}, {
        company: travel.company,
        companySkin: travel.companySkin,
        companyLogo: travel.companyLogo,
        departure: travel.departureName,
        arrival: travel.arrivalName,
        departureDate: travel.departureDate,
        arrivalDate: travel.arrivalDate,
        travelTime: travel.travelTime,
        key: travel.key,
        couponApplies: travel.couponApplies,
        discount: travel.discount,
        subtotal: travel.subtotal,
        total: travel.total,
        currency: travel.symbol,
        countryCode: travel.countryCode,
        isArrivalShuttle: travel.isArrivalShuttle,
        isDepartureShuttle: travel.isDepartureShuttle,
        departureShuttleId: travel.departureShuttleId,
        departureShuttleName: travel.departureShuttleName,
        arrivalShuttleId: travel.arrivalShuttleId,
        arrivalShuttleName: travel.arrivalShuttleName
      })
    },
    normalizeSeats: function (obj) {
      let aSeat = []
      obj.seats.forEach((seat) => {
        aSeat.push(Object.assign({}, {
          fullName: seat.passenger,
          number: seat.numberSeat,
          discount: seat.discount,
          subtotal: seat.subtotal,
          total: seat.total
        }))
      })
      return aSeat
    },
    loadShoppingCart: function (listShoppingcart) {
      this.enableButtonPay = true
      this.shoppingCart = {}
      this.shoppingCart.items = {}
      listShoppingcart.travelsOneWay.forEach((travel) => {
        if ('path' in travel) {
          let aPath = []
          travel.path.forEach((oPath) => {
            aPath.push(this.normalizeSeats(oPath))
          })
          let item = this.normalizeItem(travel, 0)
          Object.assign(item, {path: aPath})
          Vue.set(this.shoppingCart.items, travel.key, [item])
        } else {
          let item = this.normalizeItem(travel, 0)
          Object.assign(item, {seats: this.normalizeSeats(travel)})
          Vue.set(this.shoppingCart.items, travel.key, [item])
        }
      })
      if (listShoppingcart.travelsRoundTrip.length > 0) {
        listShoppingcart.travelsRoundTrip.forEach((travel) => {
          if (travel.roundKey in this.shoppingCart.items) {
            if ('path' in travel) {
              let aPath = []
              travel.path.forEach((oPath) => {
                aPath.push(this.normalizeSeats(oPath))
              })
              let item = this.normalizeItem(travel, 1)
              Object.assign(item, {path: aPath})
              this.shoppingCart.items[travel.roundKey].push(item)
            } else {
              let item = this.normalizeItem(travel, 1)
              Object.assign(item, {seats: this.normalizeSeats(travel)})
              this.shoppingCart.items[travel.roundKey].push(item)
            }
          }
        })
      }
      this.shoppingCart.discount = listShoppingcart.discount
      this.shoppingCart.subtotal = listShoppingcart.subTotal
      this.shoppingCart.fee = listShoppingcart.fee
      this.shoppingCart.total = listShoppingcart.subTotal + listShoppingcart.fee
      this.shoppingCart.currency = this.getCurrency()
    },
    goTravels: function () {
      this.setSelectTravel({
        KEY_FARE: null,
        KEY_FARE_RW: null,
        TYPE_WAY: 0,
        TRAVEL_ADD_CART_OW: false,
        TRAVEL_ADD_CART_RW: false,
        KEY_GROUP_TRAVEL: null
      })
      this.$router.push({
        name: 'services'
      })
    },
    goPayTickets: function () {
      this.$router.push({
        name: 'Payment'
      })
    },
    applyCoupon: function () {
      this.shoppingCartList()
    },
    setShuttleValue: function () {
      this.shoppingCartList()
    },
    shoppingCartList: function () {
      var vue = this
      this.setLoader({loader: true})
      this.$http.get(process.env.URL_LIST_SHOPPINGCART).then((response) => {
        if (response.data.code === 10) {
          this.data = response.data.data
          if (response.data.data.travelsOneWay.length > 0) {
            this.response = true
            console.log('julio')
            // this.createShoppingCart(response.data.data)
            this.loadShoppingCart(response.data.data)
          } else {
            this.response = false
            this.$router.push({
              name: 'delete'
            })
          }
        } else {
          this.response = false
          this.$router.push({
            name: 'delete'
          })
        }
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      }).then(() => {
        this.setLoader({loader: false})
      })
    },
    getCurrency: function () {
      for (var keyGroup in this.items) {
        return this.items[keyGroup][0].currency
      }
    },
    removeLocalStorage () {
      this.$modal.hide('dialog')
      this.$modal.show('dialog', {
        title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
        text: this.$t('MSG_DELETE_SHOPPING_CART'),
        buttons: [
          {
            title: this.$t('LBL_YES'),
            handler: () => {
              this.$modal.hide('dialog')
              this.setLoader({loader: true})
              this.$http.put(process.env.URL_END_SESSION).then(response => {
                console.log(response)
              }).catch(function (error) {
                console.log(error)
              }).then(() => {
                this.setLoader({loader: false})
                this.cartRestartState()
                this.$router.push({
                  name: 'home'
                })
              })
            }
          },
          {
            title: this.$t('LBL_NO'),
            handler: () => {
              this.$modal.hide('dialog')
            }
          }
        ]
      })
    },
    ...mapActions({
      createShoppingCart: 'cart/createShoppingCart',
      setSelectTravel: 'travels/setSelectTravel',
      setLoader: 'loader/setLoader',
      cartRestartState: 'cart/restartState'
    })
  },
  components: {
    'summary-detail': cSummaryDetail
  }
}
</script>

<style lang="scss">
  @import '../styles/base/variables';
  .resetRow{
    margin-right: 0px !important;
    margin-left: 0px !important;
  }
  .lineTop{
    padding: 20px;
  }
  .container-payment-method{
    max-width: 1100px;
    margin: 0 auto;
    padding-top: 10em;
    padding-bottom: 15em;
    .header-down{
      height: 2em;
      background: map-get($colors, primary-01);
      position: absolute;
      top: 6.9em;
      left: 0;
      width: 100%;
    }
    .alert{
      border: 1px solid map-get($colors, gray-05);
      background-color: map-get($colors, gray-01);
      padding: 0px;

    .window-emergent {
    text-align: center;
    width: 80%;
    border-radius: 3px;
      }
    }

    .radio-buttom-container{
      @media screen and (min-width: 780px) {
        display: none;
      }
      padding: 0 2em;
      .btn-primary{
        cursor: pointer;
        &:not(.active){
          background-color: map-get($colors, white);
          color: map-get($colors, primary-01);
        }
      }
    }
    div{
      .general-payment{
        border: 1px solid map-get($colors, gray-05);
        border-radius: 4px;
        padding: 1em;
        div{
          h6{
            color: map-get($colors, gray-12);
            font-family: "Roboto-Medium"
          }
          p{
            margin: 0px;
            font-size: 13px;
            line-height: 15px;
            color: map-get($colors, gray-13);
          }
          figure{
            margin: 0;
            text-align: right;
          }
        }
        .text-option-hide{
          @media screen and (max-width: 780px) {
                display: none !important;
          }
        }
        .content-icon{
          width: 3em;
          display: inline-block;
          i{
            font-size: 2em;
            color: map-get($colors, gray-14);
            &:hover{
              cursor: pointer;
            }
          }
          .fa-check-circle{
            color: map-get($colors, secondary-07);
          }
        }
        .content-head-payment{
          display: inline-block;
          vertical-align: middle;
          width: 44%;
          @media screen and (max-width: 780px) {
            width: 100%;
          }
          figure{
            @media screen and (max-width: 780px) {
              text-align: center;
            }
          }
        }
        section{
          .line{
            border-bottom:1px solid map-get($colors, gray-13);
            margin: 0 auto;
            display: block;
            margin-bottom: 0.5em;
          }
          div{
            label{
              color: map-get($colors, gray-12);
            }
          }
        }
      }
      .green-line{
        border:1px solid map-get($colors, secondary-07);
      }
    }
    .has-success{
      form{
        text-align: center;
        padding: 0;
        display: inline-block;
        input,button{
          width: 100%;
          text-align: center;
          padding: .3rem .5rem;
          height: 2.5em;
          width: 50%;
          margin: 0 auto;
          display: inline-block;
          &::first-letter{
            text-transform: uppercase;
          }
        }
        input::-webkit-input-placeholder{
          color: gray;
          font-weight: 100;
        }
        input{
          text-transform: uppercase;
          font-weight: bold;
          color: map-get($colors, primary-01);
          margin-bottom: .5em;
        }
        .btn:disabled{
          opacity: .1;
          color: map-get($colors, white);
        }
        button{
          background: none;
          color: map-get($colors, gray-12);
          &:hover{
            background: map-get($colors, gray-05);
            cursor: pointer;
          }
        }
      }
    }
    .sub-total{
        font-size: 1.5em;
    }
    .btn-remove{
      background: none;
      border: 1px solid map-get($colors, gray-05);
      padding: 0 .5em;
      color: map-get($colors, gray-12);
      border-radius: 1em;
      font-size: 14px;
      font-weight: bold;
      &:hover{
          background: map-get($colors, gray-05);
          cursor: pointer;
      }
    }
  }
  .container-button-pay{
    width: 100%;
    bottom: 0px;
    .btn-add{
    padding: 5px;
    }
  }
  .container-payment-buttom{
    div{
      font-size: 12px;
      label{
        div{
          top: 0.8em
        }
      }
      &:last-child{
        padding-top: 3em;
      }
    }
    button{
      cursor: pointer;
    }
  }
  .title-purchase-detail{
    color: map-get($colors, primary-01);
    font-family: "Roboto-Medium";
    padding: 0.5em .5em .8em;
  }
  .addNewTrip{
    background: map-get($colors, gray-14);
    color: map-get($colors, white);
    cursor: pointer;
  }
  .cupon-discount{
    margin-top: 1em;
    margin-bottom: 1em;
    input{
      display: inline-block;
      width: 80%;
      padding: 0.4rem 0 0.4rem 0.7rem;
      &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: map-get($colors, gray-14);
      }
      &::-moz-placeholder { /* Firefox 19+ */
        color: map-get($colors, gray-14);
      }
      &:-ms-input-placeholder { /* IE 10+ */
        color: map-get($colors, gray-14);
      }
      &:-moz-placeholder { /* Firefox 18- */
        color: map-get($colors, gray-14);
      }
    }
    button{
      display: inline-block;
      width: 20%;
      border-radius: 0px;
      padding: 0.6rem 0;
      background: map-get($colors, secondary-07);
      &:hover{
        cursor: pointer;
      }
    }
  }
  .payment-amount{
    div{
      div{
        &:first-child{
          padding-right: 0px;
        }
        &:last-child{
          padding-left: 0px;
        }
        p{
          color: map-get($colors, gray-14);
          font-family: "Roboto-Medium";
          border-bottom: 1px solid map-get($colors, gray-14);
        }
      }
      .text-right{
        p{
          font-weight: 600;
          color: map-get($colors, gray-12);
        }
      }
    }
    section{
      color: map-get($colors, gray-12);
      font-family: "Roboto-Medium";
      font-size: 1.3em;
      .title-detail{
        margin-top: 0.4rem;
      }
      p{
        span{
          font-size: 1.5em;
          font-weight: 600;
        }
        & span:nth-child(1){
          margin-right: .2em;
          font-size: 16px;
          font-weight: bold;
        }
      }
    }
  }
  .img-responsive{
    width: 100%;
    cursor: pointer;
  }

@media (max-width: 767.98px) {
  .title-purchase-detail{
    text-align: center;
  }
  .btn-cancelPurchase{
    margin-top: 15px;
    float: none !important;
    display: block;
  }
}
</style>



