<template>
<div>
  <div class="header header-summary">
    <div class="width-general summary">
      <nav class="navbar navbar-toggleable-md navbar-light bg-faded">
        <button class="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo02" aria-controls="navbarTogglerDemo02" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <figure class="inline logo-travel" @click="home()">
          <img src="http://boletoxpress.com/images/logo.png" alt="" width="90px">
        </figure>
        <bx-header-basic></bx-header-basic>
      </nav>
    </div>
    </div>
    <router-view></router-view>
    <bx-footer></bx-footer>
  </div>
</template>

<script>
import bxHeaderBasic from './headerBasic'
import bxFooter from './footer'

export default {
  components: {
    bxHeaderBasic,
    bxFooter
  },
  methods: {
    home: function () {
      this.$router.push({
        name: 'home'
      })
    }
  }
}
</script>
<style>
.logo-travel{
    margin: 1em 0;
}
.logo-travel:hover{
    cursor: pointer;
}
</style>



