/* ============
 * Actions for the cart module
 * ============
 *
 * The actions that are available on the
 * cart module.
 */

import * as types from './mutation-types'

export const createShoppingCart = ({ commit }, payload) => {
  commit(types.CREATE_SHOPPING_CART, payload)
}
export const restartState = ({ commit }) => {
  commit(types.RESTART_STATE)
}

export default {
  createShoppingCart,
  restartState
}



