 <template>
  <div class="inline"
    :class="[block.typeCode === 'EMPTY_SPACE' ? 'not-seat': 'seat', {'pointer': block.number}]"
    v-show="block"
    @click="selectSeat()">
    <template v-if="block.number" >
        <img :src="block.image" >
        <label v-if="block.typeCode !== 'SELECTED_SEAT' && block.typeCode !== 'SOLD_SEAT' && block.typeCode !== 'USED_SEAT'" class="pointer" v-tooltip="{
               content: block.price.name + '<br>' + block.price.currency + ' ' + block.price.price,
          placement: 'bottom-center',
          classes: ['info'],
          targetClasses: ['it-has-a-tooltip'],
          offset: 5
        }">{{ block.number | addLeftZero }}</label>
    </template>
    <template v-else-if="block.typeCode === 'TV'">
      <div class="tv">
        <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 612 612" xml:space="preserve" class="icon-led"><g><path d="M566.085,61.883H45.915C20.597,61.883,0,82.48,0,107.798v337.096c0,25.316,20.597,45.913,45.915,45.913h114.199l-22.25,17.113c-7.988,6.144-11.178,16.695-7.934,26.237c3.245,9.54,12.205,15.958,22.285,15.958h307.57c10.078,0,19.04-6.418,22.285-15.958c3.245-9.542,0.056-20.093-7.934-26.237l-22.25-17.115h114.197c25.318,0,45.915-20.597,45.915-45.913V107.798C612,82.48,591.403,61.883,566.085,61.883z M549.23,428.038H62.77V124.652h486.462v303.387H549.23z"></path></g></svg>
      </div>
    </template>
    <template v-if="block.typeCode === 'USED_SEAT'">
      <img :src="block.image" >
    </template>
    <template v-if="block.typeCode === 'STAIRS'">
      <img src="/static/images/stairs.png">
    </template>
    <template v-if="block.typeCode === 'BATH'">
      <img src="/static/images/toilet.png">
    </template>
    <template v-if="block.typeCode === 'DRIVER_SEAT'">
      <img src="/static/images/driver.png">
    </template>
    <template v-if="block.typeCode === 'CABIN'">
      <img src="/static/images/cabin.png">
    </template>
  </div>
 </template>
<script>
export default {
  props: {
    block: {
      type: Object,
      required: true
    }
  },
  methods: {
    selectSeat () {
      if (this.block.number && this.block.seatId != null) {
        this.$emit('selectSeat', this.block)
      }
    }
  }
}
</script>



