/* ============
 * Actions for the travels module
 * ============
 *
 * The actions that are available on the
 * travels module.
 */

import * as types from './mutation-types'

export const setListTravel = ({ commit }, payload) => {
  commit(types.SET_LIST_TRAVEL, payload)
}
export const setListOptions = ({ commit }, payload) => {
  commit(types.SET_LIST_OPTIONS, payload)
}
export const setSelectTravel = ({ commit }, payload) => {
  commit(types.SET_SELECT_TRAVEL, payload)
}
export const restartState = ({ commit }) => {
  commit(types.RESTART_STATE)
}

export default {
  setListTravel,
  setListOptions,
  setSelectTravel,
  restartState
}



