/* ============
 * Actions for the auth module
 * ============
 *
 * The actions that are available on the
 * auth module.
 */

import * as types from './mutation-types'

export const check = ({ commit }) => {
  commit(types.CHECK)
}

export const login = ({ commit }, payload) => {
  commit(types.LOGIN, payload)
}
export const culqi = ({ commit }, payload) => {
  commit(types.CULQI, payload)
}
export const loginSeller = ({ commit }, payload) => {
  commit(types.LOGIN_SELLER, payload)
}
export const logout = ({ commit }) => {
  commit(types.LOGOUT)
}

export const restartState = ({ commit }) => {
  commit(types.RESTART_STATE)
}

export const setLang = ({ commit }, payLoad) => {
  commit(types.SET_LANG, payLoad)
}

export default {
  check,
  login,
  culqi,
  loginSeller,
  logout,
  restartState,
  setLang
}



