import Vue from 'vue'
import { Validator } from 'vee-validate'
import moment from 'moment'

export default function setTranslate (i18n, lang) {
  let savedLangs = window.localStorage.getItem('savedLangs')
  let hasCurrent = false
  if (savedLangs) {
    savedLangs = JSON.parse(savedLangs)
    savedLangs.forEach((obj) => {
      if (obj.lang === lang) {
        hasCurrent = true
      }
    })
  } else {
    savedLangs = []
  }
  if (!savedLangs || !hasCurrent) {
    Vue.$http({
      method: 'post',
      data: {lang: lang},
      url: process.env.API_DOMAIN + 'params/lang'
    }).then((response) => {
      let langObj = { lang: lang, values: response.data.data }
      savedLangs.push(langObj)
      window.localStorage.setItem('savedLangs', JSON.stringify(savedLangs))
      i18n.setLocaleMessage(lang, response.data.data)
      i18n.locale = lang
      var localeMessages = require('vee-validate/dist/locale/' + lang)
      var localeAttributes = require('./validator/' + lang)
      var objDictionary = {}
      objDictionary[lang] = { messages: localeMessages.messages, attributes: localeAttributes.default }
      Validator.localize(objDictionary)
      Validator.localize(lang)
      moment.locale(lang)
    })
  } else {
    savedLangs.forEach((obj) => {
      i18n.setLocaleMessage(obj.lang, obj.values)
    })
    i18n.locale = lang
    var localeMessages = require('vee-validate/dist/locale/' + lang)
    var localeAttributes = require('./validator/' + lang)
    var objDictionary = {}
    objDictionary[lang] = { messages: localeMessages.messages, attributes: localeAttributes.default }
    Validator.localize(objDictionary)
    Validator.localize(lang)
    moment.locale(lang)
  }
}



