<template>
  <div class="">
    <div class="row travel-price">
      <div class="col" v-for="(price, index) in fareDetail" :key="'trpr' + index">
        <p>{{price.name}}</p>
        <div v-if="price.price.length">
          <!--Crear componente de grupo de asientos dentro de c/tarifa :farePrice = 'price.price'-->
          <label class="rad">
            <input type="radio" class="fare" :class="getClass()" :id="price.fareKey" @click="changeSelected(getClass(), price, price.fareKey)">
            <i aria-hidden="true" style="none"></i>
            <div class="col" v-for="(amount, index) in price.price" :key="'prf' + index">
               <label for="one" class="set-color">{{currency}}<strong>{{amount.price}}</strong> - <span>{{amount.name}}</span></label>
            </div>
          </label>
        </div>
        <div v-else>
          <label class="rad">
            <input type="radio" class="fare" :class="getClass()" :id="price.fareKey" @click="changeSelected(getClass(), price, price.fareKey)">
            <i aria-hidden="true" style="none"></i>
            <label for="one" class="set-color">{{currency}}<strong>{{price.price}}</strong></label>
          </label>
        </div>
      </div>
    </div>
    <div class="row travel-btn-seat">
      <div class="col-sm-12">
          <button type="button" name="button" class="btn-seat" :class="getClassButton" v-show="checkedFare==true" @click="showSeats()">{{ getTextButton() }}<i :class="getClassIconButton" aria-hidden="true"></i></button>
      </div>
    </div>
    <div class="free-seats"><span>{{free + ' '}}</span><span>{{$t('LBL_SEATS_AVAILABLE') }}</span></div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  props: {
    fareDetail: {
      type: Array,
      required: true
    },
    travelKey: {
      type: String
    },
    showSeat: {
      type: Boolean
    },
    currency: {
      type: String
    },
    free: {
      type: Number
    }
  },
  data () {
    return {
      selected: '',
      checkedFare: false
    }
  },
  components: {
  },
  mounted: function () {
    var keyFare = (this.selectTravel.TYPE_WAY === 2) ? this.selectTravel.KEY_FARE_RW : this.selectTravel.KEY_FARE
    if (keyFare !== null) {
      var objFare = this.fareDetail.find(obj => obj.fareKey === keyFare)
      if (typeof objFare !== 'undefined') {
        if (document.getElementById(keyFare) != null) {
          document.getElementById(keyFare).click()
          document.querySelector(`.${keyFare}`).click()
        }
      }
    }
  },
  computed: {
    ...mapGetters({
      selectTravel: 'travels/selectTravel'
    }),
    getClassButton () {
      var classButton = ''
      this.fareDetail.forEach((obj) => {
        classButton = classButton + obj.fareKey + ' '
      })
      if (!this.showSeat) {
        return classButton + 'activeClass'
      } else {
        return classButton
      }
    },
    getClassIconButton () {
      var classIconButton = ''
      this.fareDetail.forEach((obj) => {
        classIconButton = classIconButton + obj.fareKey + ' '
      })
      if (!this.showSeat) {
        return 'fa fa-angle-double-down'
      } else {
        return classIconButton
      }
    }
  },
  methods: {
    getTextButton () {
      return this.showSeat ? this.$t('LBL_VIEW_HIDE_SEATS') : this.$t('LBL_VIEW_SEATS')
    },
    getClass () {
      return `${this.travelKey}`
    },
    getId (price) {
      return `${this.travelKey}${price.price}`
    },
    showSeats () {
      if (this.showSeat) {
        // ocultar bus
        let isTravelAddToCart = (this.selectTravel.TYPE_WAY === 2) ? this.selectTravel.TRAVEL_ADD_CART_RW : this.selectTravel.TRAVEL_ADD_CART_OW
        if (isTravelAddToCart) {
          this.$modal.hide('dialog')
          this.$modal.show('dialog', {
            title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
            text: this.$t('MSG_WARNING_HIDE_SEATS'),
            buttons: [
              {
                title: this.$t('LBL_ACCEPT'),
                handler: () => {
                  this.$modal.hide('dialog')
                  this.$emit('showSeats', !this.showSeat)
                }
              },
              {
                title: this.$t('LBL_CANCEL'),
                handler: () => {
                  this.$modal.hide('dialog')
                }
              }
            ]
          })
        } else {
          this.$emit('showSeats', !this.showSeat)
        }
      } else {
        this.$emit('showSeats', !this.showSeat)
      }
    },
    changeSelected (cssClass, fare, id) {
      let nodes = document.querySelectorAll(`.fare.${cssClass}:not(#${id})`) // Selecciona tarifa  por viaje
      Array.prototype.forEach.call(nodes, (node) => {
        node.checked = false
      })
      this.$set(this, 'selected', fare)
      this.$emit('selectedFare', fare)
      this.checkedFare = true
      if (!this.showSeat) {
        this.showSeat = false
        this.showSeats()
      }
    },
    selectedFare (fare) {
      this.$emit('selectedFare', fare)
    }
  }
}
</script>



