<template>
  <div>
    <div class="date-dropdown-container" v-if="format === 'PE'">
      <v-select class="date-dropdown-day" v-model="selectedDay" :options="days" :searchable="false" :maxHeight="'150px'"></v-select>
      <v-select class="date-dropdown-month" v-model="selectedMonth" :options="months" :searchable="false" :maxHeight="'150px'"></v-select>
      <v-select class="date-dropdown-year" v-model="selectedYear" :options="years" :searchable="false" :maxHeight="'150px'"></v-select>
    </div>
    <div class="date-dropdown-container" v-else>
      <v-select class="date-dropdown-month" v-model="selectedMonth" :options="months" :searchable="false" :maxHeight="'150px'"></v-select>
      <v-select class="date-dropdown-day" v-model="selectedDay" :options="days" :searchable="false" :maxHeight="'150px'"></v-select>
      <v-select class="date-dropdown-year" v-model="selectedYear" :options="years" :searchable="false" :maxHeight="'150px'"></v-select>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    default: {
      type: String,
      required: false
    },
    min: {
      type: String,
      required: false
    },
    max: {
      type: String,
      required: false
    },
    format: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      days: [],
      selectedDay: '',
      selectedMonth: '',
      selectedYear: '',
      monthsNames: {
        0: this.$t('LBL_JANUARY'),
        1: this.$t('LBL_FEBRUARY'),
        2: this.$t('LBL_MARCH'),
        3: this.$t('LBL_APRIL'),
        4: this.$t('LBL_MAY'),
        5: this.$t('LBL_JUNE'),
        6: this.$t('LBL_JULY'),
        7: this.$t('LBL_AUGUST'),
        8: this.$t('LBL_SEPTEMBER'),
        9: this.$t('LBL_OCTOBER'),
        10: this.$t('LBL_NOVEMBER'),
        11: this.$t('LBL_DECEMBER')
      }
    }
  },
  watch: {
    default: function (val) {
      if (val !== null && val !== this.calculatedDate) {
        let aDate = val.split('-')
        let x = new Date(aDate[0], aDate[1] - 1, aDate[2])
        this.selectedDay = x.getDate()
        this.selectedYear = x.getFullYear()
        this.selectedMonth = {
          label: this.monthsNames[x.getMonth()],
          value: x.getMonth()
        }
      }
    },
    selectedMonth: function (val) {
      this.updateDays()
    },
    selectedYear: function (val) {
      this.updateDays()
    }
  },
  computed: {
    minDate () {
      if (this.min) {
        return new Date(this.min)
      }
    },
    maxDate () {
      if (this.max) {
        return new Date(this.max)
      }
    },
    specifiedDate () {
      return this.default === '' ? new Date(this.max) : new Date(this.default)
    },
    calculatedDate () {
      if (
        this.selectedMonth === '' ||
        this.selectedDay === '' ||
        this.selectedDay === null
      ) {
        return null
      } else {
        const day =
          this.selectedDay >= 10 ? this.selectedDay : `0${this.selectedDay}`
        let valueMonth = this.selectedMonth.value
        const month =
          valueMonth + 1 >= 10 ? valueMonth + 1 : `0${valueMonth + 1}`
        const year = this.selectedYear
        // Y-m-d
        return day === '0' || month === '0' || year === ''
          ? null
          : `${year}-${month}-${day}`
      }
    },
    months () {
      let months = []
      for (let i = 0; i < 12; i++) {
        months.push(this.monthsNames[i])
      }
      return months.map((month, index) => {
        return { label: month, value: index }
      })
    },
    years () {
      let firstYear
      if (this.min) {
        firstYear = this.minDate.getFullYear() + 1
      } else if (this.default) {
        firstYear = this.specifiedDate.getFullYear()
      } else {
        firstYear = new Date().getFullYear()
      }
      let through = this.max ? this.maxDate.getFullYear() + 1 - firstYear : 101
      let years = []
      // for (let i = firstYear, len = firstYear + through; i < len; i++) {
      //   years.push(i)
      // }
      for (let i = firstYear + through - 1; i >= firstYear; i--) {
        years.push(i)
      }
      return years.map(year => {
        return year.toString()
      })
    }
  },
  methods: {
    getDays () {
      let days = []
      let valueMonth = this.selectedMonth === '' ? 0 : this.selectedMonth.value
      let daysNumberOfMonth = new Date(
        this.selectedYear,
        valueMonth + 1,
        0
      ).getDate()
      let valid = false
      for (let i = 1; i < daysNumberOfMonth + 1; i++) {
        days.push(i)
        if (!valid && this.selectedDay !== '') {
          // entra siempre que no se encuentre coincidencia entre del dia con la lista de dias
          // pone tue a la variablea cuando el dia se encuentra en la lista de dias
          valid = this.selectedDay.toString() === i.toString()
        }
      }
      // valid es false cuando el dia no coincide con la lista de dias
      if (valid === false && this.selectedDay !== '') {
        this.selectedDay = 1
      }
      // this.selectedDay = !valid ? '' : this.selectedDay
      return days.map((day) => {
        return day.toString()
      })
    },
    updateDays () {
      this.days = this.getDays()
    },
    sendDate () {
      const date = this.calculatedDate
      if (date !== null) {
        this.$emit('input', date)
      }
    },
    setDate () {
      this.updateDays()
      if (this.default !== '') {
        let aDate = this.default.split('-')
        let date = new Date(aDate[0], aDate[1] - 1, aDate[2])
        this.selectedDay = date.getDate()
        this.selectedYear = date.getFullYear()
        this.selectedMonth = {
          label: this.monthsNames[date.getMonth()],
          value: date.getMonth()
        }
      } else {
        this.selectedDay = ''
        this.selectedMonth = ''
        this.selectedYear = ''
      }
      this.sendDate()
    },
    clearDate () {
      this.selectedDay = ''
      this.selectedMonth = ''
      this.selectedYear = ''
    }
  },
  updated () {
    this.sendDate()
  },
  mounted: function () {
    this.setDate()
    this.$eventBus.$on('clearDateDropdown', $event => {
      this.clearDate()
    })
  }
}
</script>

<style lang="scss">
.date-dropdown-container {
  display: flex;
  select {
    border-bottom: 0px;
  }
  .dropdown-toggle {
    border: none !important;
    input {
      position: absolute !important;
      left: 0;
    }
  }
}
.v-select .dropdown-toggle {
  .clear {
    display: none !important;
  }
}
.v-select.date-dropdown-day {
  .dropdown-toggle {
    width: 50px !important;
    height: 35.26px;
  }
}
.v-select.date-dropdown-month {
  .dropdown-toggle {
    width: 100px !important;
    height: 35.26px;
  }
}
.v-select.date-dropdown-year {
  .dropdown-toggle {
    width: 65px !important;
    height: 35.26px;
  }
}
.v-select.unsearchable input[type=search] {
  opacity: 1 !important;
}
</style>



