<template lang="html">
  <div class="container-delete text-center">
    <h2> {{ $t('LBL_EMPTY_SHOPPING_CART') }} </h2>
    <p> {{ $t('LBL_EMPTY_SHOPPING_CART_DESC') }}</p>
    <button type="button" class="btn btn-primary" @click="addNewTravel()">COMPRAR</button>
  </div>
</template>

<script>
export default {
  methods: {
    addNewTravel: function () {
      this.$router.push({
        name: 'services'
      })
    }
  }
}
</script>



