<template lang="html">
  <div class="container-payment-method">
    <div class="header-down"></div>
    <div v-if="this.seller">
      <div class="row form-group">
        <div class="col-12">
          <label>{{ $t('LBL_PAYMENT_METHODS') }}:</label>
        </div>
        <div class="col-2">
          <label class="rad">
            <input id="Cash" type="radio" value="Cash" name="methodpayment" v-model="personInformation.methodPayment">
            <i aria-hidden="true"></i>
            <label for="Cash">{{ $t('LBL_CASH') }}</label>
          </label>
        </div>
        <div class="col-3">
          <label class="rad">
            <input id="CreditCard" type="radio" value="CreditCard" name="methodpayment" v-model="personInformation.methodPayment">
            <i aria-hidden="true"></i>
            <label for="CreditCard">{{ $t('LBL_CREDIT_CART') }}</label>
          </label>
        </div>
      </div>
    </div>
    <div class="row resetRow">
      <div class=" col-md-12">
        <form id="payment-Complete"  @submit.prevent="paymetTickets">
          <div v-if="this.personInformation.methodPayment === 'CreditCard' || !this.seller" class="general-payment paymentMethodOne green-line">
            <div class="content-icon text-option-hide">
              <i @click="methodPayment(1)" class="fa fa-credit-card"></i>
            </div>
            <div class="content-head-payment text-option-hide">
              <h6>{{ $t('LBL_CREDIT_CART') }}</h6>
              <p>{{ $t('LBL_CREDIT_CART_MESSAGE') }}.</p>
            </div>
            <div class="content-head-payment">
              <figure>
                <img src="/static/images/logo-visa.svg" alt="">
                <img src="/static/images/logo-mastercard.svg" alt="">
                <img src="/static/images/logo-americanexpress.svg" alt="">
              </figure>
            </div>
              <span class="line"></span>
              <section class="row content-form">
                  <div class="form-group alert-message col-12 col-md-4">
                    <label for="firstName">{{ $t('PHD_FIRST_NAME') }}:</label>
                    <input autocomplete="off" type="text" v-model="personInformation.firstName" class="form-control" id="firstName" v-validate data-vv-rules="required|alpha_spaces|max:40" name="name">
                    <small v-if="errors.has('name')">{{ errors.first('name') }}</small>
                  </div>
                  <div class="form-group alert-message col-12 col-md-4">
                    <label for="lastName">{{ $t('PHD_LAST_NAME') }}:</label>
                    <input autocomplete="off" type="text" v-model="personInformation.lastName" class="form-control" id="lastName" v-validate data-vv-rules="required|alpha_spaces|max:40" name="lastname">
                    <small v-if="errors.has('lastname')">{{ errors.first('lastname') }}</small>
                  </div>
                  <div class="form-group alert-message col-12 col-md-4">
                    <label for="email">{{ $t('PHD_EMAIL') }}:</label>
                    <input autocomplete="off" type="email" v-model="personInformation.email" class="form-control"  v-validate data-vv-rules="required|email|max:45" name="email" >
                    <small v-if="errors.has('email')">{{ errors.first('email') }}</small>
                  </div>
                  <div class="form-group col-12 col-md-2">
                    <label for="cardType">{{ $t('LBL_TYPE_CARD') }}:</label>
                    <select class="form-control" v-model="personInformation.cardType" name="card" v-validate data-vv-rules="required">
                      <option disabled value="">{{ $t('LBL_SELECT_CARD_TYPE') }}</option>
                      <option value="visa">Visa</option>
                      <option value="master">MasterCard</option>
                      <option value="discover">Discover</option>
                      <option value="amex">American Express</option>
                    </select>
                    <small v-if="errors.has('card')">{{ errors.first('card') }}</small>
                  </div>
                  <div class="form-group alert-message col-12 col-md-3">
                    <label for="card">{{ $t('LBL_CARD_NUMBER') }}:</label>
                    <input autocomplete="off" type="text" v-model="card" class="form-control" id="card" v-validate data-vv-rules="required" name="numbercard" v-mask="'####-####-####-####'" placeholder="XXXX-XXXX-XXXX-XXXX">
                    <small v-if="errors.has('numbercard')">{{ errors.first('numbercard') }}</small>
                  </div>
                  <div class="form-group col-12 col-md-3 alert-message">
                      <label for="expirationDate">{{ $t('LBL_EXPIRATION_DATE') }}:</label>
                      <input autocomplete="off" type="text" v-model="expirationDate" class="form-control" id="expirationDate" placeholder="MM/YYYY" v-validate data-vv-rules="required|date_format:MM/YYYY"  v-mask="'##/####'" name="date" >
                      <small v-if="errors.has('date')">{{ errors.first('date') }}</small>
                  </div>
                  <div class="form-group col-12 col-md-2 alert-message">
                      <label for="cardCVV">{{ $t('LBL_CVV_CODE') }}:</label>
                      <input autocomplete="off" type="text" v-model="personInformation.cardCVV" class="form-control" id="cardCVV" placeholder="CVV" v-validate data-vv-rules="required|numeric|max:6" name="CVV">
                      <small v-if="errors.has('CVV')">{{ errors.first('CVV') }}</small>
                    </div>
                  <div class="form-group alert-message col-12 col-md-2" v-if="needZipCode()">
                    <label for="zipcode">{{ $t('LBL_ZIP_CODE') }}:</label>
                    <input autocomplete="off" type="text" v-model="personInformation.zipcode" class="form-control" id="emal" v-validate data-vv-rules="required|numeric|max:10" name="zipcode">
                    <small v-if="errors.has('zipcode')">{{ errors.first('zipcode') }}</small>
                  </div>
                  <div v-show="false">
                     <input type="text" name="email" id="card[email]" data-culqi="card[email]" v-model="personInformation.email"/>
                     <input type="text" name="email" id="card[number]" data-culqi="card[number]" v-model="cardNumber"/>
                     <input type="text" name="email" id="card[cvv]" data-culqi="card[cvv]" v-model="personInformation.cardCVV"/>
                     <input type="text" name="email" id="card[exp_month]" data-culqi="card[exp_month]" v-model="cardExpMonth"/>
                     <input type="text" name="email" id="card[exp_year]" data-culqi="card[exp_year]" v-model="cardExpYear" />
                  </div>
              </section>
          </div>
          <div v-if="this.personInformation.methodPayment === 'Cash'" class="general-payment paymentMethodOne green-line">
            <div class="content-icon text-option-hide">
              <i @click="methodPayment(1)" class="fa fa-money"></i>
            </div>
            <div class="content-head-payment text-option-hide">
              <h6>{{ $t('LBL_CASH') }}</h6>
              <p>{{ $t('LBL_CASH_MESSAGE') }}.</p>
            </div>
            <div class="content-head-payment">
            </div>
              <span class="line"></span>
              <section class="row content-form">
                  <div class="form-group alert-message col-12 col-md-4">
                    <label for="firstName">{{ $t('PHD_FIRST_NAME') }}:</label>
                    <input autocomplete="off" type="text" v-model="personInformation.firstName" class="form-control" id="firstName" v-validate data-vv-rules="required|alpha_spaces|max:40" name="name">
                    <small v-if="errors.has('name')">{{ errors.first('name') }}</small>
                  </div>
                  <div class="form-group alert-message col-12 col-md-4">
                    <label for="lastName">{{ $t('PHD_LAST_NAME') }}:</label>
                    <input autocomplete="off" type="text" v-model="personInformation.lastName" class="form-control" id="lastName" v-validate data-vv-rules="required|alpha_spaces|max:40" name="lastname">
                    <small v-if="errors.has('lastname')">{{ errors.first('lastname') }}</small>
                  </div>
                  <div class="form-group alert-message col-12 col-md-4">
                    <label for="email">{{ $t('PHD_EMAIL') }}:</label>
                    <input autocomplete="off" type="email" v-model="personInformation.email" class="form-control"  v-validate data-vv-rules="required|email|max:45" name="email" >
                    <small v-if="errors.has('email')">{{ errors.first('email') }}</small>
                  </div>
              </section>
          </div>
          <div class="container-button-pay">
            <section>
              <div class="row resetRow container-payment-buttom">
                <div class="col-12">
                  <button class="col-12 col-md-2 mb-3 mb-md-0 btn btn-primary newPurchase" type="button" name="button" v-on:click="goSummary()">{{ $t('LBL_BACK') }}</button>
                  <button class="col-12 col-md-3 mb-3 mb-md-0 btn btn-default newPurchase" type="button" name="button" v-on:click="removeLocalStorage()">{{ $t('LBL_CANCEL_PURCHASE') }}</button>
                  <button class="col-12 col-md-2 btn btn-primary newPurchase right" type="submit" name="button">{{ $t('LBL_PAY') }} {{' '}} {{ total }}</button>
                </div>
              </div>
            </section>
          </div>
        </form>
        <br />
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import { mapGetters, mapActions } from 'vuex'
import {mask} from 'vue-the-mask'
export default {
  data: function () {
    return {
      expirationDate: '',
      card: '',
      personInformation: {
        methodPayment: null,
        card: '',
        cardExpYear: '',
        cardExpMonth: '',
        cardType: 'Visa',
        cardCVV: '',
        firstName: '',
        lastName: '',
        zipcode: '',
        email: '',
        token: ''
      }
    }
  },
  mounted: function () {
    this.$eventBus.$on('errorClear', () => {
      this.errors.clear()
    })
    if (this.paymentPassenger.KEY != null) {
      this.personInformation.firstName = this.paymentPassenger.PASSENGER.firstName
      this.personInformation.lastName = this.paymentPassenger.PASSENGER.lastName
      this.personInformation.email = this.paymentPassenger.PASSENGER.email
    }
  },
  beforeRouteLeave (to, from, next) {
    // se llama cuando iras a otra ruta
    if (this.shoppingCartHasTrips && to.name === 'home') {
      this.$modal.hide('dialog')
      this.$modal.show('dialog', {
        title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
        text: this.$t('MSG_VALIDATION_LEAVE_PAGE'),
        buttons: [
          {
            title: this.$t('LBL_ACCEPT'),
            handler: () => {
              this.$modal.hide('dialog')
              this.setLoader({loader: true})
              this.$http.put(process.env.URL_END_SESSION).then(response => {
                console.log(response)
              }).catch(function (error) {
                console.log(error)
              }).then(() => {
                this.setLoader({loader: false})
                next()
              })
            }
          },
          {
            title: this.$t('LBL_CANCEL'),
            handler: () => {
              this.$modal.hide('dialog')
              next(false)
            }
          }
        ]
      })
    } else {
      next()
    }
  },
  computed: {
    ...mapGetters({
      shoppingCartHasTrips: 'cart/shoppingCartHasTrips',
      paymentPassenger: 'payment/passenger',
      items: 'cart/items',
      total: 'cart/total',
      seller: 'auth/seller',
      culqi: 'auth/culqi'
    }),
    cardExpMonth: function () {
      var vals = this.expirationDate.split('/')
      var month = vals[0]
      return month
    },
    cardExpYear: function () {
      var vals = this.expirationDate.split('/')
      var year = vals[1]
      return year
    },
    cardNumber: function () {
      return this.card.replace(/-/g, '')
    },
    isPeru: function () {
      var response = false
      for (var key in this.items) {
        response = this.items[key][0].countryCode === 'PE'
        break
      }
      return response
    }
  },
  methods: {
    ...mapActions({
      cartRestartState: 'cart/restartState',
      searchRestartState: 'search/restartState',
      travelsRestartState: 'travels/restartState',
      autthRestartState: 'auth/restartState',
      setLoader: 'loader/setLoader'
    }),
    removeLocalStorage () {
      this.$modal.hide('dialog')
      this.$modal.show('dialog', {
        title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
        text: this.$t('MSG_DELETE_SHOPPING_CART'),
        buttons: [
          {
            title: this.$t('LBL_YES'),
            handler: () => {
              this.$modal.hide('dialog')
              this.setLoader({loader: true})
              this.$http.put(process.env.URL_END_SESSION).then(response => {
                console.log(response)
              }).catch(function (error) {
                console.log(error)
              }).then(() => {
                this.setLoader({loader: false})
                this.cartRestartState()
                this.$router.push({
                  name: 'home'
                })
              })
            }
          },
          {
            title: this.$t('LBL_NO'),
            handler: () => {
              this.$modal.hide('dialog')
            }
          }
        ]
      })
    },
    restartState () {
      this.cartRestartState()
      this.searchRestartState()
      this.travelsRestartState()
      this.autthRestartState()
    },
    newCompra () {
      sessionStorage.setItem('id_token', '')
      this.restartState()
      this.$router.push({
        name: 'home'
      })
    },
    goSummary: function () {
      this.$router.push({
        name: 'summary'
      })
    },
    createCulqi: function () {
      Culqi.createToken()
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve(Culqi.token)
        }, 3000)
      })
    },
    paymetTickets: function () {
      this.$validator.validateAll().then(result => {
        if (result) {
          this.setLoader({loader: true, message: this.$t('MSG_PAYMENT_IS_BEING_PROCESSED')})
          this.personInformation.cardExpYear = this.cardExpYear
          this.personInformation.cardExpMonth = this.cardExpMonth
          this.personInformation.card = this.cardNumber
          if (this.isPeru && this.personInformation.methodPayment === 'CreditCard') {
            new Promise((resolve, reject) => {
              Culqi.createToken()
              setTimeout(() => {
                resolve(Culqi.token)
              }, 3000)
            }).finally(() => {
              console.log(Culqi)
              if (Culqi.token == null) {
                this.$modal.hide('dialog')
                this.$modal.show('dialog', {
                  title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
                  text: Culqi.error.user_message,
                  buttons: [{
                    title: this.$t('LBL_ACCEPT'),
                    handler: () => { this.$modal.hide('dialog') }
                  }]
                })
                this.setLoader({loader: false})
              } else {
                this.personInformation.token = Culqi.token.id
                this.processPayment()
              }
            })
          } else {
            this.processPayment()
          }
        } else {
          this.$modal.hide('dialog')
          this.$modal.show('dialog', {
            title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
            text: this.$t('MSG_INCOMPLETE_FORM'),
            buttons: [{
              title: this.$t('LBL_ACCEPT'),
              handler: () => { this.$modal.hide('dialog') }
            }]
          })
        }
      })
    },
    processPayment: function () {
      var vue = this
      this.$http.post(process.env.URL_COMPLETE_SHOPPINGCART, this.personInformation).then(response => {
        this.$router.push({
          name: 'success',
          params: { data: response.data.data, fullName: this.personInformation.firstName + ' ' + this.personInformation.lastName }
        })
        this.restartState()
      }).catch(function (error) {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      }).then(() => {
        this.setLoader({loader: false})
      })
    },
    needZipCode: function () {
      return !this.isPeru
    }
  },
  created () {
    this.setLoader({loader: false})
    Culqi.publicKey = this.culqi
    Culqi.init()
  },
  directives: { mask }
}
</script>
<style lang="scss">
