  .resetRow{
    margin-right: 0px !important;
    margin-left: 0px !important;
    button{
      cursor: pointer;
    }
  }
  .container-payment-method{
    max-width: 1100px;
    margin: 0 auto;
    // margin-top: 50px;
    padding-top: 10em;
    padding-bottom: 15em;
    .radio-buttom-container{
      @media screen and (min-width: 780px) {
        display: none;
      }
      padding: 0 2em;
      .btn-primary{
        &:not(.active){
          background-color: map-get($colors, white);
          color: map-get($colors, primary-01);
          cursor: pointer;
        }
      }
    }
    >div{
      &:first-child{
        >div{
          >div{
            &:first-child{
              label{
                margin-bottom: 1em;
                margin-left: .6em;
                font-size: 1.5em;
                color: map-get($colors, primary-01);
                cursor: pointer;
              }
            }
            &:nth-child(2){
              padding-left: 3em;
            }
            label{
                font-size: 18px;
                padding-top: 3px;
                color: gray;
                cursor: pointer;
            }
          }
        }
      }
    }
    div{
      .general-payment{
        border: 1px solid map-get($colors, gray-13);
        border-radius: 4px;
        padding: 1em;
        div{
          h6{
            color: map-get($colors, gray-12);
            font-family: "Roboto-Medium"
          }
          p{
            margin: 0px;
            font-size: 13px;
            line-height: 15px;
            color: map-get($colors, gray-13);
          }
          figure{
            margin: 0;
            text-align: right;
          }
        }
        .text-option-hide{
          @media screen and (max-width: 780px) {
                display: none !important;
          }
        }
        .content-icon{
          width: 10%;
          display: inline-block;

          i{
            font-size: 2em;
            color: map-get($colors, gray-14);

          }
          .fa-check-circle{
            color: map-get($colors, secondary-07);
          }
        }
        .content-head-payment{
          display: inline-block;
          vertical-align: middle;
          width: 44%;
          @media screen and (max-width: 780px) {
            width: 100%;
          }
          figure{
            @media screen and (max-width: 780px) {
              text-align: center;
            }
          }
        }
        .line{
            border-bottom:1px solid map-get($colors, gray-05);
            margin: 0 auto;
            display: block;
            margin: 0.5em 0;
          }
        .content-form{
            & >div:nth-child(1), >div:nth-child(2){
              input{
                text-transform: capitalize;
              }
            }
          }
        section{
          div{
            label{
              color: map-get($colors, gray-12);
            }
          }
        }
      }
      .green-line{
        border:1px solid map-get($colors, secondary-07);
      }
    }
  }
  .container-button-pay{
    margin-top: 0em;
    // border-top: 1px solid map-get($colors, gray-08);
    padding-top: 1em;
  }
  .container-payment-buttom{
    div{
      font-size: 12px;
      label{
        padding-top: 17px;
        div{
          top: 0.8em
        }
      }
    }
    button{
      cursor: pointer;
    }
  }
  .title-purchase-detail{
    color: map-get($colors, primary-01);
    font-family: "Roboto-Medium";
    box-shadow: 0 5px 27px -19px map-get($colors, negro);
    padding: .5em;
  }
  .addNewTrip{
    background: map-get($colors, gray-14);
    color: map-get($colors, white);
    cursor: pointer;
  }
  .cupon-discount{
    margin-top: 1em;
    margin-bottom: 1em;
    input{
      display: inline-block;
      width: 80%;
      padding: 0.4rem 0 0.4rem 0.7rem;
      &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: map-get($colors, gray-14);
      }
      &::-moz-placeholder { /* Firefox 19+ */
        color: map-get($colors, gray-14);
      }
      &:-ms-input-placeholder { /* IE 10+ */
        color: map-get($colors, gray-14);
      }
      &:-moz-placeholder { /* Firefox 18- */
        color: map-get($colors, gray-14);
      }
    }
    button{
      display: inline-block;
      width: 20%;
      border-radius: 0px;
      padding: 0.6rem 0;
      background: map-get($colors, secondary-07);
