  .payment-amount{
    margin-top: 2em;
    div{
      div{
        &:first-child{
          padding-right: 0px;
        }
        &:last-child{
          padding-left: 0px;
        }
        p{
          color: map-get($colors, gray-14);
          font-family: "Roboto-Medium";
          border-bottom: 1px dashed map-get($colors, gray-08);
        }
      }
      .text-right{
        p{
          font-weight: 600;
          color: map-get($colors, gray-12);
        }
      }
    }
    section{
      color: map-get($colors, gray-12);
      font-family: "Roboto-Medium";
      font-size: 1.3em;
      .title-detail{
        margin-top: 0.4rem;
      }
      p{
        span{
          font-size: 1.5em;
          font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
        }
