/* ============
 * Mutations for the auth module
 * ============
 *
 * The mutations that are available on the
 * account module.
 */

import Vue from 'vue'
import {
  CHECK,
  LOGIN,
  CULQI,
  LOGIN_SELLER,
  LOGOUT,
  RESTART_STATE,
  SET_LANG
} from './mutation-types'

export default {
  [CHECK] (state) {
    state.authenticated = !!sessionStorage.getItem('id_token')
    if (state.authenticated) {
      Vue.$http.defaults.headers.common.Authorization = `Bearer ${sessionStorage.getItem('id_token')}`
    }
  },
  [LOGIN] (state, token) {
    sessionStorage.setItem('id_token', token)
    Vue.$http.defaults.headers.common.Authorization = `Bearer ${token}`
    state.authenticated = true
  },
  [CULQI] (state, culqi) {
    state.culqi = culqi
  },
  [LOGIN_SELLER] (state, data) {
    sessionStorage.setItem('id_token', data.token)
    Vue.$http.defaults.headers.common.Authorization = `Bearer ${data.token}`
    state.authenticated = true
    state.seller = true
    state.sellerUser = data.sellerUser
  },
  [LOGOUT] (state) {
    state.authenticated = false
    state.seller = false
    state.culqi = ''
    sessionStorage.removeItem('id_token')
    Vue.$http.defaults.headers.common.Authorization = ''
  },
  [RESTART_STATE] (state) {
    state.authenticated = false
    state.token = sessionStorage.getItem('id_token')
    state.culqi = ''
  },

  [SET_LANG] (state, lang) {
    state.lang = lang
  }
}



