<template>
  <div class="row detail-bus" v-show="bus">
    <div class="col-12">
      <div class="row route-info">
        <div class="col routes" v-if="numberPaths > 1">
          <div class=" step-route" >
            <div class="">{{$t('LBL_PATH')}}</div>
            <div class="">{{ index + 1 }}</div>
          </div>
          <div class="destinations row">
            <div class="col">
              <p>{{ bus.header.departureName }}</p>
              <span>{{ dateFormatDeparture()}}<span>-</span>{{hourFormatDeparture('hh:mm A')}}</span>
            </div>
            <div class="col">
              <p>{{ bus.header.arrivalName }}</p>
              <span>{{ dateFormatArrival()}}<span>-</span>{{hourFormatArrival('hh:mm A')}}</span>
            </div>
          </div>
        </div>
        <div class="col legened">
          <div class="seat-legened">
            <strong class="sky">{{ getSelectedSeats() | addLeftZero }}</strong>
            <span>{{ $t('LBL_SELECTED_SEATS') }}</span>
          </div>
          <div class="seat-legened">
            <strong class="dark-gray">{{ bus.seatsInfo[0].reserveds + bus.seatsInfo[0].solds + bus.seatsInfo[0].selected | addLeftZero }}</strong>
            <span>{{ $t('LBL_OCCUPIED_SEATS') }}</span>
          </div>
          <div class="seat-legened">
            <strong class="gray">{{ getFreeSeats() | addLeftZero }}</strong>
            <span>{{ $t('LBL_AVAILABLE_SEATS') }}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="col-10 content-bus">
      <div class="flats">
          <div
            :class="getSelectedFloor(n) ? 1: 'blocked'"
            class="inline"
            @click="changeActiveFloor(n)"
            v-for="n in bus.header.floors"
            :key="n">
            <!--<img src="/static/images/bus.svg" v-if="n === 1">
            <img src="/static/images/bus-second-floor.svg" v-if="n === 2">-->
            <span>{{ $t('LBL_FLOOR') }} {{n}}</span>
            <i v-if="getSelectedFloor(n)" class="fa fa-caret-right" aria-hidden="true"></i>
          </div>
        </div>
      <div class="picture-bus">
        <div class="block inline border-right-light">
          <!--<img src="/static/img/conductor.0b84fb8.svg">-->
        </div>
        <div class="block block body-bus inline">
          <div v-for="col in bus.header.columns" :key="col">
            <template v-for="(row, index) in bus.header.rows">
              <bx-bus-block :key="index"
                @selectSeat="selectSeat($event)"
                :block="getSeat(row, col)">
              </bx-bus-block>
            </template>
          </div>
        </div>
        <div class="block body-bus inline"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import bxBusBlock from './block'
import moment from 'moment'

export default {
  props: {
    bus: {
      type: Object,
      required: true
    },
    travelItem: {
      type: Object,
      required: true
    },
    selectedFare: {
      type: Object,
      required: true
    },
    numberPaths: {
      required: true
    },
    index: {
      required: true
    },
    FARE_ID: {
      required: true
    },
    KEY_FARE: {
      required: true
    },
    KEY_FARE_RW: {
      required: true
    },
    KEY_GROUP_TRAVEL: {
      required: true
    },
    TRAVEL_ADD_CART_OW: {
      required: true
    },
    TRAVEL_ADD_CART_RW: {
      required: true
    },
    TYPE_WAY: {
      required: true
    }
  },
  data () {
    return {
      selectedFloor: 1,
      seatImages: {
        busy: '/static/images/ocupated.svg',
        free: '/static/images/seat-gray.svg',
        selected: '/static/images/selected.svg',
        bath: '/static/images/toilet.png',
        stair: '/static/images/stairs.png',
        driver_seat: '/static/images/driver.png',
        cabin: '/static/images/cabin.png'
      }
    }
  },
  mounted () {
    this.$eventBus.$on('updateBus', ($event) => {
      $event.aSeat.forEach((obj) => {
        if (this.bus.keyPath === obj.keyPath) {
          this.updateBus({
            keyPath: obj.keyPath,
            seat: obj.seatId
          }, false)
        }
      })
    })
    this.$eventBus.$on('selectOrUnSelectSeat', ($event) => {
      this.selectOrUnSelectSeat($event.seat, false)
    })
  },
  computed: {
    ...mapGetters({
      items: 'cart/items',
      selectTravel: 'travels/selectTravel',
      travel: 'search/travel',
      loader: 'loader/loader'
    })
  },
  methods: {
    formatDate (input, country = null) {
      var format = ''
      if (country === 'PE') {
        format = 'DD/MM/YYYY'
      } else {
        format = 'MM-DD-YYYY'
      }
      return moment(input).format(format)
    },
    dateFormatDeparture () {
      var country = this.bus.countryCode
      if ('path' in this.travelItem) {
        return this.formatDate(this.travelItem.path[this.index].travelDepartureDatetime, country)
      } else {
        return this.formatDate(this.travelItem.travelDepartureDatetime, country)
      }
    },
    hourFormatDeparture (format) {
      if ('path' in this.travelItem) {
        return moment(this.travelItem.path[this.index].travelDepartureDatetime).format(format)
      } else {
        return moment(this.travelItem.travelDepartureDatetime).format(format)
      }
    },
    dateFormatArrival () {
      var country = this.bus.countryCode
      if ('path' in this.travelItem) {
        return this.formatDate(this.travelItem.path[this.index].travelArrivalDatetime, country)
      } else {
        return this.formatDate(this.travelItem.travelArrivalDatetime, country)
      }
    },
    hourFormatArrival (format) {
      if ('path' in this.travelItem) {
        return moment(this.travelItem.path[this.index].travelArrivalDatetime).format(format)
      } else {
        return moment(this.travelItem.travelArrivalDatetime).format(format)
      }
    },
    getFreeSeats () {
      return this.bus.seatsInfo[0].free - this.getSelectedSeats() - this.bus.seatsInfo[0].selected
    },
    getBusySeats () {
      return this.bus.seatsInfo[0].reserveds + this.bus.seatsInfo[0].solds + this.bus.seatsInfo[0].selected - this.getSelectedSeats()
    },
    getSeat (row, column) {
      let seat = this.bus.detail.find((seat) => seat.row === row && seat.column === column && seat.floor === this.selectedFloor)
      var objectPrice = { price: this.selectedFare.price, name: this.selectedFare.name, currency: '$' }
      switch (seat.typeCode) {
        case 'FREE_SEAT':
          seat.image = this.seatImages.free
          seat.price = (seat.hasOwnProperty('price') ? seat.price : objectPrice)
          break
        case 'SOLD_SEAT':
          seat.image = this.seatImages.busy
          break
        case 'SELECTED_SEAT':
          seat.image = this.seatImages.busy
          seat.price = (seat.hasOwnProperty('price') ? seat.price : objectPrice)
          break
        case 'SELECTED_SEAT_SESSION':
          seat.image = this.seatImages.selected
          break
        case 'RESERVED_SEAT':
          seat.image = this.seatImages.busy
          break
        case 'STAIRS':
          seat.image = this.seatImages.stair
          break
        case 'DRIVER_SEAT':
          seat.image = this.seatImages.driver_seat
          break
        case 'BATH':
          seat.image = this.seatImages.bath
          break
        case 'USED_SEAT':
          seat.image = this.seatImages.busy
          break
        default:
          seat.image = null
      }

      return seat
    },
    getSelectedFloor (floor) {
      return this.selectedFloor === floor
    },
    changeActiveFloor (floor) {
      this.selectedFloor = floor
    },
    selectSeat (seat) {
      if (this.travelItem.companyCode !== 'plataforma10') {
        this.toggleSeat(this.bus.keyPath, seat)
      } else {
        var vue = this
        console.log(this.travelItem)
        console.log(seat)
        let config = {
          url: process.env.URL_REFERENCE_SALE,
          data: {
            key: this.travelItem.key,
            seat: seat.seatId
          },
          method: 'POST'
        }
        this.setLoader({loader: true})
        this.$http(config).then(response => {
          window.location = 'https://beta-www.plataforma10.com.ar/es/servicios/Taquilla?claveSolicitud=' + this.travelItem.travelCode
        }).catch(function (error) {
          console.log(error)
          if (error.response.data.code === 444) {
            alert('entra')
          } else {
            vue.$modal.hide('dialog')
            vue.$modal.show('dialog', {
              title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
              text: error.response.data.details,
              buttons: [{
                title: vue.$t('LBL_ACCEPT'),
                handler: () => { vue.$modal.hide('dialog') }
              }]
            })
          }
        }).then(() => {
          this.setLoader({loader: false})
        })
      }
    },
    getSelectedSeats () {
      let keyTravel = (this.TYPE_WAY === 2) ? this.selectTravel.KEY_FARE_RW : this.selectTravel.KEY_FARE
      if (this.selectTravel.KEY_GROUP_TRAVEL in this.items) {
        // existe al menos un viaje agregado al carrito
        let currentItem = this.items[this.selectTravel.KEY_GROUP_TRAVEL].find(item => item.key === keyTravel)
        if (currentItem) {
          if (currentItem.isMultiroute) {
            if (currentItem.path) {
              let oPath = currentItem.path.find(obj => obj.keyPath === this.bus.keyPath)
              if (typeof oPath !== 'undefined') {
                if (oPath.seats) {
                  return oPath.seats.length
                }
              }
            }
          } else {
            if (currentItem.seats) {
              return currentItem.seats.length
            }
          }
        }
      }
      return 0
    },
    toggleSeat (keyPath, seat) {
      let keyTravel = this.selectedFare.fareKey
      let currentSeat = null
      if (this.selectTravel.KEY_GROUP_TRAVEL in this.items) {
        // existe al menos un viaje agregado al carrito
        let currentItem = this.items[this.selectTravel.KEY_GROUP_TRAVEL].find(item => item.key === keyTravel)
        if (currentItem) {
          if (currentItem.isMultiroute) {
            if (currentItem.path) {
              let oPath = currentItem.path.find(obj => obj.keyPath === keyPath)
              if (typeof oPath !== 'undefined') {
                if (oPath.seats) {
                  currentSeat = oPath.seats.find(obj => obj.seatId === seat.seatId)
                }
              }
            }
          } else {
            // Ruta Directa
            keyPath = keyTravel
            if (currentItem.seats) {
              currentSeat = currentItem.seats.find(obj => obj.seatId === seat.seatId && obj.keyPath === keyPath)
            }
          }
        }
      }
      let params = {
        key: keyTravel,
        keyPath: keyPath,
        seat: seat
      }
      let hasBeenSelected = !!currentSeat
      if (hasBeenSelected) {
        // desmarcar asiento
        this.selectOrUnSelectSeat(params, false)
      } else {
        // marcar asiento
        let isTravelAddToCart = (this.TYPE_WAY === 2) ? this.selectTravel.TRAVEL_ADD_CART_RW : this.selectTravel.TRAVEL_ADD_CART_OW
        if (isTravelAddToCart) {
          // si el viaje esta agregado al carrito
          this.selectOrUnSelectSeat(params, true)
        } else {
          this.addTravelToShoppingCart(params)
        }
      }
    },
    addTravelToShoppingCart (params) {
      var vue = this
      let data = {
        key: this.selectedFare.fareKey,
        fare: this.selectedFare.fareId
      }
      if (this.TYPE_WAY === 2) {
        Object.assign(data, {owKey: this.selectTravel.KEY_FARE})
      }
      this.setLoader({loader: true})
      this.$http.post(process.env.URL_ADD_SHOPPINGCART, data).then(response => {
        if (this.TYPE_WAY === 0) {
          // Viaje solo de ida
          this.setSelectTravel({KEY: this.selectedFare.fareKey, KEY_FARE: this.selectedFare.fareKey, FARE_ID: this.selectedFare.fareId, KEY_GROUP_TRAVEL: this.selectedFare.fareKey, TRAVEL_ADD_CART_OW: true})
        } else if (this.TYPE_WAY === 1) {
          // Vieje de ida de un viaje redondo
          this.setSelectTravel({KEY_FARE: this.selectedFare.fareKey, FARE_ID: this.selectedFare.fareId, KEY_GROUP_TRAVEL: this.selectedFare.fareKey, TRAVEL_ADD_CART_OW: true})
        } else if (this.TYPE_WAY === 2) {
          // Vieje de vuelta de un viaje redondo
          this.setSelectTravel({KEY_FARE_RW: this.selectedFare.fareKey, FARE_ID: this.selectedFare.fareId, TRAVEL_ADD_CART_RW: true})
        }
        this.selectOrUnSelectSeat(params, true)
      }).catch(function (error) {
        console.log(error.response.data)
        if (error.response.data.code === 90) {
          vue.$modal.hide('dialog')
          vue.$modal.show('dialog', {
            title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
            text: error.response.data.details,
            buttons: [{
              title: vue.$t('LBL_CONTINUE'),
              handler: () => {
                vue.$modal.hide('dialog')
                vue.$eventBus.$emit('showShoppingCart')
              }
            }]
          })
        } else {
          vue.$modal.hide('dialog')
          vue.$modal.show('dialog', {
            title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
            text: error.response.data.details,
            buttons: [{
              title: vue.$t('LBL_ACCEPT'),
              handler: () => { vue.$modal.hide('dialog') }
            }]
          })
        }
      }).then(() => {
        this.setLoader({loader: false})
      })
    },
    selectOrUnSelectSeat (params, isSelectSeat) {
      let key = params.key
      let seatToVuex = {
        key: key,
        keyPath: params.keyPath,
        seat: {
          seatId: params.seat.seatId,
          number: params.seat.number,
          keyPath: params.keyPath,
          indexPath: this.index + 1
        }
      }
      if (!isSelectSeat || this.validateAddSeat({seatToVuex: seatToVuex, KEY_GROUP_TRAVEL: this.KEY_GROUP_TRAVEL})) {
        // si es remover asiento que pase sin importar la validacion
        var vue = this
        let config = {
          url: process.env.URL_SEAT,
          data: {
            key: key,
            keyPath: params.keyPath,
            seat: params.seat.seatId
          }
        }
        this.setLoader({loader: true})
        config.method = isSelectSeat ? 'POST' : 'DELETE'
        this.$http(config).then(response => {
          this.updateBus({
            keyPath: params.keyPath,
            seat: params.seat.seatId
          }, isSelectSeat)
          this.$eventBus.$emit('loadShoppingCart')
        }).catch(function (error) {
          console.log(error)
          if (error.response.data.code === 444) {
            alert('entra')
          } else {
            vue.$modal.hide('dialog')
            vue.$modal.show('dialog', {
              title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
              text: error.response.data.details,
              buttons: [{
                title: vue.$t('LBL_ACCEPT'),
                handler: () => { vue.$modal.hide('dialog') }
              }]
            })
          }
        }).then(() => {
          this.setLoader({loader: false})
        })
      } else {
        this.$modal.hide('dialog')
        this.$modal.show('dialog', {
          title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: this.$t('MSG_VALIDATION_MAX_NUMBER_SELECTED'),
          buttons: [{
            title: this.$t('LBL_ACCEPT'),
            handler: () => { this.$modal.hide('dialog') }
          }]
        })
      }
    },
    validateAddSeat (object) {
      if (this.TYPE_WAY === 0 || this.TYPE_WAY === 1) {
        // no se valida ya que es el viaje de ida
        return true
      } else {
        // se valida ya que el viaje de vuelta no puede tener mas asientos marcados que la ida
        var seatInfo = object.seatToVuex
        var keyGroupTravel = object.KEY_GROUP_TRAVEL
        let objEdit = {}
        let toEdit = this.items[keyGroupTravel].find((i) => i.key === seatInfo.key)
        if (toEdit) {
          if (toEdit.isMultiroute) {
            if (!('path' in toEdit)) {
              return true
            }
            objEdit = toEdit.path.find(obj => obj.keyPath === seatInfo.seat.keyPath)
            if (typeof objEdit === 'undefined') {
              return true
            }
          } else {
            objEdit = toEdit
          }
          if (objEdit.seats) {
            // cuando ya existe por lo menos un asiento marcado
            if (toEdit.typeWay === 2) {
              // es el regreso de un viaje redondo
              if (this.items[keyGroupTravel][0].isMultiroute) {
                // el viaje de ida es multiruta
                if (objEdit.seats.length < this.items[keyGroupTravel][0].path[0].seats.length) {
                  return true
                } else {
                  return false
                }
              } else {
                // el viaje de ida es ruta directa
                if (objEdit.seats.length < this.items[keyGroupTravel][0].seats.length) {
                  return true
                } else {
                  return false
                }
              }
            }
          }
          return true
        } else {
          return true
        }
      }
    },
    updateBus (seatInfo, isSelectSeat) {
      var seat = {
        seatId: seatInfo.seat
      }
      seat.typeCode = isSelectSeat ? 'SELECTED_SEAT_SESSION' : 'FREE_SEAT'
      this.$emit('busHasChanged', seat)
    },
    ...mapActions({
      setSelectTravel: 'travels/setSelectTravel',
      setLoader: 'loader/setLoader'
    })
  },
  components: {
    bxBusBlock
  }
}
</script>



