          :departureId="travel.DEPARTURE.id"
          @select="setArrival($event)">
        </basic-select>
      </div>
    </div>
    <div class="col-md-12 col-lg-3 clearCol w-dateTitle">
      <div class="w-headerTitle">
        <div class="containerDate">
          <i class="fa fa-calendar-o" aria-hidden="true"></i>
          {{ $t('LBL_DEPARTURE') }}
        </div>

        <div class="containerDate">
          <i class="fa fa-calendar-o" aria-hidden="true"></i>
          {{ $t('LBL_RETURN') }}
        </div>
      </div>
      <div class="w-containerData">
        <div class="containerInfoData">
          <datepicker :key="1"
          title="Ida"
          name="origin"
          :openDatepicker="false"
          eventAfterChangeDate="changeDeparture"
          :myDate="travel.DATE_DEPARTURE"
          @changeDeparture="setDepartureDate($event)"
          format="DD/MM/YYYY">
          </datepicker>
        </div>
        <div class="containerInfoData">
          <label v-show="!showDatepicker" @click="modifyReturnDate()">
            <i class="fa fa-plus-circle" aria-hidden="true"></i>
            {{ $t('LBL_ADD_DATE') }}
          </label>
          <span v-show="showDatepicker" @click="disReturnDate()"><i class="fa fa-times-circle" aria-hidden="true"></i></span>
          <datepicker v-if="showDatepicker" :key="1"
          title="vuelta"
          name="retorno"
          :openDatepicker="openDatepicker"
          :minDate="travel.DATE_DEPARTURE"
          eventAfterChangeDate="changeArrival"
          :myDate="travel.DATE_RETURN"
          @changeArrival="setArrivalDate($event)"
          format="DD/MM/YYYY">
          </datepicker>
        </div>
        </div>
      </div>
      <div class="col-md-12 col-lg-2 clearCol">
        <button id="btnSearchTrips"  :class="[blockButton ? 'blockButton': 'btnSearch']" type="button" name="button" @click='searchTravels()' :disabled='false'>{{ $t('LBL_SEARCH') }}</button>
      </div>
      </div>
    </div>
  </div>

