<script>
import cSelect from '@/components/cSelect'
import cBasicSelect from '@/components/cBasicSelect'
import DatePickerComponent from '@/components/datepicker/index'
import moment from 'moment'
import { mapGetters, mapActions } from 'vuex'

export default {
  props: {
    isOpen: {
      type: Boolean,
      default: false
    }
  },
  data: function () {
    return {
      openDatepicker: true,
      showDatepicker: false,
      Departure: [],
      Arrival: [],
      itemD: {
        value: '',
        label: ''
      },
      itemA: {
        value: '',
        label: ''
      },
      scriptData: {
        departureDate: moment().startOf('day'),
        arrivalDate: moment().startOf('day')
      }
    }
  },
  mounted: function () {
    // this.loadDeparture()
    // if (typeof this.travel.DEPARTURE.id !== 'undefined') {
    //   this.loadArrival(this.travel.DEPARTURE.id)
    // }
    if (this.travel.DATE_RETURN != null) {
      this.openDatepicker = false
      this.showDatepicker = true
    }
    this.scriptData.departureDate = this.departureDate
    this.scriptData.arrivalDate = this.arrivalDate
  },
  computed: {
    ...mapGetters({
      travel: 'search/travel',
      departureDate: 'search/dateDeparture',
      selectTravel: 'travels/selectTravel',
      items: 'cart/items',
      arrivalDate: 'search/dateArrival'
    }),
    blockButton: function () {
      return !(typeof this.travel.ARRIVAL.value !== 'undefined' && typeof this.travel.DEPARTURE.value !== 'undefined')
    }
  },
  methods: {
    loadDeparture: function () {
      var vue = this
      this.$http.get(process.env.URL_LIST_DEPARTURES).then(response => {
        this.Departure = response.data.data
      }).catch(error => {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      })
    },
    loadArrival: function (departureId) {
      var vue = this
      this.$http.get(`${process.env.URL_LIST_ARRIVALS}?departure=${departureId}`).then(response => {
        this.Arrival = response.data.data
      }).catch(error => {
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      })
    },
    setDeparture: function (departure) {
      if (this.travel.DEPARTURE.id !== departure.id) {
        let obj = { DEPARTURE: departure }
        this.setTravel(obj)
        this.itemD.value = obj.DEPARTURE.id
        this.reset()
        this.setArrival({})
        document.getElementById('inputArrival').focus()
        this.loadArrival(obj.DEPARTURE.id)
      }
    },
    setArrival: function (arrival) {
      let obj = { ARRIVAL: arrival }
      this.setTravel(obj)
      this.itemA.value = obj.ARRIVAL.id
      this.itemA.text = obj.ARRIVAL.value
      document.getElementById('btnSearchTrips').focus()
    },
    changeDepartureAndArrival () {
      let aux = this.Arrival
      this.Arrival = this.Departure
      this.Departure = aux
      this.swapLocations()
    },
    setDepartureDate (ev) {
      this.setTravel({ DATE_DEPARTURE: ev })
    },
    setArrivalDate (ev) {
      this.setTravel({ DATE_RETURN: ev })
    },
    reset: function () {
      this.itemA.text = ''
    },
    verifyCompleteNowTravel: function (keyGroup) {
      let validate = true
      if (Object.keys(this.items).length === 0) {
        validate = true
      } else {
        if (keyGroup in this.items) {
          if (this.items[keyGroup].length === 1 && this.items[keyGroup][0].typeWay !== 0) {
            // si el viaje es redondo y no tiene la vuelta
            validate = false
          }
          this.items[keyGroup].forEach((travel) => {
            if (travel.isMultiroute) {
              if ('path' in travel) {
                if (travel.path.length !== travel.numberPaths) {
                  // No marco asientos en todos los tramos
                  validate = false
                }
                travel.path.forEach((oPath) => {
                  if ('seats' in oPath) {
                    oPath.seats.forEach((seat) => {
                      if (!('dataPerson' in seat)) {
                        // No tiene registrado al pasajero
                        validate = false
                      }
                    })
                  } else {
                    // No existe asientos
                    validate = false
                  }
                })
                if (validate === true) {
                  // verificamos si todos los tramos tienen el mismo numero de asientos marcados
                  if (!travel.path.every((obj, i, arr) => obj.seats.length === arr[0].seats.length)) {
                    validate = false
                  }
                }
              } else {
                // No existe path de multirura
                validate = false
              }
            } else {
              if ('seats' in travel) {
                travel.seats.forEach((seat) => {
                  if (!('dataPerson' in seat)) {
                    // No tiene registrado al pasajero
                    validate = false
                  }
                })
              } else {
                // No existe asientos
                validate = false
              }
            }
          })
        }
      }
      return validate
    },
    searchTravels: function () {
      if (!this.blockButton) {
        let query = {
          d: this.travel.DEPARTURE.id,
          a: this.travel.ARRIVAL.id,
          dd: this.travel.DATE_DEPARTURE.format('YYYY-MM-DD')
        }
        if (this.travel.DATE_RETURN != null) {
          Object.assign(query, {rd: this.travel.DATE_RETURN.format('YYYY-MM-DD')})
        }
        if (this.verifyCompleteNowTravel(this.selectTravel.KEY_GROUP_TRAVEL)) {
          // todo esta correcto y completado
          this.setSelectTravel({
            KEY_FARE: null,
            KEY_FARE_RW: null,
            TYPE_WAY: 0,
            TRAVEL_ADD_CART_OW: false,
            TRAVEL_ADD_CART_RW: false,
            KEY_GROUP_TRAVEL: null
          })
          this.$router.push({
            name: 'services',
            query: query
          })
        } else {
          // falta completar datos en el viaje
          this.$modal.hide('dialog')
          this.$modal.show('dialog', {
            title: '<div class="warning"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
            text: this.$t('MSG_INCOMPLETE_TRIP'),
            buttons: [
              {
                title: this.$t('LBL_ACCEPT'),
                handler: () => {
                  this.$modal.hide('dialog')
                  var vue = this
                  this.setLoader({loader: true})
                  this.$http.delete(process.env.URL_REMOVE_SHOPPINGCART, { data: {key: this.selectTravel.KEY_FARE} }).then(response => {
                    this.$eventBus.$emit('loadShoppingCart')
                  }).catch(function (error) {
                    vue.$modal.hide('dialog')
                    vue.$modal.show('dialog', {
                      title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
                      text: error.response.data.details,
                      buttons: [{
                        title: vue.$t('LBL_ACCEPT'),
                        handler: () => { vue.$modal.hide('dialog') }
                      }]
                    })
                  }).then(() => {
                    this.setLoader({loader: false})
                    this.setSelectTravel({
                      KEY_FARE: null,
                      KEY_FARE_RW: null,
                      TYPE_WAY: 0,
                      TRAVEL_ADD_CART_OW: false,
                      TRAVEL_ADD_CART_RW: false,
                      KEY_GROUP_TRAVEL: null
                    })
                    this.$router.replace({
                      name: 'services',
                      query: query
                    }, function () {
                      vue.$eventBus.$off()
                    }, function () {
                      vue.$emit('loadTravels')
                    })
                  })
                }
              },
              {
                title: this.$t('LBL_CANCEL'),
                handler: () => {
                  this.$modal.hide('dialog')
                }
              }
            ]
          })
        }
      }
    },
    modifyReturnDate: function () {
      this.showDatepicker = true
      this.openDatepicker = true
      let obj = { DATE_RETURN: this.departureDate, TYPE_TRAVEL: 1 }
      this.setTravel(obj)
    },
    disReturnDate: function () {
      this.showDatepicker = false
      let obj = { DATE_RETURN: null, TYPE_TRAVEL: 0 }
      this.setTravel(obj)
    },
    ...mapActions({
      setTravel: 'search/setTravel',
      swapLocations: 'search/swapLocations',
      setLoader: 'loader/setLoader',
      setSelectTravel: 'travels/setSelectTravel'
    })
  },
  watch: {
    'scriptData.departureDate': function () {
      let obj = { DATE_DEPARTURE: this.scriptData.departureDate }
      this.setTravel(obj)
    },
    'scriptData.arrivalDate': function () {
      let obj = { DATE_RETURN: this.scriptData.arrivalDate }
      this.setTravel(obj)
    },
    openDatepicker: function () {
      this.$forceUpdate()
    }
  },
  components: {
    'select-simple': cSelect,
    'basic-select': cBasicSelect,
    'datepicker': DatePickerComponent
  }
}
</script>
