<style lang="scss">
@import '../../styles/base/variables';
  .title-modify{
    display: none;
  }
  .title-city{
    display: none;
  }
  .widget-min .w-containerData, .widget-min .title-search, .widget-min .w-dateTitle, .widget-min .title-departure, .widget-min .title-arrival{
    display: none;
  }
  .widget-min .title-modify, .widget-min .title-city{
    display:inline-block;
  }
  .widget-min .topLeftRadius{
    border-bottom-left-radius: 5px;
  }
  @media (min-width: 992px){
    .widget-min .col-lg-6 {
      -webkit-box-flex: 0;
      -ms-flex: 0 0 83.33333%;
      flex: 0 0 83.33333%;
      max-width: 83.33333%;
    }
  }
  .blockButton{
    background: map-get($colors, secondary-04);
    color: map-get($colors, white);
    border: 0px;
    width: 100%;
    height: 100%;
    font-weight: 600;
    font-size: 1.3em;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    opacity: 0.65 !important;
    cursor: not-allowed !important;
  }
  .containerInfoData label{
    font-weight: bold;
    cursor: pointer;
    margin-bottom: 0px;
    color: gray;
    opacity: .7;
    -webkit-transition: .2s linear;
    -moz-transition: .2s linear;
    transition: .2s linear;
  }
  .containerInfoData label img{
    margin-right: .5em;
  }
  .containerInfoData label:hover{
    opacity: 1;
    }
  .containerInfoData > span{
    position: absolute;
    right: .5em;
    opacity: .7;
    font-size: 14px;
    cursor: pointer;
    -webkit-transition: .2s linear;
    -moz-transition: .2s linear;
    transition: .2s linear;
  }
  .containerInfoData > span:hover{
    opacity: .5;
    }
  @keyframes show-me {
   0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.show-enter-active {
  animation: show-me .1s;
}
.show-leave-active {
  animation: show-me .1s reverse;
}
</style>



