/* ============
 * Actions for the loader module
 * ============
 *
 * The actions that are available on the
 * loader module.
 */

import * as types from './mutation-types'

export const setPassengerPays = ({ commit }, payload) => {
  commit(types.SET_PASSENGER_PAYS, payload)
}
export const restartState = ({ commit }) => {
  commit(types.RESTART_STATE)
}
export default {
  setPassengerPays,
  restartState
}



