<template>
  <div class="col-sm-12" :class="[showTravel ? '': 'hide-travel']">
    <div class="row travel-resume">
      <div class="col-sm-12 green-line">
        <div class="row">
          <div class="col-sm-6">
            <div class="travel-summary">
              <div class="row travel-places">
                <div class="col-sm-4">
                  <slot name="departureTitle"></slot>
                  <span>{{formatDate(travelItem.travelDepartureDatetime, travelItem.countryCode)}}</span>
                </div>
                <div class="col-sm-4">
                  <v-popover>
                    <img src="http://placehold.it/88x32" class="inline img-business" v-if="!travelItem.companyLogo">
                    <img :src="'/static/images/logos-companies/'+travelItem.companyCode+'-logo.png'" class="inline img-business tooltip-target " @click="toggleInfoCompany()" v-else>
                    <template slot="popover" class="content-info-company">
                      <i v-if="loadingCompany" class="fa fa-spinner" aria-hidden="true"></i>
                      <div v-else class="content-info-company">
                        <div class="form-group col-12 alert-message"  v-if="company.name">
                          <label for="labelCompanyName" class="">{{ $t('LBL_COMPANY_NAME') }}</label>
                          <label id="labelCompanyName" name="labelCompanyName" class="">{{company.name}}</label>
                        </div>
                        <div class="form-group col-12 alert-message" v-if="company.ruc">
                          <label for="labelRuc" class="" >{{ $t('LBL_RUC') }}</label>
                          <label id="labelRuc" name="labelRuc" class="" v-if="company.ruc">{{company.ruc}}</label>
                        </div>
                        <div class="form-group col-12 alert-message" v-if="company.address">
                          <label for="labelAddress" class="">{{ $t('LBL_ADDRESS') }}</label>
                          <label id="labelAddress" name="labelAddress" class="">{{company.address}}</label>
                        </div>
                        <div class="form-group col-12 alert-message" v-if="company.phone">
                          <label for="labelPhone" class="">{{ $t('LBL_PHONE') }}</label>
                          <label id="labelPhone" name="labelPhone" class="" >{{company.phone}}</label>
                        </div>
                        <div class="form-group col-12 alert-message">
                          <label for="labelBoarding" class="">{{ $t('LBL_BOARDING_TERMINAL') }}</label>
                          <label id="labelBoarding" name="labelBoarding" class="" v-if="company.boarding">{{company.boarding}}</label>
                          <label id="labelBoarding" name="labelBoarding" class="" v-else>{{$t('LBL_NO_INFORMATION')}}</label>
                        </div>
                        <div class="form-group col-12 alert-message">
                          <label for="labelLanding" class="">{{ $t('LBL_LANDING_TERMINAL') }}</label>
                          <label id="labelLanding" name="labelLanding" class="" v-if="company.landing">{{company.landing}}</label>
                          <label id="labelLanding" name="labelLanding" class="" v-else>{{$t('LBL_NO_INFORMATION')}}</label>
                        </div>
                      </div>
                    </template>
                  </v-popover>
                  <div class="tag-stops">
                      {{ $t('LBL_STOPS') }}:
                      <div class="inline">{{ travelItem.stops === 'VIEW_DIRECT' ? $t( travelItem.stops) :  travelItem.stops + ' '+ $t('VIEW_STOPS')}}</div>
                  </div>
                  <div class="border-arrow">
                    <div v-if="travelItem.travelTime" class="inline">
                      {{ $t('LBL_TRAVEL_TIME') }} <br><i class="fa fa-clock-o" aria-hidden="true"></i> {{formatDays(travelItem.travelTime)}}
                    </div>
                    <div v-if="travelItem.path ">
                      {{ $t('LBL_TRAVEL_TYPE') }}:
                      <div class="inline-group" v-for="(pathService, index) in travelItem.path" :key="index">
                          {{ pathService.travelService }}</div>
                      </div>
                  </div>
                </div>
                <div class="col-sm-4 grey-border-right">
                  <slot name="arrivalTitle"></slot>
                  <span>{{formatDate(travelItem.travelArrivalDatetime, travelItem.countryCode)}}</span>
                </div>
              </div>
            </div>
            <v-popover class="popover-route">
              <div v-tooltip.right class="route-detail-lbl tooltip-target"><i class="fa fa-info-circle" aria-hidden="true"></i>{{ $t('LBL_VIEW_DETAIL') }}</div>
              <template slot="popover">
              <!-- <div class="content-info-route-detail" v-if="travelItem.routeDetail[0].routeDetail.length > 0">
                <div v-for="(routePath, index) in travelItem.routeDetail" :key="index">
                  <div class="content-info-path">{{ routePath.routeDetail[0].departureName }}</div>
                  <div v-for="(route, index) in routePath.routeDetail" :key="index">
                    <div v-if="index !== routePath.routeDetail.length-1">{{ route.arrivalName }}</div>
                  </div>
                  <div>{{ routePath.routeHeader[0].arrivalName }}</div>
                </div>
              </div>
              <div v-else>
                  {{ $t('LBL_NO_ROUTE_DETAIL') }}
              </div> -->
              </template>
            </v-popover>
            <v-popover class="" @show="toggleRestrictions()">
              <div class="fare-detail-lbl">
                <i class="fa fa-info-circle" aria-hidden="true"></i>
                {{ $t('LBL_FARE_RESTRICTIONS') }}
                </div>
              <template slot="popover" class="content-info-fare">
                <i v-if="loadingRestrictionsFare" class="fa fa-spinner" aria-hidden="true"></i>
                <div v-else>
                  <div class="content-info-fare" v-if="fareRestrictions && fareRestrictions.body.length > 0">
                    <div>
                      <div class="info-fare-header">
                        <p class="col-6">{{ $t('LBL_FARE_RESTRICTIONS') }}</p>
                        <div class="col" v-for="(fare, index) in fareRestrictions.header" :key="'fare' + index">{{fare}}</div>
                      </div>
                      <div class="col-12 info-fare-body">
                        <div class="row" v-for="(item, index) in fareRestrictions.body" :key="'benefit' + index">
                          <p class="col-6">{{item.benefit}}</p>
                          <div class="col" v-for="(checks, index) in item.checks" :key="'checks' + index">
                            <i v-if="checks" class="fa fa-check"></i>
                            <i v-else class="fa fa-remove"></i>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="content-info-fare" v-else>
                      <p class="col-12"></p>
                      <p class="col-12">{{ $t('LBL_FARE_RESTRICTIONS_EMPTY') }}</p>
                  </div>
                </div>
              </template>
            </v-popover>
          </div>
          <div class="col-sm-6">
            <bx-travel-fares
              @selectedFare="selectFare($event)"
              @showSeats="toggleSeats($event)"
              :showSeat = 'showSeats'
              :fareDetail = 'travelItem.fare'
              :travelKey = 'travelItem.key'
              :currency = 'travelItem.currency'
              :free = 'travelItem.free'>
            </bx-travel-fares>
          </div>
        </div>
        <bx-travel-seats
            :travelItem="travelItem"
            :selectedFare="selectedFare"
            :KEY="selectTravel.KEY"
            :FARE_ID="selectTravel.FARE_ID"
            :KEY_FARE="selectTravel.KEY_FARE"
            :KEY_FARE_RW="selectTravel.KEY_FARE_RW"
            :KEY_GROUP_TRAVEL="selectTravel.KEY_GROUP_TRAVEL"
            :TRAVEL_ADD_CART_OW="selectTravel.TRAVEL_ADD_CART_OW"
            :TRAVEL_ADD_CART_RW="selectTravel.TRAVEL_ADD_CART_RW"
            :TYPE_WAY="selectTravel.TYPE_WAY"
            @showBusEvent="showBusEvent($event)"
            v-if="showSeats"></bx-travel-seats>
      </div>
    </div>
  </div>
</template>

<script>
import bxTravelFares from './fares'
import bxTravelSeats from './detail'
import { mapActions, mapGetters } from 'vuex'
import moment from 'moment'

export default {
  props: {
    travelItem: {
      type: Object,
      required: true
    },
    departureTitle: {
      type: String
    },
    arrivalTitle: {
      type: String
    },
    showCompany: {
      type: Boolean,
      required: true
    }
  },
  data () {
    return {
      showSeats: false,
      selectedFare: null,
      showInfoCompany: this.showCompany,
      showViewDetail: false,
      fareRestrictions: '',
      loadingRestrictionsFare: false,
      loadingCompany: false,
      company: {
        name: '',
        address: '',
        phone: '',
        ruc: '',
        boarding: '',
        landing: ''
      }
    }
  },
  mounted: function () {
    this.$eventBus.$on('toggleSeats', ($event) => {
      this.toggleSeats(true)
    })
  },
  watch: {
    showCompany: function (value) {
      this.showInfoCompany = value
    }
  },
  computed: {
    showTravel () {
      let isTravelAddToCart = (this.selectTravel.TYPE_WAY === 2) ? this.selectTravel.TRAVEL_ADD_CART_RW : this.selectTravel.TRAVEL_ADD_CART_OW
      if (isTravelAddToCart) {
        // Si el viaje ya fue agregado al carrito
        let keyTravel = (this.selectTravel.TYPE_WAY === 2) ? this.selectTravel.KEY_FARE_RW : this.selectTravel.KEY_FARE
        for (let fare in this.travelItem.fare) {
          if (this.travelItem.fare[fare].fareKey === keyTravel) {
            return true
          }
        }
        return false
      }
      return true
    },
    ...mapGetters({
      items: 'cart/items',
      selectTravel: 'travels/selectTravel'
    })
  },
  components: {
    bxTravelFares,
    bxTravelSeats
  },
  methods: {
    toggleRestrictions () {
      this.loadingRestrictionsFare = true
      this.$http.get(`${process.env.URL_GET_FARE_RESTRICTIONS}?key=` + this.travelItem.key).then(response => {
        this.loader = !this.loader
        let data = response.data
        if (data.code === 10) {
          this.fareRestrictions = data.data
        } else {
          console.log('error')
        }
      }).catch(function (error) {
        this.loadingRestrictionsFare = false
        console.log(error)
      }).then(() => {
        this.loadingRestrictionsFare = false
        this.showFareRestrictions = true
      })
    },
    formatDate (input, country = null) {
      var format = ''
      if (country === 'PE') {
        format = 'DD/MM/YYYY - hh:mm a'
      } else {
        format = 'MM-DD-YYYY - hh:mm a'
      }
      return moment(input).format(format)
    },
    formatDays (input) {
      var hh = parseInt(input.split(':')[0])
      if (hh > 23) {
        var mm = parseInt(input.split(':')[1])
        var base = moment.duration(0, 'd')
        var hours = moment.duration(hh, 'h')
        var minutes = moment.duration(mm, 'm')
        base.add(hours).add(minutes)
        return base.days() + 'd ' + base.hours() + 'h ' + base.minutes() + 'm'
      } else {
        return moment(input, 'hh:mm:ss').format('hh[h] mm[m]')
      }
    },
    formatHour (input, formatInput = 'hh:mm:ss', formatOutput = 'hh[h] mm[m]') {
      return moment(input, formatInput).format(formatOutput)
    },
    fareBySkin (skin) {
      return this.selectedFares.find((item) => item.key.split('|')[0] === skin)
    },
    selectFare (e) {
      this.selectedFare = e
    },
    showBusEvent (e) {
      this.showSeats = e
    },
    toggleViewDetail () {
      this.showViewDetail = !this.showViewDetail
    },
    toggleInfoCompany () {
      if (!this.showInfoCompany) {
        this.loadingCompany = true
        this.$http.get(`${process.env.URL_GET_INFO_COMPANY}?key=` + this.travelItem.key).then(response => {
          this.loader = !this.loader
          let data = response.data
          if (data.code === 10) {
            var result = data.data
            this.company.name = result.companyName
            this.company.address = result.companyAddress
            this.company.phone = result.companyPhone
            this.company.ruc = result.companyRuc
            this.company.boarding = result.terminalBoarding
            this.company.landing = result.terminalLanding
          } else {
            // alert('Error mostrando informacion de empresa')
          }
        }).catch(function (error) {
          this.loadingCompany = false
          console.log(error)
        }).then(() => {
          this.showInfoCompany = true
          this.loadingCompany = false
        })
      } else {
        this.showInfoCompany = false
      }
    },
    toggleSeats (value) {
      if (!value) {
        // ocultar bus
        let isTravelAddToCart = (this.selectTravel.TYPE_WAY === 2) ? this.selectTravel.TRAVEL_ADD_CART_RW : this.selectTravel.TRAVEL_ADD_CART_OW
        if (isTravelAddToCart) {
          // Si el viaje ya fue agregado al carrito
          var vue = this
          this.setLoader({loader: true})
          let keyTravel = (this.selectTravel.TYPE_WAY === 2) ? this.selectTravel.KEY_FARE_RW : this.selectTravel.KEY_FARE
          this.$http.delete(process.env.URL_REMOVE_SHOPPINGCART, { data: {key: keyTravel} }).then(response => {
            this.$eventBus.$emit('loadShoppingCart')
            if (this.selectTravel.TYPE_WAY === 0) {
              // Viaje solo de ida
              this.setSelectTravel({KEY_FARE: null, TRAVEL_ADD_CART_OW: false, KEY_GROUP_TRAVEL: null})
            } else if (this.selectTravel.TYPE_WAY === 1) {
              // Vieje de ida de un viaje redondo
              this.setSelectTravel({KEY_FARE: null, TRAVEL_ADD_CART_OW: false, KEY_GROUP_TRAVEL: null})
            } else if (this.selectTravel.TYPE_WAY === 2) {
              // Vieje de vuelta de un viaje redondo
              this.setSelectTravel({KEY_FARE_RW: null, TRAVEL_ADD_CART_RW: false})
            }
            this.showSeats = value
          }).catch(function (error) {
            vue.$modal.hide('dialog')
            vue.$modal.show('dialog', {
              title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
              text: error.response.data.details,
              buttons: [{
                title: vue.$t('LBL_ACCEPT'),
                handler: () => { vue.$modal.hide('dialog') }
              }]
            })
          }).then(() => {
            this.setLoader({loader: false})
          })
        } else {
          // si aun no se agrego el viaje al carrito
          this.showSeats = value
        }
      } else {
        // mostrar bus
        this.showSeats = value
      }
    },
    ...mapActions({
      setShoppingCart: 'cart/setShoppingCart',
      setSelectTravel: 'travels/setSelectTravel',
      setLoader: 'loader/setLoader'
    })
  }
}
</script>
<style lang="scss">
@import '../../styles/base/variables';
.info-fare-header, .info-fare-body{
  display: flex;
}
.info-fare-header{
  background: map-get($colors, gray-16);
  text-align: center;
  color: map-get($colors, white);
  font-weight: bold;
}
.info-fare-header > p{
  padding-top: 11px;
  margin: 0;
}
.info-fare-body > div > p{
  margin: 2px 0;
  font-weight: bold;
}
.info-fare-body .fa-check{
  font-size: 16px;
  color: map-get($colors, secondary-01);
}
.info-fare-body .fa-remove{
  color: map-get($colors, gray-12);
  font-size: 16px;
}
.info-fare-body > div:nth-child(even){
  background: map-get($colors, gray-07);
}
.fare-detail-lbl{
  position: absolute;
  left: 160px;
  bottom: 2em;
  font-size: 14px;
  cursor: pointer;
  background: map-get($colors, gray-04);
  padding: 2px .5em;
  transition: .2s linear;
  border-radius: 4px;
  &:hover{
    background: gray;
    color: map-get($colors, white);
  }
  i{
    margin-right: .5em;
  }
}
</style>



