<template>
  <div class="">
    <label>
      <input type="text" name="" value="" v-model="keyword" placeholder="Seleccione una ciudad" @input="onInput($event.target.value)" @keyup.esc="isOpen = false" @blur="isOpen = false" @keydown.down="moveDown" @keydown.up="moveUp" @keydown.enter="select">
      <figure v-show="iconSelect" @click="openSelect">
        <img src="/static/images/arrow-bottom.svg" alt="">
      </figure>
      <figure v-show="iconClear" @click="clearInput">
        <img src="/static/images/arrow-left.svg" alt="">
      </figure>
      <!-- <span v-show="iconClear" @click="clearInput"><i class="fa fa-close"></i></span> -->
    </label>
    <ul class="options-list" v-if="isOpen">
      <li v-for="(option, index) in fOptions" :class="{
          'highlighted': index === highlightedPosition
          }" @mouseenter="highlightedPosition = index" @mousedown="select" :key="index">
        {{ option.title }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      isOpen: false,
      highlightedPosition: 0,
      keyword: '',
      iconClear: false,
      iconSelect: true,
      options: [
        {
          title: 'First Scene'
        },
        {
          title: 'Second Scene'
        },
        {
          title: 'asd asdasd'
        },
        {
          title: 'qweqwe qweqw'
        },
        {
          title: 'zxczxc zxc'
        },
        {
          title: 'yuiu yuiu'
        },
        {
          title: 'oiuyt rre'
        },
        {
          title: 'lkjhg ssss'
        },
        {
          title: 'mnbvs adds'
        }
      ]
    }
  },
  computed: {
    fOptions: function () {
      const re = new RegExp(this.keyword, 'i')
      return this.options.filter(o => o.title.match(re))
    }
  },
  methods: {
    onInput: function (value) {
      this.isOpen = !!value
      this.highlightedPosition = 0
    },
    moveDown: function () {
      if (!this.isOpen) {
        return
      }
      this.highlightedPosition =
        (this.highlightedPosition + 1) % this.fOptions.length
    },
    moveUp: function () {
      if (!this.isOpen) {
        return
      }
      this.highlightedPosition = this.highlightedPosition - 1 < 0
        ? this.fOptions.length - 1
        : this.highlightedPosition - 1
    },
    select: function () {
      const selectedOption = this.fOptions[this.highlightedPosition]
      this.keyword = selectedOption.title
      this.isOpen = !this.isOpen
      this.iconClear = !this.iconClear
    },
    clearInput: function () {
      this.keyword = ''
      this.iconClear = !this.iconClear
    },
    openSelect: function () {
      this.isOpen = true
    }
  }
}
</script>

<style lang="scss">
@import '../styles/base/variables';
.w-containerData{
  height: 3em;
  >div{
  }
  >div.containerinfoCity:last-child{
    right: 0;
    border-left: 1px dashed map-get($colors, gray-05);
  }
}
.containerinfoCity {
  position: absolute;
  height: 3em;
  label {
    width: 100%;
    margin: 0px;
    text-align: center;
    input {
      width: 85%;
      border: 0px;
      padding-left: 3em;
      &:focus {
        outline: 0;
      }
    }
    figure {
      display: inline-block;
    }
  }
  div.menu>.item {
    display: block;
  }
}

.options-list {
  list-style-type: none;
  padding: 0;
  z-index: 15;
  li {
    display: inline-block;
    margin: 0 10px;
    width: 100%;
    flex-wrap: wrap;
    background: map-get($colors, white);
    margin: 0;
    color: map-get($colors, gray-19);
    padding: 7px;
    cursor: pointer;
    text-align: left;
  }
  display: flex;
  flex-direction: column;
  margin-top: 0.3em;
  border: 1px solid map-get($colors, gray-06);
  border-radius: 0 0 3px 3px;
  position: absolute;
  width: 43%;
  overflow: hidden;
}

.highlighted {
  background: map-get($colors, gray-02) !important;
}
</style>



