<template>
    <div class="" v-if="busses">
      <div class="view-seat">
        <div class="row detail-routes">
          <template v-for="(stop, index) in fullRouteInfo">
            <div class="col routes" :key="'rts' + index">
              <div>
                <p>{{ stop.name }}</p>
              </div>
            </div>
            <div class="img-routes" v-if="index !== fullRouteInfo.length -1" :key="'rtsimg' + index">
              <i aria-hidden="true" class="fa fa-chevron-right"></i>
            </div>
          </template>
        </div>
        <div>
          <bx-bus-detail @busHasChanged="busHasChanged($event, bus)" v-for="(bus , index) in busses"
              :key="'bd' + index"
              :travelItem="travelItem"
              :selectedFare="selectedFare"
              :bus="bus"
              :index="index"
              :FARE_ID="FARE_ID"
              :KEY_FARE="KEY_FARE"
              :KEY_FARE_RW="KEY_FARE_RW"
              :KEY_GROUP_TRAVEL="KEY_GROUP_TRAVEL"
              :TRAVEL_ADD_CART_OW="TRAVEL_ADD_CART_OW"
              :TRAVEL_ADD_CART_RW="TRAVEL_ADD_CART_RW"
              :TYPE_WAY="TYPE_WAY"
              :numberPaths="busses.length">
          </bx-bus-detail>
        </div>
        <div class="div-show--cart row">
          <div class="bg-show--cart"></div>
          <label class="btn btn-primary" v-if="showBtnRegisterPassenger()" @click="$eventBus.$emit('showShoppingCart')">{{ $t('LBL_REGISTER_PASSENGER') }}</label>
          <label class="btn btn-primary" v-if="showBtnReturnTrip()" @click="$eventBus.$emit('mostrarvuelta')">{{ $t('LBL_RETURN_TRIP') }}</label>
        </div>
      </div>
    </div>
</template>
<script>
import bxBusDetail from '../bus/detail'
import { mapActions, mapGetters } from 'vuex'

export default {
  props: {
    travelItem: {
      type: Object,
      required: true
    },
    selectedFare: {
      type: Object,
      required: true
    },
    KEY: {
      required: true
    },
    FARE_ID: {
      required: true
    },
    KEY_FARE: {
      required: true
    },
    KEY_FARE_RW: {
      required: true
    },
    KEY_GROUP_TRAVEL: {
      required: true
    },
    TRAVEL_ADD_CART_OW: {
      required: true
    },
    TRAVEL_ADD_CART_RW: {
      required: true
    },
    TYPE_WAY: {
      required: true
    }
  },
  data () {
    return {
      busses: null,
      fullRouteInfo: {} // ?
    }
  },
  computed: {
    ...mapGetters({
      items: 'cart/items',
      loader: 'loader/loader',
      selectTravel: 'travels/selectTravel'
    })
  },
  mounted () {
    var vue = this

    if (this.travelItem.countryCode === 'AR' || this.travelItem.countryCode === 'BR') {
      var countryCode = (this.travelItem.countryCode).toLowerCase()
      var sRedirect = 'https://boletoxpress.plataforma10.com.' + countryCode + '/servicios/Taquilla?claveSolicitud=' + this.travelItem.travelCode
      if (this.travelItem.hasOwnProperty('returnDateParams')) {
        sRedirect += '/' + this.travelItem.returnDateParams
      }
      window.location = sRedirect
    } else {
      this.$http.get(`${process.env.URL_GET_BUS}?key=${this.selectedFare.fareKey}&fare=${this.selectedFare.fareId}`).then(response => {
        this.processResponse(response.data.data)
        this.$emit('showBusEvent', true)
      }).catch(error => {
        this.$emit('showBusEvent', false)
        vue.$modal.hide('dialog')
        vue.$modal.show('dialog', {
          title: '<div class="error"><i class="fa fa-exclamation-circle" aria-hidden="true"></i></div>',
          text: error.response.data.details,
          buttons: [{
            title: vue.$t('LBL_ACCEPT'),
            handler: () => { vue.$modal.hide('dialog') }
          }]
        })
      }).then(() => {
        this.setLoader({loader: false})
      })
    }
  },
  methods: {
    processResponse (busses) {
      this.busses = Array.isArray(busses) ? busses : [busses]
      let paths = this.busses.length
      this.fullRouteInfo = this.busses.reduce((routes, bus, index) => {
        let stopDeparture = { name: bus.header.departureName }
        if (!routes.includes(stopDeparture)) routes.push(stopDeparture)
        if (paths - 1 === index) {
          let stopArrival = { name: bus.header.arrivalName }
          if (!routes.includes(stopArrival)) routes.push(stopArrival)
        }
        return routes
      }, [])
    },
    busHasChanged (seat, bus) {
      let currentSeat = bus.detail.find(aSeat => aSeat.seatId === seat.seatId)
      this.$set(currentSeat, 'typeCode', seat.typeCode)
    },
    ...mapActions({
      setLoader: 'loader/setLoader'
    }),
    showBtnReturnTrip: function () {
      if (this.selectTravel.TYPE_WAY === 1) {
        // solo ida
        if (this.selectTravel.TRAVEL_ADD_CART_OW) {
          return this.verifySeatsMarked()
        } else {
          return false
        }
      } else {
        return false
      }
    },
    showBtnRegisterPassenger: function () {
      if (this.selectTravel.TYPE_WAY === 0) {
        // solo ida
        if (this.selectTravel.TRAVEL_ADD_CART_OW) {
          return this.verifySeatsMarked()
        } else {
          return false
        }
      } else {
        if (this.selectTravel.TYPE_WAY === 2) {
          // vuelta de un viaje redondo
          if (this.selectTravel.TRAVEL_ADD_CART_RW) {
            return this.verifySeatsMarked()
          } else {
            return false
          }
        } else {
          return false
        }
      }
    },
    verifySeatsMarked: function () {
      let validate = true
      if (Object.keys(this.items).length === 0) {
        validate = false
      } else {
        for (var keyGroup in this.items) {
          this.items[keyGroup].forEach((travel) => {
            if (travel.isMultiroute) {
              var indexMaxPassenger = 0
              travel.path.forEach((oPath, index) => {
                if (oPath.seats.length > travel.path[indexMaxPassenger].seats.length) {
                  indexMaxPassenger = index
                }
              })
              travel.path[indexMaxPassenger].seats.forEach((seat, index) => {
                for (var i = 0; i < travel.numberPaths; i++) {
                  if ('path' in travel) {
                    var oPath = travel.path.find(obj => obj.indexPath === i + 1)
                    if (typeof oPath === 'undefined' || !(index in oPath.seats)) {
                      validate = false
                    }
                  } else {
                    validate = false
                  }
                }
              })
            } else {
              if (!('seats' in travel)) {
                validate = false
              }
            }
          })
        }
      }
      return validate
    }
  },
  components: {
    bxBusDetail
  }
}
</script>



