<template>
  <div class="width-general row list-travel">
    <template>
      <template v-if="travel">
        <bx-travel-item v-for="(travelItem, index) in travels" :key="index" :travelItem="travelItem" :showCompany="showCompany">
          <p slot="departureTitle">{{travelItem.departureName}}</p>
          <p slot="arrivalTitle">{{travelItem.arrivalName}}</p>
        </bx-travel-item>
      </template>
      <template v-else>
        <center>No hay viajes para la ruta</center>
      </template>
    </template>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import bxTravelItem from './item'

export default {
  props: {
    travels: {
      type: Array
    },
    showCompany: {
      type: Boolean,
      required: true
    }
  },
  computed: {
    ...mapGetters({
      travel: 'search/travel',
      selectTravel: 'travels/selectTravel',
      items: 'cart/items'
    })
  },
  watch: {
    showCompany: function (value) {
      // this.showInfoCompany = value
      console.log('entra watch1')
    }
  },
  components: {
    bxTravelItem
  }
}
</script>



